                    ограниченной к распространению вступившими в законную силу
                    судебными актами или законами Республики Казахстан;
                  </li>
                  <li>
                    содержались в авторских выступлениях, идущих в эфир без
                    предварительной записи, либо текстах, не подлежащих
                    редактированию в соответствии с настоящим Законом;
                  </li>
                  <li>
                    содержались в сообщениях, материалах или их фрагментах,
                    распространенных другим средством массовой информации,
                    поставленным на учет в уполномоченном органе, при условии
                    указания в своем сообщении ссылки на средство массовой
                    информации, распространившее данное сообщение, за
                    исключением распространения информации, запрещенной или иным
                    образом ограниченной к распространению вступившими в
                    законную силу судебными актами или законами Республики
                    Казахстан;
                  </li>
                  <li>
                    содержались в обязательных сообщениях, установленных в
                    соответствии со статьей 18-2 настоящего Закона.
                  </li>
                </ol>
              </div>
            </div>
          </div>
        </div>
