        <div class="accordion-item child nested hidden">
          <p class="parent" @click="changeToActive">КЛЕВЕТА</p>
          <div class="accordion-item child hidden">
            <div class="child text">
              <p>Понятие клеветы содержится в статье 130 Уголовного Кодекса и означает распространение заведомо ложных сведений, порочащих честь и достоинство другого лица или подрывающих его репутацию. Клевета может быть:</p>
              <ul>
              <li>Без отягчающих последствий - наказывается штрафом в размере до 1000 МРП либо исправительными работами в том же размере, либо ограничением свободы на срок до одного года, либо лишением свободы на тот же срок.</li>
              <li>Совершенная публично или с использованием СМИ или сети телекоммуникаций - наказывается штрафом в размере до 2000 МРП либо исправительными работами в том же размере, либо ограничением свободы на срок до двух лет, либо лишением свободы на тот же срок.</li>
              <li>Повлекшая тяжкие последствия или с обвинением лица в совершении тяжкого, особо тяжкого преступления либо в совершении коррупции- наказываются штрафом в размере до 3000 МРП либо исправительными работами в том же размере, либо ограничением свободы на срок до трех лет, либо лишением свободы на тот же срок.</li>
              </ul>
              <p><strong>Обязательные условия для клеветы:</strong></p>
              <ul>
              <li>распространение заведомо ложных сведений, порочащих честь и достоинство другого человека или подрывающих его репутацию должны не соответствовать действительности.</li>
              <li>распространение сведений, порочащих честь и достоинство гражданина, означает опубликование их в печати, сообщение по радио, телевидению, с использованием других СМИ, изложение в характеристиках, публичных выступлениях, заявлениях, адресованных различным организациям, должностным лицам, или сообщение в иной, в том числе устной форме нескольким лицам или хотя бы одному лицу.</li>
              <li>должен быть прямой умысел. То есть необходимо установить, что виновный сознавал ложность сообщаемых им фактов, которые порочат честь и достоинство другого человека или подрывают его репутацию, и желал это сделать. Добросовестное заблуждение относительно достоверности распространяемых сведений, т.е. в случае, когда лицо было уверено в правдивости сведений, хотя на самом деле они были ложными, исключает ответственность за клевету, но может нести уголовную ответственность по ст. 130 УК, если эти сведения носили оскорбительный характер.</li>
              </ul>
              <p><strong>ВАЖНО! Сообщение таких сведений лишь одному лицу, которого они касаются, не может признаваться их распространением.</strong></p>
              <p>Есть также особые составы клеветы - статья 411 УК РК  «Клевета в отношении судьи, присяжного заседателя, прокурора, лица, осуществляющего досудебное расследование, эксперта, судебного пристава, судебного исполнителя».</p>
              <p><strong>Клевета в отношении судьи или присяжного заседателя</strong> в связи с рассмотрением дел или материалов в суде наказывается штрафом в размере до 2000 МРП либо исправительными работами в том же размере, либо привлечением к общественным работам на срок до шестисот часов, либо ограничением свободы на срок до двух лет, либо лишением свободы на тот же срок.</p>
              <p><strong>Клевета в отношении прокурора, лица, осуществляющего досудебное расследование, эксперта, судебного пристава, судебного исполнителя</strong> в связи с осуществлением досудебного расследования, проведением судебной экспертизы либо исполнением приговора, решения суда или иного судебного акта наказывается штрафом в размере до 2000 МРП либо исправительными работами в том же размере, либо привлечением к общественным работам на срок до шестисот часов, либо ограничением свободы на срок до двух лет, либо лишением свободы на тот же срок.</p>
              <p><strong>Клевета, с обвинением лица в совершении тяжкого или особо тяжкого преступления</strong> наказываются штрафом в размере до 4000 МРП либо исправительными работами в том же размере, либо привлечением к общественным работам на срок до одной тысячи часов, либо ограничением свободы на срок до четырех лет, либо лишением свободы на тот же срок.</p>
              <p><strong>ВАЖНО! Нельзя называть подозреваемое лицо виновным до вынесения приговора (принцип презумпции невиновности, ст. 77 Конституции РК и статья 19 УПК РК). Также помните, что приговор может быть обжалован и отменен. К уголовной ответственности, в отличие от гражданской, может быть привлечено только физическое лицо (журналист), а не редакция. </strong></p>
            </div>
          </div>
