        <!-- child-3 -->
        <div class="accordion-item child nested hidden">
          <p class="parent" @click="changeToActive">ОСКОРБЛЕНИЕ</p>
          <div class="accordion-item child hidden">
            <div class="child text">
              <p>
                Оскорбление - унижение чести и достоинства другого лица,
                выраженное в неприличной форме.
              </p>
              <p>Как суд определяет неприличную форму?</p>
              <ul>
                <li>
                  суд должен исходить из норм нравственности нашего общества, а
                  не только из ее восприятия самим потерпевшим.
                </li>
                <li>
                  не имеет значения, соответствует ли отрицательная оценка
                  потерпевшего действительности или нет.
                </li>
                <li>
                  при рассмотрении дел суды назначают лингвистическую экспертизу
                  и полагаются на заключение эксперта о неприличности формы
                  выражения, либо суд решает этот вопрос самостоятельно.
                </li>
              </ul>
              <p>Условия:</p>
              <ul>
                <li>
                  Оскорбление может быть высказано как публично, так и с глазу
                  на глаз, непосредственно потерпевшему или в его отсутствие
                  через третьих лиц.
                </li>
                <li>
                  Оскорбление должно быть выражено в отношении конкретной
                  личности или конкретных лиц (например, нецензурная брань в
                  общественном месте, не оскорбляющая никого персонально,
                  квалифицируется как хулиганство).
                </li>
                <li>
                  Оскорбление может быть не только в устной или письменной
                  форме, но и в физических действиях, которые унижают
                  человеческое достоинство (пощечина, плевок, щелчок по носу или
                  уху, неприличные знаки, срывание одежды, циничные
                  прикосновения к телу и т.п.)Однако физическое оскорбление
                  может быть спорным. В таких случаях считается необходимым
                  более детально выяснять умысел виновного, который должен быть
                  направлен именно на унижение чести и достоинства потерпевшего.
                </li>
                <li>
                  Не считается оскорблением критические замечания, высказанные в
                  резкой форме, негативное суждение о конкретном лице,
                  упоминание его отрицательных качеств, концентрация внимания на
                  недостатках его характера, высказанные в корректной, приличной
                  форме.
                </li>
              </ul>
              <p>
                Преступление следует считать оконченным с момента
                оскорбительного высказывания или жеста в неприличной форме
                относительно личных или социальных качеств потерпевшего.
              </p>
              <p>
                Ответственность за оскорбление: штраф в размере до ста МРП либо
                исправительные работы в том же размере, либо привлечение к
                общественным работам на срок до ста двадцати часов.
              </p>
              <p>
                Оскорбление, совершенное публично или с использованием СМИ или
                сетей телекоммуникаций, наказывается штрафом в размере до
                двухсот МРП либо исправительными работами в том же размере, либо
                привлечением к общественным работам на срок до ста восьмидесяти
                часов.
              </p>
            </div>
          </div>
        </div>
