        <!-- child-5 -->
        <div class="accordion-item child nested hidden">
          <p class="parent" @click="changeToActive">
            ЗАЩИТА ЧЕСТИ, ДОСТОИНСТВА И ДЕЛОВОЙ РЕПУТАЦИИ
          </p>
          <div class="accordion-item child hidden">
            <div class="child text">
                            <p>Честь - это общественная оценка лица, мера его духовных и социальных качеств.</p>
              <p>Достоинство - внутренняя самооценка лицом собственных качеств, способностей, мировоззрения, своего общественного значения.</p>
              <p>Деловая репутация - устойчивая положительная оценка деловых (производственных, профессиональных) достоинств лица общественным мнением.</p>
              <p><strong>Право на ответ и опровержение (статья 19 Закона о СМИ)</strong></p>
              <p>Гражданин или юридическое лицо вправе требовать от СМИ опровержения сведений, не соответствующих действительности и порочащих его честь, достоинство и деловую репутацию, которые были распространены в данном СМИ.</p>
              <p>В случае обращения гражданина или юридического лица за опровержением редакция средства массовой информации, <strong>не располагающая доказательствами</strong> того, что распространенные данным средством массовой информации сведения соответствуют действительности, обязана опровергнуть их в том же СМИ.</p>
              <p><strong>ВАЖНО! Требование об опровержении должно быть оформлено письменно с указанием сведений, не соответствующих действительности, времени и способа их распространения данным СМИ.</strong></p>
              <p><strong>Право на ответ</strong></p>
              <p>Гражданин или юридическое лицо, в отношении которого СМИ опубликованы сведения, ущемляющие его права или законные интересы, имеет право на бесплатную публикацию своего ответа в тех же СМИ.</p>
              <p>СМИ вправе отказать в опубликовании ответа в случае, если ответ гражданина или юридического лица либо их представителя содержит информацию, распространение которой запрещено законами РК или иным образом нарушает права и законные интересы физических и (или) юридических лиц или государства.</p>
              <p><strong>Процедура опубликования</strong></p>
              <p>В периодических печатных изданиях и ответ, и опровержение должны быть опубликованы тем же шрифтом и размещены под заголовком  «Ответ» на том месте, где было расположено опровергаемое сообщение или материал.</p>
              <p>По радио и телевидению ответ или опровержение должны быть переданы в то же время суток и в той же теле-, радиопрограмме, что и опровергаемое сообщение или материал, а в случаях закрытия указанной теле-, радиопрограммы - в иной теле-, радиопрограмме с соответствующей тематической направленностью.</p>
              <p>Объем ответа или опровержения не может вдвое и более превышать объем опровергаемого сообщения или материала.</p>
              <p><strong>ВАЖНО! Требование гражданина или юридического лица о публикации опровержения либо ответа в СМИ рассматривается судом в случае, если СМИ отказало в такой публикации либо в течение месяца не опубликовало опровержение или ответ, а также в случае ликвидации данного СМИ.</strong></p>
              <p><strong>Иски о защите чести и достоинства </strong></p>
              <p>Согласно статье 143 ГК гражданин вправе требовать в судебном порядке опровержения сведений, порочащих его честь, достоинство или деловую репутацию.</p>
              <p>Необходимые условия для предъявления иска:</p>
              <ul>
              <li>Сведения распространены,</li>
              <li>они порочат честь и достоинство гражданина, репутацию организации,</li>
              <li>они соответствуют действительности.</li>
              </ul>
              <p>Что значат  «порочащие сведения»?</p>
              <p>Порочащими являются такие не соответствующие действительности сведения, которые умаляют честь и достоинство гражданина или организации в общественном мнении или мнении отдельных граждан с точки зрения соблюдения законов, моральных принципов общества (например, сведения о совершении нечестного поступка, недостойном поведении в трудовом коллективе, в семье; сведения, опорочивающие производственно-хозяйственную деятельность, репутацию и т.п.). В то же время не могут признаваться обоснованными требования об опровержении сведений, содержащих соответствующую действительности критику недостатков в работе, в общественном месте, в коллективе, в быту.</p>
              <p><strong>Журналист не несет ответственность за распространение сведений, не соответствующих действительности</strong> (это не касается запрещенной или иным образом ограниченной к распространению вступившими в законную силу судебными актами или законами информации), если:</p>
              <ul>
              <li>Если информация содержалась в официальных, сообщениях (например, пресс-релиз государственного органа),</li>
              <li>Если информация содержалась в ответе на запрос СМИ,</li>
              <li>Если информация получена от рекламодателей и рекламопроизводителей;</li>
              <li>Если информация являлась дословным воспроизведением официальных выступлений депутатов представительных органов, должностных или уполномоченных лиц государственных органов, органов местного самоуправления, физических и юридических лиц, за исключением распространения информации, запрещенной или иным образом ограниченной к распространению вступившими в законную силу судебными актами или законами. То есть если человек выступил на собрании, митинге или ином публичных мероприятии, вы его процитировали, а оказалось, что его речь содержит недостоверную информацию, вы не будете нести ответственность за его высказывания. Но здесь есть условие: вы должны зафиксировать его речь и при публикации оформить фразы как прямую речь.</li>
              <li>Если информация содержалась в авторских выступлениях, идущих в эфир без предварительной записи, либо текстах, не подлежащих редактированию в соответствии с настоящим Законом. Обратите внимание на фразу  «идущих в эфир без предварительной записи».</li>
              <li>Если недостоверная информация была распространена другим средством информации, вы ее перепечатали, указали ссылку на первоисточник, но СМИ-первоисточник должно быть поставлено на учет в уполномоченном органе, т.е. в Министерстве информации и общественного развития РК. Это значит, что вы не можете ссылаться на иностранные СМИ, которые не поставлены на учет как СМИ в Казахстане, либо на сайты, не поставленные на учет как сетевые издания.</li>
              </ul>
              <p><strong>Мнения и утверждения</strong></p>
              <p>Мнение (оценочное суждение) - это личная точка зрения, которая представляет точку зрения человека. Оно не может быть проверено на предмет действительности, поэтому не является предметом судебной защиты и не должно быть наказуемыми. Каждый человек имеет право на собственное мнение.</p>
              <p>Утверждение о факте - это то, что действительно имело место или о котором известно, что оно существует, что может быть подтверждено доказательствами.</p>
              <p>Определить, являлось ли высказывание оценочным суждением либо утверждение о факте, может судебная лингвистическая экспертиза.</p>
              <p>Важно! Многие считают, что если в материале поставить фразы  «я думаю»,  «на мой взгляд», то это защитит от судебных исков. Но на самом деле это не так.</p>
              <p><strong>Можно ли требовать извинения за публикацию?</strong></p>
              <p>Нет, извинение как способ судебной защиты в законодательстве РК не предусмотрено, поэтому суд не вправе обязывать ответчиков принести истцам извинение, даже если они указывают это в своих исковых требованиях.</p>
              <p><strong>Компенсация морального вреда </strong></p>
              <p>Физическое лицо, личные неимущественные права которого нарушены, имеет право на возмещение морального вреда.</p>
              <p>Моральный вред - это нравственные или физические страдания, испытываемые гражданином в результате противоправного нарушения, умаления или лишения принадлежащих ему личных неимущественных благ и прав.</p>
              <p>Под нравственными страданиями (эмоционально-волевыми переживаниями человека) следует понимать испытываемые им чувства унижения, раздражения, подавленности, гнева, стыда, отчаяния, ущербности, состояние дискомфорта и т. д. Эти чувства могут быть вызваны, например, противоправным посягательством на жизнь и здоровье как самого потерпевшего, так и его близких родственников, супруга/супруги; незаконным лишением или ограничением свободы либо права свободного передвижения; причинением вреда здоровью, в том числе уродующие открытые части тела человека шрамами и рубцами; раскрытием семейной, личной или врачебной тайны; нарушением тайны переписки, телефонных или телеграфных сообщений; распространением не соответствующих действительности сведений, порочащих честь и достоинство гражданина; нарушением права на имя, на изображение; нарушением его авторских и смежных прав и т. д.</p>
              <p><strong>ВАЖНО! В исковом заявлении истец обязан указать обстоятельства и привести доказательства, подтверждающие факт нарушения его личных неимущественных благ и прав, необходимость их защиты, а также сумму компенсации, которая, по его мнению, обеспечит возмещение причиненного ему морального вреда.</strong></p>
              <p>Моральный вред компенсируется в денежной форме. При этом сумму компенсации суд определяет исходя из критериев разумности и справедливости, принимая во внимание:</p>
              <ul>
              <li>жизненную важность личных неимущественных благ и прав (жизнь, здоровье, свобода, неприкосновенность жилища, личная и семейная тайна, честь и достоинство и т. д.);</li>
              <li>степень испытываемых потерпевшим нравственных или физических страданий (лишение свободы, причинение телесных повреждений, утрата близких родственников, утрата или ограничение трудоспособности и т. д.);</li>
              <li>форму вины (умысел, неосторожность) причинителя вреда, когда для возмещения морального вреда необходимо ее наличие.</li>
              </ul>
            </div>
          </div>
        </div>
      </div>
      <!-------- ДЕТИ В МЕДИА ---------->
      <div id="deti" class="accordion-item">
        <p class="parent" @click="changeToActive">ДЕТИ В МЕДИА</p>
        <div class="accordion-item child nested hidden">
          <p class="parent" @click="changeToActive">ОБЩИЕ ПРАВИЛА</p>
          <div class="accordion-item child hidden">
            <div class="child text">
              <p>
                У всех детей, как и у взрослых, есть право на собственное
                изображение и защиту персональных данных. При проведении
                интервью, фото и видеосъемки с детьми необходимо согласовать
                действия с родителями или законными представителями, поскольку в
                соответствии с Законом «О браке и семье» (ст.71) родители
                являются законными представителями своего ребенка и выступают в
                защиту его прав и интересов в отношениях с любыми физическими и
                юридическими лицами, в том числе в суде, без специальных
                полномочий.
              </p>
              <h2>Особые случаи</h2>
              <p>
                Дети, пострадавшие в результате противоправных действий
                (бездействия) и подозреваемые и (или) обвиняемые в совершении
                административных правонарушений.
              </p>
              <h2>НЕЛЬЗЯ:</h2>
              <ul>
                <li>
                  распространять в СМИ или сетях телекоммуникаций персональные и
                  биометрические данные такого ребенка, включая информацию об
                  его родителях и иных законных представителях, иной информации,
                  позволяющей установить личность.
                </li>
              </ul>
              <h2>МОЖНО:</h2>
              <ul>
                <li>
                  если несовершеннолетний признан судом виновным в совершении
                  тяжких и (или) особо тяжких преступлений, включая информацию о
                  его родителях и иных законных представителях;
                </li>
                <li>
                  если есть согласие несовершеннолетнего, достигшего 14-летнего
                  возраста, пострадавшего в результате противоправных действий
                  (бездействия), и его законных представителей;
                </li>
                <li>
                  если есть согласие законных представителей
                  несовершеннолетнего, не достигшего 14-летнего возраста,
                  пострадавшего в результате противоправных действий
                  (бездействия);
                </li>
                <li>
                  если есть согласие несовершеннолетнего, достигшего
                  16-тилетнего возраста, совершившего административные и (или)
                  уголовные правонарушения, и его законных представителей;
                </li>
                <li>
                  если речь идет о распространении в СМИ информации, полученной
                  от органов, ведущих уголовный процесс (в целях содействия
                  расследованию преступления, установлению лиц, причастных к
                  совершению преступления, розыску пропавших несовершеннолетних
                  в объеме, необходимом для достижения указанных целей);
                </li>
                <li>
                  если есть согласие одного законного представителя
                  пострадавшего несовершеннолетнего в случае, если второй
                  законный представитель находится за пределами Республики
                  Казахстан и его согласие получить не представляется возможным;
                </li>
                <li>
                  если законный представитель является подозреваемым или
                  обвиняемым в совершении противоправных действий в отношении
                  несовершеннолетнего, достигшего 14-тилетнего возраста и
                  пострадавшего в результате противоправных действий, или его
                  законных представителей;
                </li>
              </ul>
              <h2>Ответственность за нарушение закона:</h2>
              <p>Штраф на физических лиц – 5 МРП</p>
              <p>
                на должностных лиц, субъектов малого предпринимательства или
                некоммерческие организации – 25 МРП,
              </p>
              <p>на субъектов среднего предпринимательства – 50 МРП,</p>
              <p>на субъектов крупного предпринимательства - 100 МРП.</p>
              <p>
                В случаях повторного нарушения в течение года после наложения
                административного взыскания, -
              </p>
              <p>
                штраф на физических и должностных лиц, субъектов малого
                предпринимательства или некоммерческие организации – 50 МРП,
              </p>
              <p>на субъектов среднего предпринимательства – 100 МРП,</p>
              <p>на субъектов крупного предпринимательства - 200 МРП.</p>
            </div>
          </div>
        </div>
        <div class="accordion-item child nested hidden">
          <p class="parent" @click="changeToActive">
            ЗАКОНОДАТЕЛЬНАЯ БАЗА
          </p>
          <div class="accordion-item child nested hidden">
            <p class="parent" @click="changeToActive">ЗАКОН О СМИ</p>
            <div class="accordion-item child hidden">
              <div class="child text">
                <h2>
                  Статья 14. Распространение продукции средства массовой
                  информации
                </h2>
                <p>
                  3-4. Запрещается распространение в средствах массовой
                  информации или сетях телекоммуникаций персональных и
                  биометрических данных лица, включая информацию об его
                  родителях и иных законных представителях, иной информации,
                  позволяющей установить личность, о:
                </p>
                <ol>
                  <li>
                    ребенке, пострадавшем в результате противоправных действий
                    (бездействия);
                  </li>
                  <li>
                    несовершеннолетних, подозреваемых и (или) обвиняемых в
                    совершении административных и (или) уголовных
                    правонарушений.
                  </li>
                </ol>
                <p>
                  Действие подпункта 2) части первой настоящего пункта не
                  распространяется на несовершеннолетних, признанных судом
                  виновными в совершении тяжких и (или) особо тяжких
                  преступлений, включая информацию об их родителях и иных
                  законных представителях.
                </p>
                <p>
                  Ограничения, установленные настоящим пунктом, не
                  распространяются на случаи, предусмотренные частью второй
                  пункта 2-2 статьи 25 настоящего Закона.
                </p>
                <h2>
                  Статья 25. Ответственность за нарушение законодательства
                  Республики Казахстан о средствах массовой информации
                </h2>
                <p>
                  2-2. Собственник, главный редактор (редактор), журналист
                  средства массовой информации, в том числе физические или
                  юридические лица, использующие интернет-ресурсы, не вправе
                  разглашать в распространяемых сообщениях и материалах
                  информацию, указанную в пункте 3-4 статьи 14 настоящего
                  Закона, за исключением случаев, если распространение такой
                  информации осуществляется в целях защиты прав и законных
                  интересов ребенка, пострадавшего в результате противоправных
                  действий (бездействия), и несовершеннолетних, подозреваемых и
                  (или) обвиняемых в совершении административных и (или)
                  уголовных правонарушений, за исключением несовершеннолетних,
                  признанных судом виновными в совершении тяжких или особо
                  тяжких преступлений, включая информацию об их родителях и иных
                  законных представителях.
                </p>
                <p>
                  Информация, указанная в части первой настоящего пункта, может
                  распространяться в средствах массовой информации с согласия:
                </p>
                <ol>
                  <li>
                    несовершеннолетнего, достигшего четырнадцатилетнего
                    возраста, пострадавшего в результате противоправных действий
                    (бездействия), и его законных представителей;
                  </li>
                  <li>
                    законных представителей несовершеннолетнего, не
                    достигшего четырнадцатилетнего возраста, пострадавшего в
                    результате противоправных действий (бездействия);
                  </li>
                  <li>
                    несовершеннолетнего, достигшего шестнадцатилетнего
                    возраста, совершившего административные и (или) уголовные
                    правонарушения, и его законных представителей.
                  </li>
                </ol>
                <p>
                  В случае, если одно из лиц, указанных в части второй
                  настоящего пункта, не дает своего согласия на распространение
                  информации в средствах массовой информации, распространение
                  данной информации запрещено, за исключением следующих случаев:
                </p>
                <ol>
                  <li>
                    без согласия несовершеннолетнего, достигшего
                    четырнадцатилетнего возраста и пострадавшего в результате
                    противоправных действий (бездействия), или его законных
                    представителей, если законный представитель является
                    подозреваемым или обвиняемым в совершении данных
                    противоправных действий (бездействии);
                  </li>
                  <li>
                    с согласия одного законного представителя пострадавшего
                    несовершеннолетнего в случае, если второй законный
                    представитель находится за пределами Республики Казахстан и
                    его согласие получить не представляется возможным.
                  </li>
                </ol>
                <p>
                  2-3. Средства массовой информации вправе в целях содействия
                  расследованию преступления, установлению лиц, причастных к
                  совершению преступления, розыску пропавших несовершеннолетних
                  в объеме, необходимом для достижения указанных целей, и с
                  соблюдением требований, установленных Уголовно-процессуальным
                  кодексом Республики Казахстан, на основании процессуальных
                  документов, полученных от органов, ведущих уголовный процесс,
                  распространять в средствах массовой информации сведения,
                  относящиеся к несовершеннолетнему, без получения согласия лиц,
                  установленных частью второй пункта 2-2 настоящей статьи.
                </p>
              </div>
            </div>
          </div>
        </div>
