      <!------- ДОСТУП К ИНФОРМАЦИИ ----->
      <div id="dostup_info" class="accordion-item">
        <p class="parent" @click="changeToActive">ДОСТУП К ИНФОРМАЦИИ</p>
        <div class="accordion-item child nested hidden">
          <p class="parent" @click="changeToActive">
            ВИДЫ ИНФОРМАЦИИ, ПРОЦЕДУРЫ, СРОКИ ПРЕДОСТАВЛЕНИЯ
          </p>
          <div class="accordion-item child hidden">
            <div class="child text">
              <h2>Какие виды информации предусмотрены законом о СМИ?</h2>
              <ul>
                <li>Официальные сообщения</li>
                <li>
                  Запросы о предоставлении информации, не входящей в официальные
                  сообщения
                </li>
                <li>Обязательные сообщения</li>
              </ul>
              <h2>
                В какой форме государственный орган предоставляет информацию?
              </h2>
              <p>
                Официальные сообщения могут распространяться в устной или
                письменной форме, в том числе в виде электронного документа.
                Устная форма предусматривает публичное выступление руководителя
                или уполномоченного на это должностного лица.
              </p>
              <p>
                Официальное сообщение предоставляется СМИ на равных условиях,
                независимо от их форм собственности и принадлежности.
              </p>
              <h2>Процедура и сроки предоставления официальной информации</h2>
              <p>
                Журналист имеет право отправить запрос по разъяснению
                предоставленных официальных сообщений, на что ему должны
                ответить <b>в течение двух рабочих дней</b> со дня регистрации
                запроса либо дать ответ с указанием срока его представления или
                мотивированного отказа.
              </p>
              <p>
                В случаях, требующих дополнительного изучения и проверки, срок
                рассмотрения может быть однократно продлен руководителем
                обладателя информации не более чем на
                <b>пятнадцать календарных дней</b>, о чем СМИ сообщается в
                течение двух рабочих дней с момента продления срока
                рассмотрения.
              </p>
              <p>
                В случае поступления запроса по разъяснению предоставленных
                официальных сообщений, в компетенцию которого не входит
                разрешение поставленных вопросов, в срок
                <b>не позднее двух рабочих дней</b> данный запрос должен быть
                направлен в соответствующие органы с сообщением об этом СМИ.
              </p>
              <h2>В какой форме вы можете отправить запрос?</h2>
              <p>Письменно, в том числе в виде электронного документа.</p>
              <p>
                Запрос должен быть подписан и (или) удостоверен электронной
                цифровой подписью главного редактора (редактора),
                уполномоченного лица или аккредитованного журналиста.
              </p>
              <p>Ответ предоставляется на языке обращения.</p>
              <h2>
                Процедура предоставления информации, не входящей в официальные
                сообщения
              </h2>
              <p>Форма запроса: устная, письменная.</p>
              <p>
                Если журналист обращается к обладателю информации с устным
                запросом по телефону, он обязан представиться и сообщить об
                аудиозаписи разговора в случае ее ведения.
              </p>
              <p>
                Запросы, поданные письменно или в виде электронного документа,
                направляются за подписью и или удостоверяются электронной
                цифровой подписью главного редактора (редактора),
                уполномоченного лица или аккредитованного журналиста.
              </p>
              <h2>Сроки предоставления ответа</h2>
              <p>
                Ответ представляется в течение семи рабочих дней со дня его
                поступления в форме.
              </p>
              <p>
                В случаях, когда запрашиваемая информация входит в компетенцию
                нескольких обладателей информации и при ответе на письменный
                запрос требуется получение информации от иных обладателей
                информации, срок рассмотрения может быть однократно продлен
                руководителем обладателя информации
                <b>не более чем на пятнадцать календарных дней</b>, о чем
                средству массовой информации сообщается в течение двух рабочих
                дней с момента продления срока рассмотрения.
              </p>
