            <div class="accordion-item child hidden">
              <div class="child text">
                <h2>Статья 18. Официальные сообщения</h2>
                <p>
                  1. Официальное сообщение может распространяться в устной или
                  письменной форме, в том числе в виде электронного документа.
                  Распространение официального сообщения в устной форме может
                  осуществляться путем публичного выступления руководителя или
                  уполномоченного на это должностного лица обладателя
                  информации.
                </p>
                <p>
                  При распространении в письменной форме, в том числе в виде
                  электронного документа, официальное сообщение должно быть
                  подписано руководителем или уполномоченным на это должностным
                  лицом обладателя информации (в случае электронного документа -
                  удостоверено электронной цифровой подписью).
                </p>
                <p>
                  2. Официальное сообщение предоставляется средствам массовой
                  информации на равных условиях, независимо от их форм
                  собственности и принадлежности.
                </p>
                <p>
                  3. На письменный запрос средства массовой информации по
                  разъяснению предоставленных официальных сообщений обладатель
                  информации обязан представить ответ не позднее двух рабочих
                  дней со дня регистрации запроса в порядке, определенном
                  законодательством Республики Казахстан, либо дать ответ с
                  указанием срока его представления или мотивированного отказа.
                </p>
                <p>
                  На письменный запрос по разъяснению предоставленных
                  официальных сообщений, требующий дополнительного изучения и
                  проверки, срок рассмотрения может быть однократно продлен
                  руководителем обладателя информации не более чем на пятнадцать
                  календарных дней, о чем средству массовой информации
                  сообщается в течение двух рабочих дней с момента продления
                  срока рассмотрения.
                </p>
                <p>
                  4. В случае поступления обладателю информации от средства
                  массовой информации письменного запроса по разъяснению
                  предоставленных официальных сообщений, в компетенцию которого
                  не входит разрешение поставленных вопросов, в срок не позднее
                  двух рабочих дней данный запрос должен быть направлен в
                  соответствующие органы с сообщением об этом средству массовой
                  информации.
                </p>
                <p>
                  Запросы средств массовой информации по разъяснению
                  предоставленных официальных сообщений, поданные письменно и
                  (или) в виде электронного документа, направляются за подписью
                  и (или) удостоверяются электронной цифровой подписью главного
                  редактора (редактора), уполномоченного лица или
                  аккредитованного журналиста.
                </p>
                <p>
                  5. Ответы на запросы по разъяснению предоставленных
                  официальных сообщений, поданные письменно или в виде
                  электронного документа, представляются на языке обращения.
                </p>
                <p>
                  Ответы на запросы по разъяснению предоставленных официальных
                  сообщений представляются в форме поступившего запроса, если
                  иное не указано в самом запросе.
                </p>
                <p>
                  6. Отказ в предоставлении запрашиваемых сведений может быть
                  обжалован представителем средства массовой информации в
                  вышестоящий орган или должностному лицу в порядке
                  подчиненности либо в суд в порядке, предусмотренном
                  законодательством Республики Казахстан.
                </p>
                <h2>
                  Статья 18-1. Запросы о предоставлении информации, не входящей
                  в официальные сообщения
                </h2>
                <p>
                  1. Средство массовой информации вправе обращаться к обладателю
                  информации с запросами о предоставлении информации, не
                  входящей в официальное сообщение.
                </p>
                <p>
                  Журналист средства массовой информации при обращении к
                  обладателю информации с устным запросом по телефону обязан
                  представиться и сообщить об аудиозаписи разговора в случае ее
                  ведения.
                </p>
                <p>
                  Запросы средств массовой информации о предоставлении
                  информации, поданные письменно и (или) в виде электронного
                  документа, направляются за подписью и (или) удостоверяются
                  электронной цифровой подписью главного редактора (редактора),
                  уполномоченного лица или аккредитованного журналиста.
                </p>
                <p>
                  Ответ на запрос представляется в течение семи рабочих дней со
                  дня его поступления в форме и на языке поступившего запроса,
                  если иное не указано в самом запросе.
                </p>
                <p>
                  В случаях, когда запрашиваемая информация входит в компетенцию
                  нескольких обладателей информации и при ответе на письменный
                  запрос требуется получение информации от иных обладателей
                  информации, срок рассмотрения может быть однократно продлен
                  руководителем обладателя информации не более чем на пятнадцать
                  календарных дней, о чем средству массовой информации
                  сообщается в течение двух рабочих дней с момента продления
                  срока рассмотрения.
                </p>
                <p>2. В предоставлении информации по запросу отказывается:</p>
                <ol>
                  <li>
                    если содержание запроса не входит в компетенцию обладателя
                    информации, которому был адресован запрос;
                  </li>
                  <li>
                    если запрос не соответствует требованиям настоящего Закона;
                  </li>
                  <li>
                    если запрашиваемая информация относится к информации с
                    ограниченным доступом;
                  </li>
                  <li>
                    до принятия решения по результатам проверок, проводимых в
                    рамках государственного контроля и надзора;
                  </li>
                  <li>
                    до принятия окончательного решения, вырабатываемого на
                    основе межведомственной и внутриведомственной переписки или
                    совещаний в государственных органах;
                  </li>
                  <li>
                    до принятия взаимного соглашения об условиях раскрытия
                    документов, поступивших от иностранных государств или
                    международных организаций.
                  </li>
                </ol>
                <p>
                  3. За непредставление или несвоевременное представление ответа
                  на запрос средств массовой информации должностные лица
                  государственных органов несут ответственность, установленную
                  законами Республики Казахстан.
                </p>
                <p>
                  4. Отказ в предоставлении запрашиваемой информации может быть
                  обжалован представителем средства массовой информации в
                  порядке подчиненности либо в суд в порядке, предусмотренном
                  законами Республики Казахстан для обжалования неправомерных
                  действий органов государственного управления и должностных
                  лиц, ущемляющих права граждан.
                </p>
                <h2>Статья 18-2. Обязательные сообщения</h2>
                <p>
                  1. Уполномоченные государственные органы соответствующей
                  отрасли в рамках своей компетенции и (или) местные
                  исполнительные органы при нарушении условий жизнедеятельности
                  населения на определенной территории обязаны не позднее трех
                  часов с момента наступления события предоставлять средствам
                  массовой информации официальные сообщения о текущей ситуации в
                  соответствии с порядком, определенным уполномоченным органом.
                </p>
                <p>
                  За непредоставление или несвоевременное предоставление
                  официальных сообщений должностные лица государственных органов
                  несут ответственность, установленную законами Республики
                  Казахстан.
                </p>
                <p>
                  2. Редакции средств массовой информации обязаны на
                  безвозмездной основе опубликовать:
                </p>
                <ol>
                  <li>
                    решение суда, вступившее в законную силу, если оно содержит
                    требование об опубликовании ответа или опровержения в
                    средстве массовой информации, которое являлось ответчиком по
                    данному судебному процессу;
                  </li>
                  <li>
                    официальные сообщения о введении чрезвычайного положения в
                    соответствии со статьей 5 Закона Республики Казахстан «О
                    чрезвычайном положении»;
                  </li>
                  <li>
                    официальные сообщения уполномоченных государственных
                    органов, предусмотренные статьей 47 Закона Республики
                    Казахстан «О гражданской защите»;
                  </li>
                  <li>
                    официальные сообщения уполномоченных государственных
                    органов, предусмотренные пунктом 2 статьи 15 Закона
                    Республики Казахстан «О противодействии терроризму»;
                  </li>
                  <li>
                    официальные сообщения уполномоченных государственных
                    органов, предусмотренные пунктом 1 настоящей статьи.
                  </li>
                </ol>
                <p>3. Обязательные сообщения публикуются:</p>
                <p></p>
                <ol>
                  <li>
                    теле-, радиоканалами, выходящими в эфир ежедневно, - в
                    течение суток;
                  </li>
                  <li>
                    средствами массовой информации с иной периодичностью или
                    временем выхода в свет (эфир) - в срок, установленный
                    уполномоченными государственными органами либо судом для
                    таких публикаций.
                  </li>
                </ol>
                <h2>
                  Статья 18-3. Уполномоченное лицо (подразделение) по
                  взаимодействию со средствами массовой информации
                </h2>
                <p>
                  1. В центральных государственных и местных исполнительных
                  органах, ведомствах, а также государственных органах,
                  непосредственно подчиненных и подотчетных Президенту
                  Республики Казахстан (за исключением Службы государственной
                  охраны), назначается уполномоченное лицо либо создается
                  уполномоченное подразделение по взаимодействию со средствами
                  массовой информации, осуществляющее распространение информации
                  по вопросам, относящимся
                </p>
                <p>
                  к компетенции государственного органа, а также его
                  деятельности.
                </p>
                <p>
                  Самостоятельное структурное уполномоченное подразделение и
                  (или) штатная должность создаются в пределах лимитов штатной
                  численности, утвержденных в соответствии с законодательством
                  Республики Казахстан, а также подчиняются непосредственно
                  первому руководителю государственного органа.
                </p>
                <p>
                  В государственных органах, указанных в части первой настоящего
                  пункта, со штатной численностью менее двадцати пяти человек
                  функции, установленные пунктом 2 настоящей статьи, возлагаются
                  на одного из сотрудников государственного органа без
                  освобождения от его основных функциональных обязанностей.
                </p>
                <p>
                  2. Основными функциями уполномоченного лица (подразделения) по
                  взаимодействию со средствами массовой информации являются:
                </p>
                <ol>
                  <li>
                    предоставление пользователям информации достоверной и
                    необходимой информации по вопросам, относящимся к
                    компетенции государственного органа, а также его
                    деятельности;
                  </li>
                  <li>
                    содействие в подготовке интервью, комментариев, публикаций в
                    средствах массовой информации;
                  </li>
                  <li>
                    проведение временной или постоянной аккредитации журналистов
                    и других представителей средств массовой информации, а также
                    оказание им содействия для наиболее полного и широкого
                    распространения информации по вопросам, относящимся к
                    компетенции государственного органа, а также его
                    деятельности;
                  </li>
                  <li>
                    организация пресс-конференций, брифингов, интервью и встреч
                    должностных лиц с представителями средств массовой
                    информации;
                  </li>
                  <li>
                    разработка и внесение предложений по вопросам технического
                    обеспечения деятельности интернет-ресурса, его комплексной
                    реконструкции, совершенствования дизайна, модернизации и
                    внедрения новых технологий;
                  </li>
                  <li>
                    подготовка своевременных ответов на поступающие запросы
                    пользователей информации;
                  </li>
                  <li>
                    размещение информации на интернет-ресурсе государственного
                    органа и (или) его первого руководителя;
                  </li>
                  <li>
                    иные функции, способствующие максимальному раскрытию
                    информации по вопросам, относящимся к компетенции
                    государственного органа, а также его деятельности, за
                    исключением информации, запрещенной или иным образом
                    ограниченной к распространению законами Республики Казахстан
                    или вступившими в законную силу судебными актами.
                  </li>
                </ol>
                <p>
                  3. Уполномоченное подразделение по взаимодействию со
                  средствами массовой информации осуществляет свою деятельность
                  на основании положения, разработанного на основе типового
                  положения, утвержденного уполномоченным органом, с учетом
                  специфики деятельности специальных государственных органов и
                  Национального Банка Республики Казахстан.
                </p>
                <p>
                  4. Уполномоченное лицо (подразделение) по взаимодействию со
                  средствами массовой информации взаимодействует с
                  уполномоченным органом в соответствии с порядком, определенным
                  уполномоченным органом.
                </p>
                <p>
                  5. Уполномоченное лицо (сотрудники подразделения) по
                  взаимодействию со средствами массовой информации проходят
                  курсы повышения квалификации в порядке, определенном
                  законодательством Республики Казахстан.
                </p>
                <p>
                  6. Уполномоченное лицо (подразделение) по взаимодействию со
                  средствами массовой информации не подлежит сокращению
                  (исключению) по штатной численности (из штатного расписания)
                  государственного органа, указанного в пункте 1 настоящей
                  статьи, за исключением случаев ликвидации (упразднения) или
                  реорганизации государственного органа.
                </p>
              </div>
