            <div class="accordion-item child hidden">
              <div class="child text">
                <h2>
                  Статья 2. Основные понятия, используемые в настоящем Законе
                </h2>
                <ul class="list-type-none">
                  <li>
                    1) автор - физическое лицо, творческим трудом которого
                    создано произведение науки, литературы, искусства;
                  </li>
                  <li>
                    2) авторское право - личные неимущественные и имущественные
                    права автора;
                  </li>
                  <li>
                    5) авторский договор - договор, предметом которого является
                    передача имущественных прав на использование одного или
                    более объектов авторского права. Авторский договор является
                    разновидностью лицензионного договора;
                  </li>
                  <li>
                    6) неисключительное право - право, когда одновременно с
                    правообладателем другие лица могут использовать
                    произведения, исполнения, постановки, фонограммы, передачи
                    организаций эфирного и кабельного вещания, имея на то
                    соответствующее разрешение автора или иного правообладателя,
                    кроме случаев, установленных настоящим Законом;
                  </li>
                  <li>
                    7) исключительное право - имущественное право автора и (или)
                    иного правообладателя осуществлять, разрешать и запрещать
                    использование произведения и (или) объекта смежных прав
                    любым способом в течение установленного срока;
                  </li>
                  <li>
                    10) аудиовизуальное произведение - произведение, состоящее
                    из зафиксированной серии связанных между собой кадров или
                    изображений (с сопровождением или без сопровождения их
                    звуком), предназначенное для зрительного и слухового (в
                    случае сопровождения звуком) восприятия с помощью
                    соответствующих технических устройств. Аудиовизуальные
                    произведения включают кинематографические произведения и все
                    произведения, выраженные средствами, аналогичными
                    кинематографическим (теле- и видеофильмы, диафильмы и
                    слайдофильмы и тому подобные произведения), независимо от
                    способа их первоначальной или последующей фиксации;
                  </li>
                  <li>
                    11) производитель аудиовизуального произведения - физическое
                    или юридическое лицо, взявшее на себя инициативу и
                    ответственность за создание такого произведения. При
                    отсутствии доказательства иного производителем
                    аудиовизуального произведения признается лицо, имя или
                    наименование которого обозначено на этом произведении;
                  </li>
                  <li>
                    12) запись - фиксация звуков и (или) изображений,
                    позволяющая их неоднократное восприятие, воспроизведение или
                    сообщение с помощью технических средств в какой-либо
                    материальной форме;
                  </li>
                  <li>
                    13) доведение до всеобщего сведения - сообщение объектов
                    авторского права и (или) смежных прав по проводам или
                    средствами беспроволочной связи, при котором публика может
                    осуществлять доступ к ним из любого места и в любое время по
                    ее собственному выбору (в интерактивном режиме);
                  </li>
                  <li>
                    14) опубликование - предложение публике с согласия автора
                    либо иного правообладателя на объекты авторского или смежных
                    прав экземпляров произведения, записи исполнения или
                    фонограммы в количестве, удовлетворяющем разумные
                    потребности публики;
                  </li>
                  <li>
                    14-1) интернет-ресурс - электронный информационный ресурс,
                    отображаемый в текстовом, графическом, аудиовизуальном или
                    ином виде, размещаемый на аппаратно-программном комплексе,
                    имеющий уникальный сетевой адрес и (или) доменное имя и
                    функционирующий в Интернете;
                  </li>
                  <li>
                    15) сообщение для всеобщего сведения по кабелю - сообщение
                    произведения, фонограммы, исполнения, передачи организаций
                    эфирного или кабельного вещания для всеобщего сведения
                    посредством кабеля, провода, оптического волокна или с
                    помощью аналогичных средств;
                  </li>
                  <li>
                    16) публичное исполнение - исполнение произведения
                    посредством декламации, игры, танца или каким-либо иным
                    образом, в том числе с помощью технических средств, в
                    местах, где присутствуют или могут присутствовать лица, не
                    являющиеся членами семьи;
                  </li>
                  <li>
                    17) публичный показ - показ оригинала или экземпляра
                    произведения непосредственно или в виде слайда, кино-,
                    телекадра на экране с помощью любого другого технического
                    средства или любым иным способом (в отношении
                    аудиовизуального произведения - показ отдельных кадров вне
                    их последовательности) в местах, где присутствуют или могут
                    присутствовать лица, не являющиеся членами семьи;
                  </li>
                  <li>
                    19) воспроизведение - изготовление одного или более
                    постоянных или временных экземпляров произведений или
                    объектов смежных прав любым способом и в любой форме,
                    полностью или частично, непосредственно или косвенно. Видами
                    воспроизведения являются изготовление звуко- или
                    видеозаписи, изготовление одного или более экземпляров
                    двухмерного или трехмерного произведения, репродуцирование
                    (репрографическое воспроизведение), а также любое постоянное
                    или временное хранение произведений или объектов смежных
                    прав в любой материальной форме, в том числе в открытой
                    информационно-коммуникационной сети;
                  </li>
                  <li>
                    20) правообладатель - автор (его наследники) в отношении
                    авторских прав, исполнитель (его наследники), производитель
                    фонограмм, организация эфирного или кабельного вещания в
                    отношении смежных прав, а также иные физические или
                    юридические лица, получившие исключительное право на
                    использование произведения и (или) объекта смежных прав по
                    договору или иному основанию, предусмотренному настоящим
                    Законом;
                  </li>
                  <li>
                    32) обнародование произведения - осуществление с согласия
                    автора действия, которое впервые делает произведение
                    доступным публике посредством его опубликования, публичного
                    показа, публичного исполнения, доведения до всеобщего
                    сведения и иными способами;
                  </li>
                  <li>
                    45) последующая передача в эфир - передача ранее переданных
                    в эфир или сообщенных для всеобщего сведения по кабелю
                    произведений или объектов смежных прав;
                  </li>
                  <li>
                    45-1) организации эфирного и кабельного вещания - физические
                    и юридические лица, осуществляющие сообщение в эфир и (или)
                    по кабелю теле-, радиоканалов, теле-, радиопрограмм (теле-,
                    радиопередач), включающих произведения, исполнения,
                    постановки, фонограммы. Сообщение в эфир и (или) по кабелю
                    осуществляется посредством аналогового эфирного,
                    многоканального вещания (цифрового эфирного, спутникового,
                    кабельного телерадиовещания);
                  </li>
                </ul>
                <h2>Статья 6. Объект авторского права. Общие положения</h2>
                <p>
                  1. Авторское право распространяется на произведения науки,
                  литературы и искусства, являющиеся результатом творческой
                  деятельности, независимо от их назначения, содержания и
                  достоинства, а также от способа и формы их выражения.
                </p>
                <p>
                  2. Авторское право распространяется как на обнародованные
                  (опубликованные, выпущенные в свет, изданные, публично
                  исполненные, публично показанные), так и на необнародованные
                  произведения, существующие в какой-либо объективной форме:
                </p>
                <ol>
                  <li>
                    письменной (рукопись, машинопись, нотная запись и тому
                    подобное);
                  </li>
                  <li>
                    устной (публичное произнесение, публичное исполнение и тому
                    подобное);
                  </li>
                  <li>
                    звуко- или видеозаписи (механической, цифровой, магнитной,
                    оптической и тому подобной);
                  </li>
                  <li>
                    изображения (рисунок, эскиз, картина, план, чертеж, кино,
                    теле-, видео- или фотокадр и тому подобное);
                  </li>
                  <li>
                    объемно-пространственной (скульптура, модель, макет,
                    сооружение и тому подобное);
                  </li>
                  <li>иных формах.</li>
                </ol>
                <h2>
                  Статья 7. Произведения, являющиеся объектами авторского права
                </h2>
                <p>1. Объектами авторского права являются:</p>
                <ol>
                  <li>литературные произведения;</li>
                  <li>
                    драматические и музыкально-драматические произведения;
                  </li>
                  <li>сценарные произведения;</li>
                  <li>произведения хореографии и пантомимы;</li>
                  <li>музыкальные произведения с текстом или без текста;</li>
                  <li>аудиовизуальные произведения;</li>
                  <li>
                    произведения живописи, скульптуры, графики и другие
                    произведения изобразительного искусства;
                  </li>
                  <li>произведения прикладного искусства;</li>
                  <li>
                    произведения архитектуры, градостроительства, дизайна и
                    садово-паркового искусства;
                  </li>
                  <li>
                    фотографические произведения и произведения, полученные
                    способами, аналогичными фотографии;
                  </li>
                  <li>
                    карты, планы, эскизы, иллюстрации и трехмерные произведения,
                    относящиеся к географии, топографии и к другим наукам;
                  </li>
                  <li>программы для ЭВМ;</li>
                  <li>иные произведения.</li>
                </ol>
                <p>3. К объектам авторского права также относятся:</p>
                <ol>
                  <li>
                    производные произведения (переводы, обработки, аннотации,
                    рефераты, резюме, обзоры, инсценировки, музыкальные
                    аранжировки и другие переработки произведений науки,
                    литературы и искусства);
                  </li>
                  <li>
                    сборники (энциклопедии, антологии, базы данных) и другие
                    составные произведения, представляющие собой по подбору и
                    (или) расположению материалов результат творческого труда.
                  </li>
                  <li>
                    Производные и составные произведения охраняются авторским
                    правом независимо от того, являются ли объектами авторского
                    права произведения, на которых они основаны или которые они
                    включают.
                  </li>
                </ol>
                <h2>
                  Статья 8. Произведения, не являющиеся объектами авторского
                  права
                </h2>
                <p>Не являются объектами авторского права:</p>
                <ol>
                  <li>
                    официальные документы (законы, судебные решения, иные тексты
                    законодательного, административного, судебного и
                    дипломатического характера), а также их официальные
                    переводы;
                  </li>
                </ol>
                <p>
                  О правах на проекты официальных документов см.: Гражданский
                  кодекс РК.
                </p>
                <ol>
                  <li>
                    государственные символы и знаки (флаги, гербы, ордена,
                    денежные знаки и иные государственные символы и знаки);
                  </li>
                  <li>произведения народного творчества;</li>
                  <li>
                    сообщения о событиях и фактах, имеющие информационный
                    характер.
                  </li>
                </ol>
                <h2>
                  Статья 9. Возникновение авторского права. Презумпция авторства
                </h2>
                <p>
                  1. Авторское право на произведение науки, литературы и
                  искусства возникает в силу факта его создания. Для
                  возникновения и осуществления авторского права не требуются
                  регистрация произведения, иное специальное оформление
                  произведения или соблюдение каких-либо формальностей.
                </p>
                <p>
                  Для оповещения о своих исключительных имущественных правах
                  автор и (или) правообладатель вправе использовать знак охраны
                  авторского права, который помещается на каждом экземпляре
                  произведения и обязательно состоит из трех элементов:
                </p>
                <ol>
                  <li>латинской буквы «C» в окружности;</li>
                  <li>
                    имени (наименования) обладателя исключительных авторских
                    прав;
                  </li>
                  <li>года первого опубликования произведения.</li>
                </ol>
                <p>
                  3. При опубликовании произведения анонимно или под псевдонимом
                  (за исключением случая, когда псевдоним автора не оставляет
                  сомнения в его личности) издатель, имя или наименование
                  которого обозначено на произведении, при отсутствии
                  доказательств иного считается представителем автора в
                  соответствии с настоящим Законом и в этом качестве имеет право
                  защищать права автора и обеспечивать их осуществление. Это
                  положение действует до тех пор, пока автор такого произведения
                  не раскроет свою личность и не заявит о своем авторстве.
                </p>
                <h2>
                  Статья 13. Авторское право на аудиовизуальные произведения
                </h2>
                <p>
                  1. Авторами (соавторами) аудиовизуального произведения
                  являются:
                </p>
                <ol>
                  <li>автор сценария;</li>
                  <li>
                    автор музыкального произведения (с текстом или без текста),
                    специально созданного для этого аудиовизуального
                    произведения (композитор);
                  </li>
                  <li>режиссер-постановщик;</li>
                  <li>оператор-постановщик;</li>
                  <li>художник-постановщик.</li>
                </ol>
                <p>
                  Автор ранее созданного произведения, переработанного или
                  включенного составной частью в аудиовизуальное произведение,
                  также считается соавтором аудиовизуального произведения.
                </p>
                <h2>Статья 14. Авторское право на служебные произведения</h2>
                <p>
                  1. Личное неимущественное право автора на произведение,
                  созданное в порядке выполнения служебных обязанностей или
                  служебного задания работодателя (служебное произведение),
                  принадлежит автору служебного произведения.
                </p>
                <p>
                  2. Имущественные (исключительные) права на служебное
                  произведение принадлежат работодателю, если иное не
                  предусмотрено в договоре между ним и автором.
                </p>
                <p>
                  3. Работодатель вправе при любом использовании служебного
                  произведения указывать свое наименование либо требовать такого
                  указания.
                </p>
                <p>
                  5. На создание в порядке выполнения служебных обязанностей или
                  служебного задания работодателя энциклопедий,
                  энциклопедических словарей, периодических и продолжающихся
                  сборников научных трудов, газет, журналов и других
                  периодических изданий (пункт 2 статьи 11 настоящего Закона)
                  положения настоящей статьи не распространяются.
                </p>
              </div>
            </div>
          </div>
          <div class="accordion-item child nested hidden">
            <p class="parent" @click="changeToActive">
              НОРМАТИВНОЕ ПОСТАНОВЛЕНИЕ ВЕРХОВНОГО СУДА О ПРИМЕНЕИИ СУДАМИ НОРМ
              ЗАКОНОДАТЕЛЬСТВА О ЗАЩИТЕ АВТОРСКОГО ПРАВА И СМЕЖНЫХ ПРАВАХ
            </p>
            <div class="accordion-item child hidden">
              <div class="child text">
                <p>
                  2. Авторские права возникают в силу факта создания
                  интеллектуальным творческим трудом автора (соавторов) в
                  объективной форме произведений науки, литературы и искусства,
                  виды которых перечислены в статье 972 ГК, статье 7 Закона, и
                  действуют независимо от регистрации произведения в
                  уполномоченном государственном органе.
                </p>
                <p>
                  Авторские права распространяются как на обнародованные, так и
                  на необнародованные произведения, независимо от их содержания
                  и достоинства, а также способа и формы выражения.
                </p>
                <p>
                  Авторские права распространяются не только на произведение в
                  целом, но и на его часть (название, наименование персонажей),
                  если эта часть произведения является результатом творческой
                  деятельности и может быть использована самостоятельно.
                </p>
                <p>
                  Авторские права на произведение, выраженное в устной форме,
                  распространяются только в том случае, если оно публично
                  произнесено (исполнено).
                </p>
                <p>
                  3. Лица, оказывавшие автору техническую помощь (печать,
                  корректировка и так далее), не могут признаваться соавторами
                  произведения.
                </p>
                <p>4. Авторские права включают в себя:</p>
                <ul>
                  <li>личные неимущественные права автора,</li>
                  <li>имущественные (исключительные) права,</li>
                </ul>
                <p>
                  5. Имущественные (исключительные) права автора действуют в
                  течение всей жизни автора и семидесяти лет после его смерти.
                  Этот срок распространяется на каждое произведение автора,
                  независимо от того, считается ли произведение оконченным,
                  единым или состоящим из отдельных частей. Имущественные
                  (исключительные) права соавторов действуют в течение всей
                  жизни каждого из соавторов и семидесяти лет после смерти
                  автора, пережившего других соавторов.
                </p>
                <p>
                  В случае смерти автора (соавторов) исчисление срока действия
                  имущественных (исключительных) прав автора (соавторов)
                  производится, начиная с первого января года, следующего за
                  годом смерти автора (последнего из соавторов).
                </p>
                <p>
                  6. Имущественные (исключительные) права автора в отношении
                  произведения, обнародованного анонимно или под псевдонимом,
                  действуют в течение семидесяти лет после даты его правомерного
                  обнародования.
                </p>
                <p>
                  Под правомерным обнародованием произведения следует понимать
                  доведение его до всеобщего сведения с согласия автора или его
                  наследников путем опубликования, публичного исполнения,
                  публичного показа, сообщения в эфир, в том числе по кабелю.
                </p>
                <p>
                  Если в течение семидесяти лет после обнародованного анонимно
                  или под псевдонимом произведения его автор раскроет свою
                  личность или его личность не будет вызывать сомнений, то
                  имущественные (исключительные) права автора будут действовать
                  в течение его жизни и семидесяти лет после его смерти.
                </p>
                <p>
                  7. Имущественные (исключительные) права автора произведения,
                  впервые выпущенного в свет (обнародованного) в течение
                  тридцати лет после смерти автора, действуют в течение
                  семидесяти лет после выпуска произведения в свет. Срок
                  действия такого права автора исчисляется с первого января
                  года, следующего за годом выпуска произведения.
                </p>
                <p>
                  Если автор произведения был репрессирован и реабилитирован
                  посмертно, то срок охраны имущественных (исключительных) прав
                  автора на созданные произведения действует в течение
                  семидесяти лет, начиная с первого января года, следующего за
                  годом реабилитации.
                </p>
                <p>
                  8. Истечение срока охраны имущественных (исключительных) прав
                  автора означает, что произведение перешло в общественное
                  достояние. Произведение, перешедшее в общественное достояние,
                  может быть использовано любым лицом без выплаты авторского
                  вознаграждения при соблюдении права авторства, права на имя
                  автора и права на защиту репутации автора произведения.
                </p>
                <p>
                  9. Имущественные (исключительные) права автора или исполнителя
                  произведения, перешедшие к нескольким наследникам по закону
                  или завещанию без конкретизации наследуемых объектов
                  авторского права и (или) смежных прав, свидетельствует о
                  возникновении прав на совместное использование всех
                  произведений автора и (или) объектов смежных прав. При этом ни
                  один из наследников не вправе без достаточных к тому оснований
                  запрещать использование произведений. Вознаграждение за
                  использование произведения распределяется между наследниками
                  соответственно наследственным долям.
                </p>
                <p>
                  10. Авторское право не связано с правом собственности на
                  материальный объект, в котором выражено произведение. Передача
                  прав на материальный объект не влечет передачу имущественных
                  прав на использование произведения.
                </p>
                <p>
                  11. Имущественные (исключительные) права автора, уступленные
                  полностью или частично им или его наследниками по авторскому
                  договору, в случае их нарушения другими физическими или
                  юридическими лицами могут защищаться обладателями таких прав
                  от своего имени путем предъявления иска о запрете использовать
                  произведение нарушителем таким же способом, какой предусмотрен
                  авторским договором.
                </p>
                <p>
                  Если обладатель имущественных (исключительных) прав автора,
                  уступленных ему по авторскому договору, не осуществляет защиту
                  этих нарушенных прав, то такая защита может осуществляться
                  автором или его наследниками самостоятельно.
                </p>
                <p>
                  12. Исходя из содержания и формы авторского договора,
                  предусмотренных статьей 32 Закона, автор или его наследники, а
                  равно работодатель - обладатель имущественных прав на
                  служебное произведение, могут уступать имущественные
                  (исключительные) права другим лицам.
                </p>
                <p>
                  По авторскому договору не могут передаваться исключительные
                  (имущественные) права в отношении произведения, которое автор
                  желает создать в будущем. Условия авторского договора,
                  ограничивающие права автора на создание в будущем произведений
                  на конкретную тему или в конкретной области науки, литературы
                  или искусства, а также противоречащие положениям Закона,
                  являются недействительными.
                </p>
                <p>
                  13. Смежными неимущественными и имущественными правами,
                  предусмотренными соответственно статьями 37, 38, 39 и 40
                  Закона, на постановки, исполнения, фонограммы, передачи
                  организаций эфирного и кабельного вещания обладают
                  исполнители, производители фонограмм и организации эфирного и
                  кабельного вещания.
                </p>
                <p>Смежные неимущественные права являются неотчуждаемыми.</p>
                <p>
                  Имущественные смежные права исполнителя (например, актера,
                  певца, танцора, музыканта, исполнителя эстрадного, циркового
                  или кукольного номера, дирижера, режиссера-постановщика) на
                  исполнение путем воспроизведения; производителя фонограмм на
                  воспроизведение и распространение фонограммы или права
                  организации эфирного и кабельного вешания на использование
                  передачи могут быть полностью или частично уступлены другим
                  лицам на основании лицензионного договора.
                </p>
                <p>
                  В то же время осветители, звукооформители и другие лица,
                  осуществляющие технические функции в процессе исполнения
                  произведения, изготовления фонограмм или передач смежными
                  имущественными правами не обладают.
                </p>
                <p>
                  Под репродуцированием или репрографическим воспроизведением
                  понимается факсимильное воспроизведение в любом размере и
                  форме одного или более экземпляров оригиналов или копий
                  письменных и других графических произведений посредством
                  фотокопирования или с помощью других технических средств,
                  иных, чем издание, в том числе, таких как печать,
                  сканирование, цифровое копирование.
                </p>
                <p>
                  13-1. Размещение объектов авторского права и (или) смежных
                  прав в телекоммуникационных сетях, в частности, в сети
                  Интернет, является использованием данных объектов в виде
                  доведения до всеобщего сведения в соответствии с подпунктом
                  10-1) пункта 2 статьи 16 Закона. Так, запись произведения или
                  объекта смежных прав в память электронно-вычислительной машины
                  является использованием, если по инициативе лица, совершившего
                  запись, неопределенный круг лиц получает доступ к этому
                  произведению или объекту смежных прав. Созданные (полученные)
                  в результате такого использования экземпляры произведений или
                  объектов смежных прав с нарушением Закона являются
                  контрафактными.
                </p>
                <p>
                  Лица, осуществившие подобные действия, признаются нарушителями
                  авторского права и (или) смежных прав.
                </p>
                <p>
                  18. Аудиовизуальное произведение (кинофильм, видеофильм, и
                  иные произведения, указанные в пункте 4 статьи 2 Закона)
                  считается публично исполненным, если оно показано в
                  кинотеатрах или иных местах, открытых для свободного доступа,
                  либо в местах, где присутствуют лица, не принадлежащие к
                  обычному кругу семьи.
                </p>
                <p>
                  К обычному кругу семьи следует относить не только лиц,
                  состоящих между собой в родственных отношениях, но и иных лиц,
                  характер взаимоотношений которых с указанными родственниками
                  свидетельствует о длительности общения, доверительном
                  характере отношений, или о наличии других обстоятельств,
                  позволяющих утверждать о существовании общих интересов.
                </p>
                <p>
                  Авторы музыкального произведения (с текстом или без текста),
                  специально создавшие его для включения в кино, - видеофильмы,
                  спектакли, диафильмы, слайдофильмы и подобные этим
                  произведения, имеют право на получение авторского
                  вознаграждения за использование этого музыкального
                  произведения при каждом публичном показе, публичном исполнении
                  и сообщении, сдаче в прокат (наем) этих аудиовизуальных
                  произведений. Таким же правом обладают авторы ранее созданных
                  музыкальных произведений, впоследствии включенных в
                  аудиовизуальное произведение, спектакль.
                </p>
                <p>
                  19. Произведения, созданные в результате перевода, переделки,
                  аранжировки или другой переработки существующих произведений,
                  относятся к производным произведениям.
                </p>
                <p>
                  Введение в гражданский оборот производного произведения с
                  нарушением авторских прав автора основного произведения либо
                  имущественных прав наследников или обладателей имущественных
                  прав автора влечет предусмотренную законодательными актами
                  Республики Казахстан ответственность.
                </p>
                <p>
                  20. Под правомерным обнародованием произведения (введением в
                  гражданско-правовой оборот) понимается осуществление с
                  согласия автора произведения действий, которые впервые делают
                  произведение доступным неопределенному кругу лиц посредством
                  его опубликования, публичного показа, публичного исполнения,
                  доведения до всеобщего сведения, совершения сделок
                  купли-продажи экземпляров произведения и иными способами.
                </p>
                <p>
                  При обнародовании произведения анонимно или под псевдонимом
                  издатель произведения считается представителем автора и
                  осуществляет защиту законных прав и интересов автора. При
                  подаче издателем иска в защиту прав и законных интересов
                  автора к заявлению приобщается экземпляр произведения, на
                  котором указано наименование издателя.
                </p>
                <p>
                  Подлинное имя автора произведения указывается в авторском
                  договоре, которым устанавливаются отношения между автором и
                  издателем. Указанный договор в судебном заседании не
                  исследуется. Если до окончания судебного заседания автор не
                  раскроет свою личность, то, при обоснованности заявленного
                  требования, суд принимает решение об удовлетворении иска в
                  пользу издателя.
                </p>
                <p>
                  22. Без согласия производителя фонограммы и исполнителя,
                  исполнение которого записано на фонограмме, допускается
                  публичное исполнение произведения с использованием технических
                  средств в барах, кафе, ресторанах и других публичных местах
                  обслуживания граждан, но с выплатой авторского вознаграждения,
                  если такое исполнение производится в присутствии лиц, не
                  относящихся к обычному кругу семьи. При решении вопроса о том,
                  относятся ли присутствующие при таком исполнении произведения
                  лица, к обычному кругу семьи, следует принимать во внимание не
                  только наличие родственных отношений, но и личные связи,
                  период общения и характер взаимоотношений, а также иные
                  обстоятельства (свадьба, юбилейное или иное торжественное
                  мероприятие и другое).
                </p>
                <p>
                  За указанный вид публичного исполнения авторское
                  вознаграждение выплачивается в размере, предусмотренном
                  постановлением Правительства Республики Казахстан от 20
                  октября 2004 года № 1083 «Об утверждении минимальных ставок
                  авторского вознаграждения за некоторые виды использования
                  произведений». В этом случае исполнитель или производитель
                  фонограмм не обладает правом на взыскание компенсации,
                  предусмотренной статьей 49 Закона.
                </p>
                <p>
                  24. По делам о защите авторских и смежных прав истцами
                  являются автор (соавтор) произведения, исполнитель,
                  производитель фонограмм, организация эфирного и кабельного
                  вещания, наследники имущественных прав автора и исполнителя,
                  издатели анонимных произведений, лица, которым конкретные
                  имущественные права уступлены на основании авторского или
                  лицензионного договора.
                </p>
                <p>
                  Иные лица могут предъявлять иск в интересах автора или
                  обладателя смежных прав только при наличии доверенности.
                </p>
                <p>
                  25. Надлежащим ответчиком по делу о защите авторских или
                  смежных прав является лицо, которое своими действиями нарушило
                  личные неимущественные или имущественные права автора (его
                  наследников) или обладателя смежных прав либо права
                  обладателей имущественных прав, уступленных им на основании
                  авторского или лицензионного договора.
                </p>
                <p>
                  Типография, осуществляющая печатание тиража экземпляров
                  произведения в соответствии с договором и издателем, выполняет
                  технические функции и надлежащим ответчиком не является. Если
                  же типография по своей инициативе увеличивает тираж издания,
                  то она считается изготовителем контрафактных экземпляров
                  произведения и несет предусмотренную законодательными актами
                  Республики Казахстан ответственность.
                </p>
                <p>
                  В качестве ответчиков могут выступать несколько лиц, которые,
                  в зависимости от нарушения имущественных прав автора или
                  исполнителя, несут солидарную либо долевую ответственность.
                </p>
                <p>
                  При рассмотрении дел о защите нарушенных авторских или смежных
                  прав истец в соответствии со статьей 72 ГПК обязан доказать
                  факт неправомерного использования ответчиком принадлежащих ему
                  прав, а ответчик обязан доказать, что совершенные им в
                  отношении объекта авторского права или смежных прав действия
                  соответствуют требованиям Закона и не нарушают прав автора,
                  исполнителя, производителя фонограмм или организации эфирного
                  и кабельного вещания. В противном случае ответчик должен
                  признаваться нарушителем авторского или смежных прав с
                  привлечением к гражданско-правовой ответственности.
                </p>
                <p>
                  30. В случае нарушения личных неимущественных прав автора или
                  исполнителя произведении, автор или исполнитель вправе
                  требовать возмещения морального вреда.
                </p>
                <p>
                  Поскольку личные неимущественные права автора или исполнителя
                  произведения не переходят к наследникам ни по закону, ни по
                  завещанию, наследники не вправе заявлять иск о возмещении
                  морального вреда в свою пользу, а нарушенные личные
                  неимущественные права таких авторов и исполнителей подлежат
                  защите иными предусмотренными законом способами.
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
      <!-- ПРАВА И ОБЯЗАННОСТИ ЖУРНАЛИСТОВ -->
      <div id="journalism" class="accordion-item">
        <p class="parent" @click="changeToActive">
          ПРАВА И ОБЯЗАННОСТИ ЖУРНАЛИСТОВ
        </p>
        <div class="accordion-item child nested hidden">
          <p class="parent" @click="changeToActive">СТАТУС ЖУРНАЛИСТА</p>
          <div class="accordion-item child hidden">
            <div class="child text">
              <p>
                По закону журналист (представитель средства массовой информации)
                - физическое лицо, осуществляющее деятельность по сбору,
                обработке и подготовке сообщений и материалов для СМИ на основе
                трудовых или иных договорных отношений.
              </p>
            </div>
          </div>
        </div>

        <div class="accordion-item child nested hidden">
          <p class="parent" @click="changeToActive">ПРАВА ЖУРНАЛИСТА</p>
          <div class="accordion-item child hidden">
            <div class="child text">
              <ul>
                <li>
                  <b>
                    Осуществлять поиск, запрашивать, получать и распространять
                    информацию;
                  </b>
                </li>
              </ul>
              <p>
                Статья 20 Конституции РК гарантирует право на получение и
                распространение информации, любым, не запрещенным законом
                способом. В этой же статье говорится о том, что перечень
                сведений, составляющих государственные секреты, определяются
                законом.
              </p>
              <p>
                Статья 2 Закона о СМИ также гласит, что свобода слова,
                творчества, выражения в печатной и иной форме своих взглядов и
                убеждений, получения и распространения информации любым не
                запрещенным законом способом гарантируются Конституцией РК.
                Цензура запрещается.
              </p>
              <p></p>
              <p>
                Государственные органы, общественные объединения, должностные
                лица и средства массовой информации обязаны обеспечить каждому
                гражданину возможность ознакомиться с затрагивающими его права и
                интересы документами, решениями и источниками информации.
              </p>
              <p>
                Процедура получения доступа к информации содержится в Законе о
                СМИ (подробнее
                <span class="highlight">«Доступ к информации»</span>).
              </p>
              <ul>
                <li>
                  <b>
                    Посещать государственные органы, организации всех форм
                    собственности и быть принятым их должностными лицами в связи
                    с осуществлением своих служебных обязанностей,
                    присутствовать на всех мероприятиях, проводимых
                    аккредитовавшим его органом, за исключением случаев, когда
                    принято решение о проведении закрытого мероприятия.
                  </b>
                </li>
                <li>
                  <b>
                    Производить записи, в том числе с использованием средств
                    аудиовизуальной техники, кино- и фотосъемку, за исключением
                    случаев, запрещенных законодательными актами РК;
                  </b>
                </li>
              </ul>
              <p>
                Следует помнить, что СМИ не требуется согласие изображаемого
                лица в следующих случаях:
              </p>
              <ol>
                <li>
                  если данное лицо присутствует или участвует в зрелищных
                  культурно-массовых, социально значимых в области культуры,
                  спортивно-массовых мероприятиях, собраниях, митингах, шествиях
                  или демонстрациях и иных публичных мероприятиях;
                </li>
                <li>
                  если распространяемая информация содержит изображение лица и
                  сведения, связанные со служебной и (или) публичной
                  деятельностью данного лица, а также опубликована самим лицом,
                  его законным представителем или уполномоченным лицом в
                  источниках, доступ к которым не ограничен;
                </li>
                <li>
                  если использование изображаемого лица осуществляется в целях
                  защиты конституционного строя, охраны общественного порядка,
                  прав и свобод человека, здоровья и нравственности населения.
                  (подробнее <span class="highlight">«Фото, видеосъемка»</span>.)
                </li>
              </ol>
              <p>
                В некоторых случаях съемка запрещена (освещение судебных
                процессов, проведение съемки режимных объектов внутри здания и
                т.д.) (подробнее
                <span class="highlight">«Освещение судебных процессов»</span>,
                <span class="highlight"> «Видео, фотосъемка» </span>).
              </p>
              <ul>
                <li>
                  <b>
                    Присутствовать по предъявлению удостоверения журналиста в
                    районе стихийных бедствий, на митингах и демонстрациях, а
                    также при иных формах выражения общественных, групповых и
                    личных интересов и протеста;
                  </b>
                </li>
              </ul>
              <p>
                Журналист имеет право присутствовать по предъявлению
                удостоверения журналиста в районе стихийных бедствий, на
                митингах и демонстрациях, а также при иных формах выражения
                общественных, групповых и личных интересов и протеста. При себе
                необходимо иметь удостоверение журналиста и документ
                удостоверяющий личность.
              </p>
              <p>
                НЕ ТРЕБУЕТСЯ аккредитация или предварительное согласование с
                органами власти и местного самоуправления, полицией или
                организаторами для посещения митинга и его освещения в прессе.
              </p>
              <p><b>Если у вас требуют прекратить съемку?</b></p>
              <p>
                Помните, что журналист имеет право снимать и фотографировать
                лиц, участвующих в митинге, сотрудников органов полиции,
                осуществляющих охрану общественного порядка. (ст. 14 Закона о
                СМИ). По возможности при освещении митингов старайтесь
                идентифицировать себя как журналиста (это может быть жилет с
                надписью).
              </p>
              <p>
                Если вам мешают осуществлять свою профессиональную деятельность,
                знайте, что воспрепятствование журналистам в осуществлении
                профессиональной деятельности является грубым нарушением и
                влечет за собой уголовную ответственность. Для этого вам
                необходимо зафиксировать нарушение и написать заявление в
                правоохранительные органы (подробнее
                <span class="highlight">«Образцы документов»</span>).
              </p>
              <ul>
                <li>
                  <b>
                    получать доступ к документам и материалам, за исключением их
                    фрагментов, содержащих сведения, составляющие
                    государственные секреты;
                  </b>
                </li>
              </ul>
              <p>
                Государственные секреты – это защищаемые государством сведения,
                составляющие государственную и служебную тайны, распространение
                которых ограничивается государством с целью осуществления
                эффективной военной, экономической, научно-технической,
                внешнеэкономической, внешнеполитической, разведывательной,
                контрразведывательной, оперативно-розыскной и иной деятельности,
                не вступающей в противоречие с общепринятыми нормами
                международного права.
              </p>
              <p>
                Статья 17 закона “О государственных секретах” содержит сведения,
                не подлежащие засекречиванию. К числу таких сведений относятся:
              </p>
              <ul>
                <li>
                  о чрезвычайных ситуациях и катастрофах, угрожающих
                  безопасности и здоровью граждан, и их последствиях, а также о
                  стихийных бедствиях, их официальных прогнозах и последствиях;
                </li>
                <li>
                  о состоянии экологии, здравоохранения, санитарии, демографии,
                  образования, культуры, сельского хозяйства, а также о
                  состоянии преступности;
                </li>
                <li>
                  о привилегиях, компенсациях и льготах, предоставляемых
                  государством гражданам, должностным лицам и организациям;
                </li>
                <li>о фактах нарушения прав и свобод гражданина;</li>
                <li>
                  о размерах золотовалютных активов Национального банка
                  Республики Казахстан и правительственного (бюджетного) резерва
                  драгоценных металлов и драгоценных камней;
                </li>
                <li>
                  о фактах нарушения законности государственными органами и
                  организациями, их должностными лицами;
                </li>
                <li>
                  о массовых репрессиях по политическим, социальным и другим
                  мотивам, в том числе находящихся в архивах, за исключением
                  сведений, предусмотренных статьей 14 настоящего Закона.
                </li>
              </ul>
              <p>
                Чтобы выяснить, какая информация отнесена к государственным
                секретам, необходимо отправить письменный запрос в
                соответствующий орган (подробнее
                <span class="highlight">«Тайны и секреты»</span>).
              </p>
              <p>
                <b>
                  ВАЖНО! Предусмотрена уголовная ответственность за сбор
                  сведений, составляющих государственные секреты, путем
                  похищения документов, подкупа или угроз в отношении лиц,
                  владеющих государственными секретами, или их близких,
                  перехвата в средствах связи, незаконного проникновения в
                  компьютерную систему или сеть, использования специальных
                  технических средств, а равно иным незаконным способом.
                </b>
              </p>
              <ul>
                <li><b>Проверять достоверность получаемой информации; </b></li>
              </ul>
              <p>
                Проверять достоверность получаемой информации необходимо,
                поскольку ответственность за распространение недостоверной
                информации несет журналист.
              </p>
              <ul>
                <li>
                  <b
                    >Обращаться к специалистам при проверке полученных
                    информационных материалов;
                  </b>
                </li>
              </ul>
              <p>
                Обращение к специалистам за разъяснением либо комментарием
                поможет в дальнейшем избежать проблем, связанных как с
                предъявлением искового заявления о защите чести, достоинства и
                деловой репутации физического или юридического лица, так и с
                заявлением по статье клевета. (подробнее
                <span class="highlight">«Диффамация»</span>).
              </p>
              <ul>
                <li>
                  <b
                    >Распространять подготовленные им сообщения и материалы за
                    своей подписью, под условным именем (псевдонимом);
                  </b>
                </li>
              </ul>
              <p>
                Гражданский кодекс предусматривает, что гражданин приобретает и
                осуществляет свои права и обязанности под своим именем, включая
                фамилию и имя. Эта же статья говорит и о том, что перемена имени
                не является основанием для прекращения или изменения его прав и
                обязанностей, приобретенных под прежним именем, анонимно или
                псевдонимом.
              </p>
              <p>
                Таким образом, право журналиста - ставить под подготовленным им
                материалом свою подпись или использовать псевдоним. Но если
                возникнет судебный конфликт, использование псевдонима ни в коим
                случае не освобождает журналиста от ответственности в случаях,
                где сведения признают клеветническими.
              </p>
              <ul>
                <li>
                  <b
                    >Отказываться от публикации материала за своей подписью,
                    если его содержание после редакционной правки противоречит
                    личным убеждениям журналиста;</b
                  >
                </li>
              </ul>
              <p>
                Право отказаться от публикации позволяет журналисту выразить
                свое мнение к подготовленному им материалу после редактирования.
                В случае, если журналист не согласен с редакторскими правками,
                он вправе отказаться от своей подписи под материалом.
              </p>
              <ul>
                <li>
                  <b
                    >На сохранение тайны авторства и источников информации, за
                    исключением случаев, когда эти тайны обнародуются по
                    требованию суда.
                  </b>
                </li>
              </ul>
              <p>
                Защита источников информации журналистов - журналистской тайны -
                является важной гарантией свободы слова, предусмотренного ст. 20
                Конституции РК. Но помните, что вы обязаны раскрыть источник
                информации, если того требует суд.
              </p>
            </div>
          </div>
        </div>
        <div class="accordion-item child nested hidden">
          <p class="parent" @click="changeToActive">ОБЯЗАННОСТИ ЖУРНАЛИСТА</p>
          <div class="accordion-item child hidden">
            <div class="child text">
              <ul>
                <li>
                  <b
                    >осуществлять программу деятельности СМИ, с которым он
                    состоит в договорных отношениях, руководствуясь
                    законодательством РК;</b
                  >
                </li>
              </ul>
              <p>
                Журналисту для регулирования своей деятельности в редакции
                необходимо иметь соответствующий договор со СМИ, в котором он
                работает.
              </p>
              <ul>
                <li>
                  <b
                    >принимать меры по проверке достоверности распространяемой
                    информации путем направления запросов соответствующим
                    физическим и (или) юридическим лицам либо иными способами,
                    не запрещенными законодательством РК;</b
                  >
                </li>
              </ul>
              <p>
                Журналист необходимо проверять достоверность распространяемой
                информации, поскольку к ответственности за распространение
                недостоверной информации могут привлечь в том числе и
                журналиста. Для проверки вы можете оправить запрос либо
                обратиться за комментарием, разъяснением и т.д к источнику
                информации. (подробнее <span class="highlight">«Доступ к информации»</span>)
              </p>
              <ul>
                <li>
                  <b
                    >не распространять информацию, не соответствующую
                    действительности;</b
                  >
                </li>
              </ul>
              <p>
                Распространение журналистом информации, не соответствующей
                действительности, может послужить причиной конфликтных ситуаций
                и судебных разбирательств. В соответствии со статьей 143 ГК РК,
                гражданин или юридическое лицо вправе требовать по суду
                опровержения сведений, порочащих его честь, достоинство или
                деловую репутацию, если распространивший такие сведения не
                докажет, что они соответствуют действительности. Поэтому
                журналисту необходимо при публикации своих материалов иметь
                доказательства, соответствующие документы, записи, фотографии и
                т.д.
              </p>
              <p>
                Кроме того, заявитель может обратиться в суд с частной жалобой
                по статье 130 УК РК (распространение заведомо ложных сведений,
                порочащих честь и достоинство лица или подрывающих его
                репутацию), а также по статье 131 УК РК (оскорбление, то есть
                унижение чести и достоинства другого лица, выраженное в
                неприличной форме) (подробнее <span class="highlight">«Диффамация»</span>)
              </p>
              <p><b>ВАЖНО! </b></p>

              <ol>
                <li>Всегда используйте несколько источников информации</li>
                <li>
                  Собирайте и храните доказательства по спорным материалам,
                  поскольку по делам о защите чести и достоинства нет сроков
                  исковой давности.
                </li>
                <li>
                  Перед публикацией спорного материала по возможности дайте его
                  на проверку юристу
                </li>
                <li>
                  Если вам поступила досудебная претензия или судебный иск,
                  немедленно обратитесь за помощью к юристу или адвокату
                </li>
              </ol>

              <ul>
                <li>
                  <b
                    >удовлетворять просьбы лиц, предоставивших информацию, об
                    указании их авторства;</b
                  >
                </li>
              </ul>
              <p>
                Если к вам обращаются с просьбой указать авторство, то вы
                обязаны ее выполнить. В случае, если материал станет предметом
                судебного разбирательства, это позволит переложить
                ответственность на лицо, от которого вы получили информацию.
              </p>
              <ul>
                <li>
                  <b
                    >получать согласие от физического и (или) юридического лица
                    либо их представителей на распространение в СМИ личной,
                    семейной, врачебной, банковской, коммерческой и иных
                    охраняемых законом тайн, за исключением случаев, если
                    сведения указаны в официальных сообщениях и (или)
                    распространены самим физическим и (или) юридическим лицом
                    либо их представителями в источниках, доступ к которым не
                    ограничен;</b
                  >
                </li>
              </ul>
              <p>
                В законе не сказано, в какой форме нужно получать согласие –
                письменно, устно, в форме электронного документа. Самой
                безопасной является письменная форма согласия. Согласие не
                требуется, если сведения содержатся в официальных сообщениях и
                (или) распространены самим физическим и (или) юридическим лицом
                либо их представителями в источниках, доступ к которым не
                ограничен. (подробнее <span class="highlight">«Тайны и секреты»</span>).
              </p>
              <ul>
                <li>
                  <b
                    >уважать законные права и интересы физических и юридических
                    лиц;</b
                  >
                </li>
              </ul>
              <p>
                К законным правам и интересам физических лиц относятся
                неприкосновенность частной жизни в том числе тайны переписки,
                телефонных переговоров, дневников, заметок, записок, интимной
                жизни, усыновления, рождения, врачебной, адвокатской тайны,
                тайны банковских вкладов, тайны усыновления (удочерения) вопреки
                воле усыновителя, коммерческая и других охраняемых законом тайн,
                право на собственное изображение, права детей. К юридическим
                лицам –коммерческая тайна, защита деловой репутации и т.д.
              </p>
              <ul>
                <li>
                  <b
                    >выполнять иные обязанности, возложенные на него в
                    соответствии с законодательством РК.</b
                  >
                </li>
              </ul>
              <p>
                К примеру, освещение прав несовершеннолетних, использование
                процедуры досудебного урегулирования и т.д.
              </p>
            </div>
          </div>
        </div>
        <div class="accordion-item child nested hidden">
          <p class="parent" @click="changeToActive">
            АККРЕДИТАЦИЯ
          </p>
          <div class="accordion-item child hidden">
            <div class="child text">
              <p>
                Это процедура назначения журналиста и признания его полномочий
                государственным органом, общественным объединением и
                организацией. Ее цель - широкое и оперативное информирование
                общественности о деятельности перечисленных организаций.&nbsp;
              </p>
              <p>
                В обязанности государственных органов, общественных объединений
                и организаций входит предварительное извещение СМИ о своих
                заседаниях, совещаниях и иных мероприятиях, обеспечение
                стенограммами, протоколами и иными документами.
              </p>
              <p>Аккредитация выдается сроком на один год.</p>
              <p>
                Для получения аккредитации необходимо подать заявление, к
                которому прилагаются:&nbsp;
              </p>
              <ul>
                <li>
                  <p>копия удостоверения личности аккредитуемого журналиста;</p>
                </li>
                <li>
                  <p>
                    копия свидетельства о постановке на учет периодического
                    печатного издания или информационного агентства либо теле-,
                    радиоканала, выданного уполномоченным органом.
                  </p>
                </li>
              </ul>
              <p>
                В случае, если заявление подается от представителя СМИ, к
                заявлению, кроме документов, прилагается доверенность
                собственника издания на проведение процедуры аккредитации
                журналиста.
              </p>
              <p>
                В случае, если заявление подается от собственника
                интернет-ресурса, к заявлению, кроме документов, прилагается
                документ, подтверждающий право собственности на интернет-ресурс.
              </p>
              <p>
                Аккредитованный журналист имеет право присутствовать на
                заседаниях, совещаниях и других мероприятиях, проводимых
                аккредитовавшими его государственными органами, общественными
                объединениями и организациями, за исключением случаев, когда
                приняты решения о проведении закрытого мероприятия.
              </p>
              <p>
                <strong>Журналист может быть лишен аккредитации за:</strong>
              </p>
              <ul>
                <li>
                  <p>Нарушение правил аккредитации,&nbsp;</p>
                </li>
                <li>
                  <p>
                    Распространение не соответствующих действительности
                    сведений, порочащих честь и достоинство аккредитовавших его
                    государственных органов, общественных объединений и
                    организаций.
                  </p>
                </li>
              </ul>
              <p>
                <strong
                  >ВАЖНО! Отсутствие аккредитации у журналистов не может
                  являться основанием для отказа присутствовать на открытых
                  мероприятиях.&nbsp;</strong
                >
              </p>
            </div>
          </div>
        </div>
        <div class="accordion-item child nested hidden">
          <p class="parent" @click="changeToActive">
            ВОСПРЕПЯТСТВОВАНИЕ ЗАКОННОЙ ДЕЯТЕЛЬНОСТИ ЖУРНАЛИСТОВ
          </p>
          <div class="accordion-item child hidden">
            <div class="child text">
              <p>
                Воспрепятствование законной профессиональной деятельности
                журналиста - это  «принуждение к распространению либо
                отказу от распространения информации, а равно путем создания
                условий, препятствующих выполнению журналистом профессиональной
                деятельности либо полностью лишающих его этой
                возможности». Также к воспрепятствованию относится
                совершенное лицом противоправное действие с использованием
                своего служебного положения, а равно с применением насилия или
                угрозы, либо с повреждением или уничтожением их имущества.
              </p>
              <p>
                Воспрепятствование законной профессиональной деятельности
                журналиста влечет уголовную ответственность по статье 158 УК РК.
                Для возбуждения дела вам необходимо зафиксировать нарушение и
                написать заявление в правоохранительные органы&nbsp;(подробнее
                <span class="highlight">«Образцы документов»</span>).
              </p>
            </div>
          </div>
        </div>
        <div class="accordion-item child nested hidden">
          <p class="parent" @click="changeToActive">
            ЗАКОНОДАТЕЛЬНАЯ БАЗА
          </p>
          <div class="accordion-item child nested hidden">
            <p class="parent" @click="changeToActive">УГОЛОВНЫЙ КОДЕКС РК</p>
            <div class="accordion-item child hidden">
              <div class="child text">
                <p>
                  <strong
                    >Статья 158. Воспрепятствование законной профессиональной
                    деятельности журналиста</strong
                  >
                </p>
                <p>
                  1. Воспрепятствование законной профессиональной деятельности
                  журналиста путем принуждения его к распространению либо отказу
                  от распространения информации, а равно путем создания условий,
                  препятствующих выполнению журналистом законной
                  профессиональной деятельности либо полностью лишающих его этой
                  возможности, -
                </p>
                <p>
                  наказывается штрафом в размере до ста месячных расчетных
                  показателей либо исправительными работами в том же размере,
                  либо привлечением к общественным работам на срок до ста
                  двадцати часов, либо арестом на срок до сорока пяти суток.
                </p>
                <p>
                  2. То же деяние, совершенное лицом с использованием своего
                  служебного положения, а равно с применением насилия или угрозы
                  его применения в отношении журналиста или его близких либо с
                  повреждением или уничтожением их имущества, -
                </p>
                <p>
                  наказывается штрафом в размере до двух тысяч месячных
                  расчетных показателей либо исправительными работами в том же
                  размере, либо ограничением свободы на срок до двух лет, либо
                  лишением свободы на тот же срок, с лишением права занимать
                  определенные должности или заниматься определенной
                  деятельностью на срок до трех лет или без такового.
                </p>
              </div>
            </div>
          </div>
          <div class="accordion-item child nested hidden">
            <p class="parent" @click="changeToActive">
              ЗАКОН О ГОСУДАРСТВЕННЫХ СЕКРЕТАХ
            </p>
            <div class="accordion-item child hidden">
              <div class="child text">
                <p>
                  <strong
                    >Статья 17. Сведения, не подлежащие засекречиванию</strong
                  >
                </p>
                <p>1. Не подлежат засекречиванию сведения о:</p>
                <p>
                  1) чрезвычайных ситуациях и катастрофах, угрожающих
                  безопасности и здоровью граждан, и их последствиях, а также о
                  стихийных бедствиях, их официальных прогнозах и последствиях;
                </p>
                <p>
                  2) состоянии здравоохранения, санитарии, демографии, миграции,
                  образования, культуры, социальной защиты, экономики, сельского
                  хозяйства, а также о состоянии преступности;
                </p>
                <p>3) фактах совершения актов терроризма;</p>
                <p>
                  4) состоянии экологии, пожарной безопасности, а также о
                  санитарно-эпидемиологической и радиационной обстановке,
                  безопасности пищевых продуктов;
                </p>
                <p>
                  5) привилегиях, компенсациях и льготах, предоставляемых
                  государством гражданам и организациям;
                </p>
                <p>6) фактах нарушения прав и свобод человека и гражданина;</p>
                <p>
                  7) размерах золотовалютного резерва Национального Банка
                  Республики Казахстан;
                </p>
                <p>
                  8) формировании и расходовании средств из республиканского и
                  местного бюджетов, за исключением сведений, раскрывающих
                  обеспечение безопасности Республики Казахстан;
                </p>
                <p>
                  9) контроле за расходованием средств из республиканского и
                  местного бюджетов, за исключением сведений, раскрывающих
                  обеспечение безопасности Республики Казахстан;
                </p>
                <p>
                  10) фактах нарушения законности государственными органами и
                  организациями, их должностными лицами;
                </p>
                <p>
                  11) массовых репрессиях по политическим, социальным и другим
                  мотивам, в том числе находящиеся в архивах, за исключением
                  сведений, предусмотренных статьей 14 настоящего Закона.
                </p>
                <p>
                  2. Должностные лица, принявшие решение о засекречивании
                  перечисленных сведений либо о включении их в этих целях в
                  носители сведений, составляющих государственные секреты, несут
                  ответственность в соответствии с законами Республики
                  Казахстан. Граждане вправе обжаловать такие решения в судебном
                  порядке.
                </p>
              </div>
            </div>
          </div>
          <div class="accordion-item child nested hidden">
            <p class="parent" @click="changeToActive">ЗАКОН РК О СМИ</p>
            <div class="accordion-item child hidden">
              <div class="child text">
                <p>
                  <strong
                    >Статья 1. Основные понятия, используемые в настоящем
                    Законе</strong
                  >
                </p>
                <p>
                  журналист (представитель средства массовой информации) -
                  физическое лицо, осуществляющее деятельность по сбору,
                  обработке и подготовке сообщений и материалов для средства
                  массовой информации на основе трудовых или иных договорных
                  отношений;
                </p>
                <p><strong>Статья 20. Права журналиста</strong></p>
                <p>Журналист имеет право:</p>
                <p>
                  1) осуществлять поиск, запрашивать, получать и распространять
                  информацию;
                </p>
                <p>
                  2) посещать государственные органы, организации всех форм
                  собственности и быть принятым их должностными лицами в связи с
                  осуществлением своих служебных обязанностей, присутствовать на
                  всех мероприятиях, проводимых аккредитовавшим его органом, за
                  исключением случаев, когда принято решение о проведении
                  закрытого мероприятия;
                </p>
                <p>
                  3) производить записи, в том числе с использованием средств
                  аудиовизуальной техники, кино- и фотосъемку, за исключением
                  случаев, запрещенных законодательными актами Республики
                  Казахстан;
                </p>
                <p>
                  4) присутствовать по предъявлению удостоверения журналиста в
                  районе стихийных бедствий, на митингах и демонстрациях, а
                  также при иных формах выражения общественных, групповых и
                  личных интересов и протеста;
                </p>
                <p>
                  5) получать доступ к документам и материалам, за исключением
                  их фрагментов, содержащих сведения, составляющие
                  государственные секреты;
                </p>
                <p>6) проверять достоверность получаемой информации;</p>
                <p>
                  7) обращаться к специалистам при проверке полученных
                  информационных материалов;
                </p>
                <p>
                  8) распространять подготовленные им сообщения и материалы за
                  своей подписью, под условным именем (псевдонимом);
                </p>
                <p>
                  9) отказываться от публикации материала за своей подписью,
                  если его содержание после редакционной правки противоречит
                  личным убеждениям журналиста;
                </p>
                <p>
                  10) на сохранение тайны авторства и источников информации, за
                  исключением случаев, когда эти тайны обнародуются по
                  требованию суда.
                </p>
                <p><strong>Статья 21. Обязанности журналиста</strong></p>
                <p>Журналист обязан:</p>
                <p>
                  1) осуществлять программу деятельности средства массовой
                  информации, с которым он состоит в договорных отношениях,
                  руководствуясь законодательством Республики Казахстан;
                </p>
                <p>
                  1-2) принимать меры по проверке достоверности распространяемой
                  информации путем направления запросов соответствующим
                  физическим и (или) юридическим лицам либо иными способами, не
                  запрещенными законодательством Республики Казахстан;
                </p>
                <p>
                  2) не распространять информацию, не соответствующую
                  действительности;
                </p>
                <p>
                  3) удовлетворять просьбы лиц, предоставивших информацию, об
                  указании их авторства;
                </p>
                <p>
                  3-1) получать согласие от физического и (или) юридического
                  лица либо их представителей на распространение в средстве
                  массовой информации личной, семейной, врачебной, банковской,
                  коммерческой и иных охраняемых законом тайн, за исключением
                  случаев, если сведения указаны в официальных сообщениях и
                  (или) распространены самим физическим и (или) юридическим
                  лицом либо их представителями в источниках, доступ к которым
                  не ограничен;
                </p>
                <p>
                  4) уважать законные права и интересы физических и юридических
                  лиц;
                </p>
                <p>
                  5) выполнять иные обязанности, возложенные на него в
                  соответствии с законодательством Республики Казахстан.
                </p>
                <p><strong>Статья 22. Аккредитация журналистов</strong></p>
                <p>
                  1. Средства массовой информации по согласованию с обладателями
                  информации, общественными объединениями и организациями могут
                  аккредитовать при них своих журналистов.
                </p>
                <p>
                  2. Государственные органы, общественные объединения и
                  организации, при которых аккредитован журналист, обязаны
                  предварительно извещать его о заседаниях, совещаниях и иных
                  мероприятиях, обеспечивать стенограммами, протоколами и иными
                  документами.
                </p>
                <p>
                  3. Аккредитованный журналист имеет право присутствовать на
                  заседаниях, совещаниях и других мероприятиях, проводимых
                  аккредитовавшими его государственными органами, общественными
                  объединениями и организациями, за исключением случаев, когда
                  приняты решения о проведении закрытого мероприятия.
                </p>
                <p>
                  4. Журналист может быть лишен аккредитации, если им нарушены
                  правила аккредитации либо за распространение не
                  соответствующих действительности сведений, порочащих честь и
                  достоинство аккредитовавших его государственных органов,
                  общественных объединений и организаций.
                </p>
                <p>
                  <strong
                    >Статья 25. Ответственность за нарушение законодательства
                    Республики Казахстан о средствах массовой информации</strong
                  >
                </p>
                <p>
                  Воспрепятствование законной профессиональной деятельности
                  журналиста влечет установленную законами Республики Казахстан
                  ответственность.
                </p>
              </div>
            </div>
          </div>
        </div>
