          </p>
          <div class="accordion-item child nested hidden">
            <p class="parent" @click="changeToActive">ЗАКОН РК О СВЯЗИ</p>
            <div class="accordion-item child hidden">
              <div class="child text">
                <p>
                  <strong
                    >Статья 41-1. Порядок приостановления работы сетей и (или)
                    средств связи</strong
                  >
                </p>
                <p>
                  1. В случаях использования сетей и (или) средств связи в
                  преступных целях, наносящих ущерб интересам личности, общества
                  и государства, а также для распространения информации,
                  нарушающей законодательство Республики Казахстан о выборах,
                  содержащей призывы к осуществлению экстремистской и
                  террористической деятельности, массовым беспорядкам, а равно к
                  участию в массовых (публичных) мероприятиях, проводимых с
                  нарушением установленного порядка, пропагандирующих
                  сексуальную эксплуатацию несовершеннолетних и детскую
                  порнографию, Генеральный Прокурор Республики Казахстан или его
                  заместители вносят в уполномоченный орган предписание об
                  устранении нарушений закона с требованием о принятии мер по
                  временному приостановлению работы сетей и (или) средств связи,
                  оказания услуг связи, доступа к интернет-ресурсам и (или)
                  размещенной на них информации, за исключением случаев,
                  предусмотренных пунктом 1-2 настоящей статьи.
                </p>
                <p>
                  1-1. В случае распространения по сети телекоммуникаций
                  информации, запрещенной или иным образом ограниченной к
                  распространению вступившими в законную силу судебными актами
                  или законами Республики Казахстан, а также доступ к которой
                  был временно приостановлен внесенным в уполномоченный орган
                  представлением Генерального прокурора Республики Казахстан или
                  его заместителей об устранении нарушений законности, операторы
                  связи и (или) государственная техническая служба по
                  предписанию уполномоченного органа обязаны принять
                  незамедлительные меры по ограничению доступа к запрещенной
                  информации.
                </p>
                <p>
                  1-2. В случаях, не терпящих отлагательств и могущих привести к
                  совершению тяжких и особо тяжких преступлений, а также
                  преступлений, подготавливаемых и совершаемых преступной
                  группой, Председатель Комитета национальной безопасности
                  Республики Казахстан, его заместители или начальники
                  территориальных органов Комитета национальной безопасности
                  Республики Казахстан либо лица, их замещающие, вправе
                  приостанавливать работу сетей и (или) средств связи, оказание
                  услуг связи, доступ к интернет-ресурсам и (или) размещенной на
                  них информации в интересах всех субъектов оперативно-розыскной
                  деятельности с последующим уведомлением уполномоченного органа
                  в области связи и Генеральной прокуратуры Республики Казахстан
                  в течение двадцати четырех часов.
                </p>
                <p>
                  Порядок приостановления работы сетей и (или) средств связи,
                  оказания услуг связи, доступа к интернет-ресурсам и (или)
                  размещенной на них информации определяется совместным
                  нормативным правовым актом субъектов оперативно-розыскной
                  деятельности и уполномоченного органа в области связи по
                  согласованию с Генеральной прокуратурой Республики Казахстан.
                </p>
                <p>
                  1-3. Запрещаются работа сетей и (или) средств связи, оказание
                  услуг связи, доступ к интернет-ресурсам и (или) размещенной на
                  них информации в целях доступа к информации, запрещенной
                  вступившим в законную силу решением суда или законами
                  Республики Казахстан.
                </p>
                <p>
                  2. Уполномоченный орган в течение часа с момента получения
                  направляет требование о принятии мер по выполнению указанного
                  в пункте 1 настоящей статьи предписания об устранении
                  нарушений закона для исполнения операторам связи и (или) в
                  государственную техническую службу.
                </p>
                <p>
                  3. Операторы связи и (или) государственная техническая служба:
                </p>
                <p>
                  1) после получения от уполномоченного органа или органов
                  национальной безопасности Республики Казахстан требования о
                  принятии мер по выполнению указанного в пунктах 1, 1-1 и 1-2
                  настоящей статьи предписания или решения обязаны выполнить его
                  в течение не более трех часов путем временного приостановления
                  работы сетей и (или) средств связи, оказания услуг связи,
                  доступа к интернет-ресурсам и (или) размещенной на них
                  информации;
                </p>
                <p>
                  2) во взаимодействии с уполномоченным органом, и при
                  необходимости, совместно с правоохранительными органами
                  определяют лицо, использующее сети и (или) средства связи в
                  преступных целях, наносящих ущерб интересам личности, общества
                  и государства, а также для распространения информации,
                  нарушающей законодательство Республики Казахстан о выборах,
                  содержащей призывы к осуществлению экстремистской и
                  террористической деятельности, массовым беспорядкам, а равно к
                  участию в массовых (публичных) мероприятиях, проводимых с
                  нарушением установленного порядка, и направляют ему
                  уведомление с требованием принять меры по удалению такой
                  информации.
                </p>
                <p>
                  4. В случае если лицо, использующее сети и (или) средства
                  связи в преступных целях, наносящих ущерб интересам личности,
                  общества и государства, а также для распространения
                  информации, нарушающей законодательство Республики Казахстан о
                  выборах, содержащей призывы к осуществлению экстремистской и
                  террористической деятельности, массовым беспорядкам, а равно к
                  участию в массовых (публичных) мероприятиях, проводимых с
                  нарушением установленного порядка, удалило ее, то оно
                  направляет уведомление об этом в уполномоченный орган.
                </p>
                <p>
                  5. После получения уведомления, указанного в пункте 4
                  настоящей статьи, и проверки его достоверности уполномоченный
                  орган дает поручение операторам связи и (или) государственной
                  технической службе об отмене требования, указанного в пункте 2
                  настоящей статьи.
                </p>
                <p>
                  6. Операторы связи и (или) государственная техническая служба
                  после получения от уполномоченного органа поручения,
                  указанного в пункте 5 настоящей статьи, обязаны
                  незамедлительно его выполнить.
                </p>
                <p>
                  7. Уполномоченный орган, операторы связи и государственная
                  техническая служба несут установленную законом ответственность
                  за неисполнение определенного настоящей статьей порядка
                  приостановления работы сетей связи и (или) средств связи.
                </p>
              </div>
            </div>
          </div>
          <div class="accordion-item child nested hidden">
            <p class="parent" @click="changeToActive">ЗАКОН О СМИ</p>
            <div class="accordion-item child hidden">
              <div class="child text">
                <p>
                  4) средство массовой информации - периодическое печатное
                  издание, теле-, радиоканал, кинодокументалистика,
                  аудиовизуальная запись и иная форма периодического или
                  непрерывного публичного распространения массовой информации,
                  включая интернет-ресурсы;
                </p>
                <p>
                  Статья 1. Основные понятия, используемые в настоящем Законе
                </p>
                <p>
                  Статья 13. Приостановление и прекращение выпуска средства
                  массовой информации либо распространения продукции средства
                  массовой информации
                </p>
                <p>
                  1. Приостановление либо прекращение выпуска или
                  распространения продукции средства массовой информации,
                  поставленного на учет в уполномоченном органе, возможно по
                  решению собственника или суда.
                </p>
                <p>
                  2. Под приостановлением понимается временное прекращение
                  выпуска одного или нескольких номеров изданий, выхода в эфир
                  теле-, радиопрограмм, теле-, радиоканалов, временное
                  приостановление деятельности сетевого издания, а также
                  распространения продукции средства массовой информации до
                  устранения причин, послуживших основанием для приостановления.
                </p>
                <p>
                  Приостановление выпуска средства массовой информации либо
                  распространения продукции средства массовой информации
                  допускается на срок не более трех месяцев.
                </p>
                <p>
                  3. Основаниями для приостановления выпуска средства массовой
                  информации либо распространения продукции средства массовой
                  информации в установленном законами Республики Казахстан
                  порядке являются: пропаганда или агитация культа жестокости и
                  насилия, социального, расового, национального, религиозного,
                  сословного и родового превосходства, разглашение сведений,
                  составляющих государственные секреты или иную охраняемую
                  законом тайну, распространение информации, пропагандирующей
                  суицид, раскрывающей технические приемы и тактику
                  антитеррористических операций в период их проведения,
                  пропаганда наркотических средств, психотропных веществ, их
                  аналогов и прекурсоров, распространение теле-, радиопрограмм,
                  теле-, радиоканалов, а также демонстрация киновидеопродукции
                  порнографического и специального сексуально-эротического
                  характера, использование средства массовой информации в целях
                  нарушения условий проведения предвыборной агитации,
                  осуществления иностранцами, лицами без гражданства,
                  иностранными юридическими лицами и международными
                  организациями деятельности, препятствующей и (или)
                  способствующей выдвижению и избранию кандидатов, политических
                  партий, выдвинувших партийный список, достижению определенного
                  результата на выборах, проведения агитации в период ее
                  запрещения, принуждения к участию или отказу от участия в
                  забастовке, нарушения законодательства Республики Казахстан о
                  порядке организации и проведения мирных собраний, митингов,
                  шествий, пикетов и демонстраций, об авторском праве и смежных
                  правах в Интернете, а также нарушение требований,
                  предусмотренных пунктом 6 статьи 10 настоящего Закона.
                </p>
                <p>
                  4. Основаниями для прекращения выпуска средства массовой
                  информации либо распространения продукции средства массовой
                  информации являются: пропаганда или агитация насильственного
                  изменения конституционного строя, нарушения целостности
                  Республики Казахстан, подрыва безопасности государства, войны,
                  пропаганда экстремизма или терроризма, публикация материалов и
                  распространение информации, направленной на разжигание
                  межнациональной и межконфессиональной вражды, а также
                  неустранение причин приостановления выпуска средства массовой
                  информации либо распространения продукции средства массовой
                  информации в установленный срок.
                </p>
                <p>
                  5. В случае приостановления либо прекращения выпуска средства
                  массовой информации либо распространения продукции средства
                  массовой информации по решению собственника либо суда в
                  уполномоченный орган направляется уведомление.
                </p>
                <p>
                  Прекращение выпуска средства массовой информации либо
                  распространения продукции средства массовой информации влечет
                  аннулирование свидетельства о постановке на учет.
                </p>
                <p>
                  6. В случае приостановления либо прекращения по вступившему в
                  законную силу решению суда выпуска средства массовой
                  информации либо продукции средства массовой информации, когда
                  средством массовой информации является интернет-ресурс,
                  уполномоченные государственные органы, собственники
                  интернет-ресурсов обязаны приостановить или прекратить выпуск
                  средства массовой информации либо распространение на
                  территории Республики Казахстан продукции средства массовой
                  информации.
                </p>
                <p>
                  7. Вступление в законную силу решения суда о приостановлении
                  распространения продукции средства массовой информации либо
                  выпуска средства массовой информации, когда средством массовой
                  информации является интернет-ресурс, влечет запрет на
                  использование доменного имени с тем же или дублирующим
                  названием на срок не более трех месяцев.
                </p>
                <p>
                  Решение суда о прекращении распространения продукции средства
                  массовой информации либо выпуска средства массовой информации,
                  когда средством массовой информации является интернет-ресурс,
                  влечет отмену регистрации доменного имени и запрет на
                  использование в течение одного года доменного имени с тем же
                  или дублирующим названием, регистрация которого отменена
                  решением суда.
                </p>
              </div>
            </div>
          </div>
        </div>
