      <!-- ТАЙНЫ И СЕКРЕТЫ -->
      <div id="secrets" class="accordion-item">
        <p class="parent" @click="changeToActive">ТАЙНЫ И СЕКРЕТЫ</p>
        <div class="accordion-item child nested hidden">
          <p class="parent" @click="changeToActive">ОБЩЕДОСТУПНАЯ ИНФОРМАЦИЯ</p>
          <div class="accordion-item child hidden">
            <div class="child text"></div>
            <p>
              Информация делится на открытую (общедоступную) и информацию
              ограниченного доступа (секреты, тайны, информация для служебного
              пользования). Журналист должен знать, какая информация относится к
              конфиденциальной, как правильно ее использовать и распространять.
              В противном случае это может привести к претензиям, судебным
              спорам и даже к уголовному преследованию.
            </p>
            <p>
              К ней относятся общеизвестные сведения и иная информация, доступ к
              которой не ограничен. Общедоступную информацию может
              использоваться каждый по своему усмотрению (подробнее
             <span class="highlight"> «Доступ к информации»</span>).
            </p>
            <p>
              <strong
                >ПЕРЕЧЕНЬ ИНФОРМАЦИИ, ДОСТУП К КОТОРОЙ НЕ ПОДЛЕЖИТ
                ОГРАНИЧЕНИЮ&nbsp;</strong
              >
            </p>
            <p>Закон о Доступе к информации, ст. 6:</p>
            <ul>
              <li>
                о чрезвычайных ситуациях и катастрофах, угрожающих безопасности
                и здоровью граждан, и их последствиях, а также о стихийных
                бедствиях, их официальных прогнозах и последствиях;
              </li>
              <li>
                &nbsp;о состоянии здравоохранения, санитарии, демографии,
                миграции, образования, культуры, социальной защиты, экономики,
                сельского хозяйства, а также о состоянии преступности;
              </li>
              <li>&nbsp;о фактах совершения актов терроризма;</li>
              <li>
                о состоянии экологии, пожарной безопасности, а также о
                санитарно-эпидемиологической и радиационной обстановке,
                безопасности пищевых продуктов;
              </li>
              <li>
                о привилегиях, компенсациях и льготах, предоставляемых
                государством физическим и юридическим лицам;
              </li>
              <li>о фактах нарушения прав и свобод человека и гражданина;</li>
              <li>
                о размерах золотовалютного резерва Национального Банка
                Республики Казахстан;
              </li>
              <li>
                содержащей тексты нормативных правовых актов Республики
                Казахстан, за исключением нормативных правовых актов, содержащих
                государственные секреты и иные охраняемые законом тайны, а также
                их проекты;
              </li>
              <li>
                о формировании и расходовании средств из республиканского и
                местного бюджетов, за исключением сведений, содержащих
                государственные секреты;
              </li>
              <li>
                о контроле за расходованием средств из республиканского и
                местного бюджетов, за исключением сведений, содержащих
                государственные секреты;
              </li>
              <li>
                о фактах нарушения законности обладателями информации, их
                должностными лицами;
              </li>
              <li>
                о массовых репрессиях по политическим, социальным и другим
                мотивам, в том числе находящейся в архивах, за исключением
                информации, относимой к государственным секретам Республики
                Казахстан. &nbsp;
              </li>
            </ul>
          </div>
        </div>
        <div class="accordion-item child nested hidden">
          <p class="parent" @click="changeToActive">
            ИНФОРМАЦИЯ ОГРАНИЧЕННОГО ДОСТУПА
          </p>
          <div class="accordion-item child hidden">
            <div class="child text"></div>
            <p>
              Основания ограничений могут быть разными, например, безопасность и
              оборона страны, защита основ конституционного строя, здоровья,
              прав и законных интересов некоторых лиц.&nbsp;
            </p>
            <p>
              <strong
                >ВАЖНО! Журналист может нести ответственность за незаконный сбор
                информации и разглашение таких тайн, как государственные
                секреты, коммерческая, личная и семейная тайна и т. д., а
                ответственность за разглашение профессиональной тайны будет
                нести лицо, которому тайна была доверена по долгу службы и
                которое обязано было ее сохранить.</strong
              >
            </p>
            <p>
              <strong>Виды информации ограниченного распространения:</strong>
            </p>
            <ul>
              <li>Государственные секреты,</li>
              <li>
                служебная информация с пометкой  «Для служебного
                пользования»,
              </li>
              <li>
                личные, семейные, врачебные, банковские, коммерческие и иные
                охраняемые законом тайны.
              </li>
            </ul>
          </div>
        </div>
        <div class="accordion-item child nested hidden">
          <p class="parent" @click="changeToActive">ГОСУДАРСТВЕННЫЕ СЕКРЕТЫ</p>
          <div class="accordion-item child hidden">
            <div class="child text"></div>
            <p>
              <strong>ГОСУДАРСТВЕННЫЕ СЕКРЕТЫ -&nbsp;</strong>защищаемые
              государством сведения, составляющие государственную и служебную
              тайны, распространение которых ограничивается с целью
              осуществления эффективной военной, экономической,
              научно-технической, внешнеэкономической, внешнеполитической,
              разведывательной, контрразведывательной, оперативно-розыскной и
              иной деятельности, не вступающей в противоречие с общепринятыми
              нормами международного права.
            </p>
            <p>
              <strong>Государственная тайна</strong> - сведения военного,
              экономического, политического и иного характера, разглашение или
              утрата которых наносит или может нанести ущерб национальной
              безопасности РК.
            </p>
            <p>
              Отнести сведения к секретам могут руководители госорганов, у
              которых есть соответствующие полномочия. Они должны обосновать эту
              необходимость в соответствии с принципами засекречивания. При этом
              обоснованность отнесения сведений к государственным секретам может
              быть обжалована в судебном порядке. При решении суда о
              необоснованности засекречивания сведений эти сведения подлежат
              рассекречиванию в установленном Законом РК  «О
              государственных секретах» порядке.
            </p>
            <p><strong>Присвоение степени секретности</strong></p>
            <ul>
              <li>
                 «особой важности»,  «совершенно секретно»
                (сведения, содержащие государственную тайну)&nbsp;
              </li>
              <li>
                 «секретно» (сведения, содержащие служебную тайну)
              </li>
            </ul>
            <p><strong>Определение срока секретности:</strong></p>
            <p>
              Срок засекречивания сведений, составляющих государственные
              секреты, не должен превышать тридцать лет. В исключительных
              случаях этот срок продлевается по заключению уполномоченного
              государственного органа по защите государственных секретов.
            </p>
            <p>
              <strong
                >ВАЖНО! За незаконное собирание сведений, составляющих
                государственные секреты, а равно распространение неправомерно
                полученных сведений, составляющих государственные секреты, при
                отсутствии признаков государственной измены или шпионажа
                предусмотрена уголовная ответственность. &nbsp;</strong
              >
            </p>
          </div>
        </div>
        <div class="accordion-item child nested hidden">
          <p class="parent" @click="changeToActive">
            СЛУЖЕБНАЯ ИНФОРМАЦИЯ С ПОМЕТКОЙ «ДЛЯ СЛУЖЕБНОГО ПОЛЬЗОВАНИЯ»
          </p>
          <div class="accordion-item child hidden">
            <div class="child text">
              <p>
                Это конфиденциальная служебная информация, доступ к которой
                временно ограничен в целях соблюдения административных процедур,
                связанных с обеспечением независимости государственного органа
                при принятии решения. Ей присваивается гриф  «ДСП».
              </p>
              <p>
                Служебной информации ограниченного распространения присваивается
                пометка  «Для служебного пользования». Правила
                отнесения сведений к служебной информации ограниченного
                распространения и работы с ней устанавливаются Правительством
                РК. Эти сведения должны быть зафиксированы в документах, делах и
                изданиях.&nbsp;
              </p>
              <p>
                <strong
                  >Какие сведения относятся к служебной информации ограниченного
                  распространения?</strong
                >
              </p>
              <ul>
                <li>
                  Полученные и создаваемые в ходе проводимых проверок в рамках
                  государственного контроля и надзора, по которым не принято
                  решения;
                </li>
                <li>
                  Полученные и создаваемые в результате внутриведомственной и
                  межведомственной переписки или на основе совещаний в
                  государственных органах, по которым не принято окончательного
                  решения;
                </li>
                <li>
                  Полученные от иностранных государств или международных
                  организаций, по которым не принято взаимного соглашения об
                  условиях раскрытия такой информации.
                </li>
              </ul>
            </div>
          </div>
        </div>
        <div class="accordion-item child nested hidden">
          <p class="parent" @click="changeToActive">ТАЙНЫ</p>
          <div class="accordion-item child hidden">
            <div class="child text">
              <p><strong>Личная и семейная тайны&nbsp;</strong></p>
              <p>
                В соответствии со статьей 18 Конституции РК  «Каждый имеет
                право на неприкосновенность частной жизни, личную и семейную
                тайну, защиту своей чести и достоинства».&nbsp;
              </p>
              <p>
                Неприкосновенность личной тайны относится к личным
                неимущественным благам и правам и находится под охраной закона.
                Должностные лица, осуществляющие государственную регистрацию
                актов гражданского состояния, а также другие лица, иным образом
                осведомленные о частной жизни, обязаны сохранять личную и
                семейную тайну.&nbsp;
              </p>
              <p>
                Надо отметить, что исчерпывающего определения частной жизни не
                существует, это термин широкого толкования и охватывает разные
                сферы человеческой жизни. Разглашение сведений о личной и
                семейной жизни граждан влечет ответственность, установленную
                законами РК.&nbsp;
              </p>
              <p>
                Сюда можно отнести: сведения об отношениях в семье, с
                родственниками, о занятиях вне работы, о дружеских связях, о
                различных сторонах интимной жизни.
              </p>
              <p>
                <strong>Тайна усыновления ребенка</strong> (Кодекс РК о браке и семье)
              </p>
              <p>
                К тайне усыновления относится отсутствие родительского
                попечения; медицинское освидетельствование ребенка и кандидатов
                в усыновители в целях последующего усыновления; постановка на
                первичный учет данных о детях как ребенка, оставшегося без
                попечения родителей, так и кандидата в усыновители; судебное
                рассмотрение заявления об усыновлении; государственная
                регистрация усыновления.
              </p>
              <p><strong>Кто обязан сохранять тайну усыновления?</strong></p>
              <ul>
                <li>
                  <p>Родители, родственники,&nbsp;</p>
                </li>
                <li>
                  <p>
                    должностные лица организаций, осуществляющие функции по
                    защите прав ребенка, и (или) государственных органов,
                    осуществляющих государственную регистрацию
                    усыновления;&nbsp;
                  </p>
                </li>
                <li>
                  <p>судьи, вынесшие решение об усыновлении,&nbsp;</p>
                </li>
                <li>
                  <p>
                    другие лица, иным образом осведомленные об усыновлении
                    ребенка, обязаны сохранять тайну усыновления.
                  </p>
                </li>
              </ul>
              <p>
                Если указанные выше лица вопреки требованию закона и против воли
                усыновителей разгласили тайну усыновления ребенка, они
                привлекаются к уголовной ответственности.
              </p>
              <p>
                <strong
                  >Тайна переписки, телефонных переговоров, почтовых,
                  телеграфных и иных отправлений (УК РК)</strong
                >
              </p>
              <p>
                В соответствии с ч. 2 ст. 18 Конституции,  «Каждый имеет
                право на тайну личных вкладов и сбережений, переписки,
                телефонных переговоров, почтовых, телеграфных и иных
                сообщений».
              </p>
              <p>
                Нарушение тайны переписки, телефонных переговоров, почтовых,
                телеграфных и иных сообщений происходит в случаях, когда
                корреспонденция становится достоянием других лиц без согласия
                адресата. Под нарушением понимается ознакомление третьего лица с
                информацией, переданной одним человеком другому без законных
                оснований на совершение этих действий.
              </p>
              <p>
                Ответственность по данной статье наступает в случаях
                несанкционированного доступа к переписке, и в данном случае не
                требуется, чтобы сообщения содержали сведения, составляющие
                личную или семейную тайну. Тайна переписки будет считаться
                нарушенной в момент, когда человек ознакомился с содержимым
                сообщения.&nbsp;
              </p>
              <p>
                Статья 143 УК РК охраняет тайну самой переписки, телефонных
                переговоров, почтовых и иных сообщений независимо от их
                характера и содержания.&nbsp;
              </p>
              <p>
                <strong>Коммерческая тайна (предпринимательская)</strong> тайна
                (Предпринимательский кодекс РК)
              </p>
              <p>
                Предприниматель сам определяет круг лиц, имеющих право
                свободного доступа к информации, составляющей коммерческую тайну
                и принимает меры к охране ее конфиденциальности.&nbsp;
              </p>
              <p><strong>Признаки коммерческой тайны:</strong></p>
              <ul>
                <li>сведения, связанные с деятельностью предпринимателя;</li>
                <li>
                  тайна имеет действительную или потенциальную ценность в силу
                  ее неизвестности третьим лицам;
                </li>
                <li>
                  разглашение ее может нанести ущерб интересам предпринимателя;
                </li>
                <li>к ней нет свободного доступа;</li>
                <li>
                  обладатель информации принимает меры к охране ее
                  конфиденциальности;
                </li>
                <li>
                  предприниматель сам определяет состав и объем сведений,
                  составляющих коммерческую тайну
                </li>
              </ul>
              <p>
                <strong>Что можно отнести к коммерческой тайне?&nbsp;</strong>
              </p>
              <p>
                сведения о финансах предприятия, заключенных и планируемых
                сделках, сведения, изложенные в бухгалтерской или налоговой
                отчетности, о технологиях производства.&nbsp;
              </p>
              <p><strong>Что нельзя отнести к коммерческой тайне?</strong></p>
              <p>
                сведения, содержащиеся в уставе юридического лица, относящиеся к
                государственной статистической отчетности, о государственной
                регистрации и т.д.
              </p>
              <p>
                <strong
                  >Ответственность за разглашение коммерческой
                  тайны:&nbsp;</strong
                >гражданско-правовая, уголовная, административная,
                дисциплинарная.
              </p>
              <p>
                <strong>Тайна голосования (</strong>Конституционный Закон РК
                 «О выборах в РК»)
              </p>
              <p>
                Голосование в Казахстане является тайным, исключающим
                возможность какого бы то ни было контроля за волеизъявлением
                избирателя. Информацию о том, как проголосовал тот или иной
                избиратель, можно получить только от него или с его
                согласия.&nbsp;
              </p>
              <p>
                К тайне относится голосование на выборах Президента, депутатов
                Парламента и маслихатов, членов иных органов местного
                самоуправления.
              </p>
              <p>
                <strong>Тайна пенсионных накоплений (</strong>Закон РК  «О
                пенсионном обеспечении»)
              </p>
              <p>
                Тайна пенсионных накоплений включает в себя сведения об остатках
                и о движении денег на индивидуальных пенсионных счетах
                вкладчиков (получателей).&nbsp;
              </p>
              <p>
                Должностные лица, работники единого накопительного пенсионного
                фонда, добровольных накопительных пенсионных фондов, Центра,
                ЦОНов и иные лица не вправе разглашать сведения, составляющие
                тайну пенсионных накоплений, и несут за их разглашение
                ответственность.&nbsp;
              </p>
              <p>
                Тайна пенсионных накоплений может быть раскрыта вкладчику,
                осуществляющему пенсионные взносы, получателю, любому третьему
                лицу на основании письменного согласия вкладчика (получателя),
                оформленного в соответствии с требованиями законодательства РК.
              </p>
              <p>
                <strong>Тайна страхования</strong> (Закон РК  «О страховой
                деятельности»)
              </p>
              <p>
                Тайна страхования - это сведения, предоставленные для
                осуществления страхования (например, сведения о страхователе,
                застрахованном лице и выгодоприобретателе, состоянии их
                здоровья, а также об имущественном положении этих лиц).&nbsp;
              </p>
              <p>
                <strong>Не относятся к тайне страхования</strong> сведения о
                заключенных договорах страхования (перестрахования) страховой
                организацией, находящейся в процессе ликвидации. Тайна
                страхования может быть раскрыта третьему лицу на основании
                письменного согласия страхователя (застрахованного,
                выгодоприобретателя).
              </p>
              <p>
                Ответственность за разглашение тайны несут должностные лица,
                работники страховой (перестраховочной) организации, страховых
                холдингов-резидентов РК, страховых брокеров, страховых агентов,
                страховые агенты и иные лица, которые в силу осуществления своих
                служебных обязанностей получили доступ к сведениям, составляющим
                тайну страхования.
              </p>
              <p>
                <strong>Врачебная тайна (</strong>Кодекс  «О здоровье
                народа и системе здравоохранения»)
              </p>
              <p>
                Врачебная тайна - это запрет на разглашение медицинских сведений
                о состоянии пациента, в том числе личных данных, без согласия на
                то самого пациента. К ней относится информация о факте обращения
                за медицинской помощью, состоянии здоровья гражданина, диагнозе
                его заболевания и иные сведения, полученные при его обследовании
                и (или) лечении.
              </p>
              <p>
                <strong
                  >Не допускается разглашение сведений, составляющих врачебную
                  тайну, е</strong
                >сли они стали известны при обучении, исполнении
                профессиональных, служебных и иных обязанностей, кроме случаев,
                установленных законом.
              </p>
              <p>
                <strong>Допускается разглашение врачебной тайны</strong>
                <strong>с согласия пациента&nbsp;</strong>в интересах
                обследования и лечения пациента, для проведения научных
                исследований, использования этих сведений в учебном процессе и
                иных целях.
              </p>
              <p>
                <strong
                  >Допускается разглашение врачебной тайны без согласия
                  пациента:</strong
                >
              </p>
              <p>
                1) в целях обследования и лечения гражданина, не способного
                из-за своего состояния выразить свою волю;
              </p>
              <p>
                2) при угрозе распространения заболеваний, представляющих
                опасность для окружающих, в том числе при донорстве крови и ее
                компонентов;
              </p>
              <p>
                3) по запросу органов дознания и предварительного следствия,
                прокурора, адвоката и (или) суда в связи с проведением
                расследования или судебного разбирательства;
              </p>
              <p>
                4) при оказании медицинской помощи несовершеннолетнему или
                недееспособному лицу для информирования его законных
                представителей;
              </p>
              <p>
                5) при наличии оснований полагать, что вред здоровью гражданина
                причинен в результате противоправных деяний.
              </p>
              <p>
                Ответственность за разглашение врачебной тайны пациента может
                быть дисциплинарная, административная (статья 85-3 КоАП РК
                 «Разглашение врачебной тайны»); гражданская (п/п 4
                пункта 2 ст. 184 Кодекса  «О здоровье народа и системе
                здравоохранения»); уголовную (ст. 144 УК РК
                ответственность за разглашение медицинским работником без
                профессиональной или служебной необходимости сведений о
                заболевании или результатах, выразившееся в сообщении сведений о
                наличии у лица ВИЧ/СПИД).
              </p>
              <p><strong>Налоговая тайна</strong> (Налоговый Кодекс РК)</p>
              <p>
                Налоговую тайну составляют любые полученные налоговым органом
                сведения о налогоплательщике (налоговом агенте), например,
                например, сведения о зачетах и возвратах налогов, других
                обязательных платежей в бюджет, пеней и штрафов по
                налогоплательщикам.
              </p>
              <p><strong>Не могут являться тайной сведения:&nbsp;</strong></p>
              <p>1) о регистрационном номере налогоплательщика;</p>
              <p>
                2) о налогах и других обязательных платежах, уплаченных
                налогоплательщиком (налоговым агентом) в бюджет за налоговый
                период,&nbsp;<strong>за исключением физических лиц;</strong>
              </p>
              <p>
                3) о суммах возврата налога на добавленную стоимость по
                оборотам, облагаемым по нулевой ставке, налогоплательщику из
                бюджета за налоговый год;
              </p>
              <p>
                4) представляемых правоохранительным органам в целях
                преследования по закону лиц, совершающих налоговые
                правонарушения и преступления; судам в ходе рассмотрения дел об
                определении налоговых обязательств налогоплательщика или
                ответственности за налоговые правонарушения и преступления;
                судебным исполнителям при исполнении исполнительных документов с
                санкции прокурора; уполномоченному государственному органу по
                формированию прогнозных показателей государственного бюджета;
              </p>
              <p>
                5) представляемых налоговым или правоохранительным органам
                других государств, международным организациям в соответствии с
                международными договорами (соглашениями) о взаимном
                сотрудничестве между налоговыми или правоохранительными
                органами, одной из сторон которых является РК, а также
                договорами, заключенными Республикой Казахстан с международными
                организациями;
              </p>
              <p>
                6) о регистрационных данных налогоплательщиков, не
                представляющих налоговой отчетности по истечении шести месяцев
                со дня установленного Налоговым кодексом срока ее представления
                и (или) отсутствующих по фактическому адресу (месту жительства),
                а также о налогоплательщиках, признанных судом лжепредприятиями.
              </p>
              <p>
                Не должны разглашать налоговую тайну должностные лица налоговых
                органов, за исключением установленных законов случаев,
                должностные лица иных государственных органов, получившие
                сведения о налогоплательщике (налоговом агенте) от налоговых
                органов за исключением государственных органов, уполномоченных
                законодательством РК на передачу сведений третьим лицам в
                соответствии с международными договорами.
              </p>
              <p>
                <strong
                  >ВАЖНО! Налоговым законодательством предусмотрено положение,
                  согласно которому информация, касающаяся налогоплательщика, не
                  может быть предоставлена другому лицу без письменного
                  разрешения налогоплательщика, то есть ни один налогоплательщик
                  не может получить сведения о другом налогоплательщике без его
                  письменного согласия.</strong
                >
              </p>

              <p>
                <strong>Адвокатская тайна (</strong>Закон РК  «Об
                адвокатской деятельности»)
              </p>
              <p>
                К адвокатской тайне относятся факты обращения к адвокату,
                сведения о содержании устных и письменных переговоров с лицом,
                обратившимся за помощью, и другими лицами, сведения о характере
                и результатах, предпринимаемых в интересах лица, обратившегося
                за помощью, а также иная информация, касающаяся оказания
                юридической помощи.&nbsp;
              </p>
              <p>
                Адвокаты, их помощники и стажеры, работники президиума коллегии
                адвокатов, юридической консультации, адвокатской конторы не
                вправе разглашать, а также использовать в своих интересах или
                интересах третьих лиц какие-либо сведения, полученные в связи с
                оказанием юридической помощи.
              </p>
              <p>
                Адвокат, разгласивший сведения, относящиеся к адвокатской тайне,
                без согласия лица, обратившегося за помощью, несет
                ответственность в соответствии с законом.
              </p>
              <p>
                Обязанность хранения адвокатской тайны не ограничена во времени.
              </p>
              <p>
                <strong>Банковская тайна (</strong>Закон РК  «О банках и
                банковской деятельности в РК»)&nbsp;
              </p>
              <p>
                Банковская тайна включает в себя сведения о наличии, владельцах
                и номерах банковских счетов депозиторов, клиентов и
                корреспондентов банка, об остатках и движении денег на этих
                счетах и счетах самого банка, об операциях банка (за исключением
                общих условий проведения банковских операций), а также сведения
                о наличии, владельцах, характере и стоимости имущества клиентов,
                находящегося на хранении в сейфовых ящиках, шкафах и помещениях
                банка.&nbsp;
              </p>
              <p>
                Не относятся к тайне сведения о кредитах, выданных банком,
                находящимся в процессе ликвидации.&nbsp;
              </p>
              <p>
                Ответственность за разглашение несут должностные лица, работники
                банков и иные лица, которые в силу осуществления своих служебных
                обязанностей получили доступ к сведениям, составляющим
                банковскую тайну.&nbsp;
              </p>
              <p>
                <strong
                  >Важно! Банковская тайна может быть раскрыта только клиенту,
                  любому третьему лицу на основании согласия владельца счета
                  (имущества), данного в письменной форме либо посредством
                  идентификационного средства владельца счета, кредитному бюро
                  по банковским заемным, лизинговым, факторинговым,
                  форфейтинговым операциям, учету векселей, а также выпущенным
                  банком гарантиям, поручительствам, аккредитивам в соответствии
                  с законами РК, а также лицам, указанным в законе.</strong
                >
              </p>
              <p><strong>Тайна следствия (УПК РК)</strong></p>
              <p>
                Тайна следствия &ndash; это информация о данных дознания или
                предварительного следствия, и она охраняется законом, потому что
                ее разглашение может затруднить или сделать невозможным
                установление объективной истины.
              </p>
              <p>
                Сведения могут быть преданы гласности только с разрешения
                прокурора в том объеме, в каком им будет признано это возможным,
                если это не противоречит интересам расследования и не связано с
                нарушением прав и законных интересов других лиц.
              </p>
              <p>
                Лицо, осуществляющее досудебное расследование, предупреждает
                защитника, свидетелей, потерпевшего, гражданского истца,
                гражданского ответчика или их представителей, эксперта,
                специалиста, переводчика, понятых и других лиц, присутствующих
                при производстве следственных действий, о недопустимости
                разглашения без его разрешения имеющихся в деле сведений, о чем
                от указанных лиц отбирается подписка с предупреждением об
                ответственности.
              </p>
              <p>
                За разглашение данных досудебного производства, в случае если
                лицо было предупреждено в установленном законом РК о порядке
                недопустимости их разглашения, и если оно совершено без согласия
                прокурора или лица, осуществляющего досудебное производство
                предусмотрена уголовная ответственность в виде штрафа в размере
                до 2000 МРП либо исправительными работами в том же размере, либо
                привлечением к общественным работам на срок до шестисот часов,
                либо ограничением свободы на срок до двух лет, либо лишением
                свободы на тот же срок.
              </p>
              <p>
                <strong
                  >Тайна совещания судей, или тайна совещательной
                  комнаты</strong
                >&nbsp;
              </p>
              <p>
                Для обеспечения тайны совещательной комнаты никто не имеет права
                присутствовать в ней, кроме состава суда, который осуществляет
                судебное разбирательство.
              </p>
              <p>
                Судья не обязан давать каких-либо объяснений по существу
                рассмотренных или находящихся в производстве судебных дел. Тайна
                совещательной комнаты должна быть обеспечена во всех без
                исключения случаях.
              </p>
              <p>
                Тайна совещания судей, то есть тайна совещательной комнаты судей
                обеспечена нормами процессуального законодательства. Информация
                о том, что происходило в совещательной комнате, не подлежит
                оглашению. Любая попытка проникновения в совещательную комнату
                является незаконной, будет расценена как неуважение к суду и
                наказана по ст. 653 Кодекса об административных правонарушениях
                РК.
              </p>
              <p>
                <strong>Тайна нотариальных действий (</strong>Закон РК  «О
                нотариате»)
              </p>
              <p>
                Физическим и юридическим лицам гарантируется тайна совершенных
                ими нотариальных действий.
              </p>
              <p>
                Нотариус обязан хранить в тайне сведения, которые стали ему
                известны в связи с осуществлением его профессиональной
                деятельности. Например, сведения, которые содержатся в
                удостоверяемом документе либо сведения, которые стали им
                известны в связи с совершением нотариальных действий, а также о
                самом факте обращения гражданина к нотариусу.&nbsp;
              </p>
              <p>
                Для сохранения тайны нотариального действия в момент его
                совершения не должны присутствовать посторонние лица (не
                являющиеся лицом, от имени или по поручению которого
                осуществляется нотариальное действие).
              </p>
              <p>
                Нотариус, занимающийся частной практикой, обязан иметь одно
                помещение, пригодное для беспрепятственного доступа физических
                лиц и представителей юридических лиц, соблюдения тайны
                совершения нотариальных действий и условий для обеспечения
                сохранности нотариального делопроизводства.
              </p>
              <p>
                Обязанность соблюдения тайны нотариальных действий
                распространяется на стажеров и помощников нотариусов.
              </p>
              <p>
                Лица, умышленно разглашающие сведения о совершенном нотариальном
                действии, несут ответственность в соответствии с
                законодательством.
              </p>
              <p>
                <strong>Аудиторская тайна</strong> (Закон РК  «Об
                аудиторской деятельности»)&nbsp;
              </p>
              <p>
                Аудиторской тайной являются сведения, полученные аудитором и
                (или) аудиторской организацией при исполнении договора на
                проведение аудита, аудита по налогам, за исключением сведений,
                представляемых органам государственных доходов.
              </p>
              <p>
                Нарушение обязанности сохранения сведений, содержащих
                коммерческую тайну, влечет ответственность, установленную
                законами РК.
              </p>
              <p>
                <strong
                  >Журналистская тайна (тайна авторства и источника информации)
                  (закон РК  «О СМИ»)</strong
                >
              </p>
              <p>
                Журналист имеет право на сохранение тайны авторства и источников
                информации, например, имя источника информации, если он попросил
                сохранить его в конфиденциальности; сама информация,
                предоставленная источником, с условием ее неразглашения, когда
                это может поставить источник информации в ситуацию риска.
              </p>
              <p>
                <strong
                  >Важно! В отдельных случаях тайны обнародуются по требованию
                  суда.&nbsp;</strong
                >
              </p>
            </div>
          </div>
        </div>
        <div class="accordion-item child nested hidden">
          <p class="parent" @click="changeToActive">ПЕРСОНАЛЬНЫЕ ДАННЫЕ</p>
          <div class="accordion-item child hidden">
            <div class="child text">
              <p>
                Персональные данные - сведения, относящиеся к определенному или
                определяемому на их основании субъекту персональных данных,
                зафиксированные на электронном, бумажном и (или) ином
                материальном носителе. К ним фамилия, имя, отчество, данные о
                месте и дате рождения; данные документов, удостоверяющих
                личность; адрес и телефон. Исчерпывающего перечня персональных
                данных в Законе нет.
              </p>
              <p>
                По доступности подразделяются на общедоступные и ограниченного
                доступа.
              </p>
              <p>
                <strong>Общедоступные </strong>- персональные данные, доступ к
                которым является свободным с согласия субъекта или на которые в
                соответствии с законодательством РК не распространяются
                требования соблюдения конфиденциальности (например,
                биографические справочники, телефонные книги, информация в СМИ и
                т.п.)
              </p>
              <p>
                <strong>Ограниченного доступа</strong> - персональные данные,
                доступ к которым ограничен законодательством РК (например,
                данные удостоверения личности, ИИН, личные номера телефоны,
                адрес местожительства, национальность, место работы, условия
                трудового договора и т.д.).
              </p>
              <p>
                <strong
                  >Кто обеспечивает конфиденциальность персональных данных?
                </strong>
              </p>
              <p>
                Собственники и (или) операторы, а также третьи лица, получающие
                доступ к персональным данным ограниченного доступа. Лица,
                которым стали известны персональные данные ограниченного доступа
                в связи с профессиональной, служебной необходимостью, а также
                трудовыми отношениями, обязаны обеспечивать их
                конфиденциальность.
              </p>
              <p>
                <strong
                  >Использование персональных данных в работе
                  журналиста;</strong
                >
              </p>
              <p>
                Журналист может производить сбор, обработку персональных данных
                <strong>без согласия субъекта,</strong> если это необходимо для
                осуществления его законной профессиональной деятельности при
                условии соблюдения требований законодательства РК по обеспечению
                прав и свобод человека и гражданина.
              </p>
              <p>
                <strong
                  >Чем грозит нарушение законодательства о персональных
                  данных?</strong
                >
              </p>
              <p>административная ответственность (ст. 79 и ст. 641 КОАП РК)</p>
              <p>уголовная ответственность (ст. 147 и ст. 211 УК РК)</p>
            </div>
          </div>
        </div>
        <div class="accordion-item child nested hidden">
          <p class="parent" @click="changeToActive">ЗАКОНОДАТЕЛЬНАЯ БАЗА</p>

          <div class="accordion-item child nested hidden">
            <p class="parent" @click="changeToActive">ТАЙНА ГОЛОСОВАНИЯ</p>
            <div class="accordion-item child hidden">
              <div class="child text">
                <p>
                  <strong
                    >Конституционный закон Республики Казахстан О судебной
                    системе и статусе судей Республики Казахстан</strong
                  >
                </p>
                <p>Статья 25. Независимость судьи</p>
                <p>
                  1. Независимость судьи защищается Конституцией и законом. При
                  осуществлении правосудия судьи независимы и подчиняются только
                  Конституции Республики Казахстан и закону.
                </p>
                <p>
                  2. Никто не вправе вмешиваться в осуществление правосудия и
                  оказывать какое-либо воздействие на судью и присяжных
                  заседателей. Такие действия преследуются по закону.
                </p>
                <p>
                  3. Судья не обязан давать каких-либо объяснений по существу
                  рассмотренных или находящихся в производстве судебных дел.
                  Тайна совещательной комнаты должна быть обеспечена во всех без
                  исключения случаях.
                </p>
              </div>
            </div>
          </div>

          <div class="accordion-item child nested hidden">
            <p class="parent" @click="changeToActive">
