                <p><strong>УГОЛОВНЫЙ КОДЕКС РК </strong></p>
                <p>
                  Статья 185 Незаконное собирание сведений, составляющих
                  государственные секреты, а равно распространение неправомерно
                  полученных сведений, составляющих государственные секреты, при
