                  &ndash;
                </p>
                <p>
                  наказываются ограничением свободы на срок до пяти лет либо
                  лишением свободы на тот же срок, с лишением права занимать
                  определенные должности или заниматься определенной
                  деятельностью на срок до трех лет или без такового.
                </p>
                <p>
                  2. Разглашение сведений, составляющих государственные секреты,
                  лицом, которому они были доверены или стали известны по
                  службе, работе, либо по иным предусмотренным законом
                  Республики Казахстан основаниям, при отсутствии признаков
                  государственной измены &ndash;
                </p>
                <p>
                  наказывается ограничением свободы на срок до пяти лет либо
                  лишением свободы на тот же срок, с лишением права занимать
                  определенные должности или заниматься определенной
                  деятельностью на срок до трех лет или без такового.
                </p>
                <p>
                  3. Деяния, предусмотренные частями первой или второй настоящей
                  статьи, если они повлекли причинение крупного ущерба или
                  наступление иных тяжких последствий, &ndash;
                </p>
                <p>
                  наказываются лишением свободы на срок от пяти до восьми лет с
                  лишением права занимать определенные должности или заниматься
                  определенной деятельностью на срок до трех лет.
                </p>
