                  предназначенных для негласного получения информации, либо
                  путем незаконного доступа к электронным информационным
                  ресурсам, информационной системе или незаконного перехвата
                  информации, передаваемой по сетям телекоммуникаций, -
                </p>
                <p>
                  наказывается лишением свободы на срок до пяти лет с лишением
                  права занимать определенные должности или заниматься
                  определенной деятельностью на срок от двух до пяти лет.
