          <div class="accordion-item child nested hidden">
            <p class="parent" @click="changeToActive">ТАЙНА СЛЕДСТВИЯ</p>
            <div class="accordion-item child hidden">
              <div class="child text">
                <p>
                  <strong
                    >Уголовно-процессуальный кодекс Республики Казахстан
                  </strong>
                </p>
                <p>
                  Статья 201. Недопустимость разглашения данных досудебного
                  расследования
                </p>
                <p>
                  1. Данные досудебного расследования не подлежат разглашению.
                  Они могут быть преданы гласности только с разрешения прокурора
                  в том объеме, в каком им будет признано это возможным, если
                  это не противоречит интересам расследования и не связано с
                  нарушением прав и законных интересов других лиц.
                </p>
                <p>
                  2. Лицо, осуществляющее досудебное расследование,
                  предупреждает защитника, свидетелей, потерпевшего,
                  гражданского истца, гражданского ответчика или их
                  представителей, эксперта, специалиста, переводчика, понятых и
                  других лиц, присутствующих при производстве следственных
                  действий, о недопустимости разглашения без его разрешения
                  имеющихся в деле сведений, о чем от указанных лиц отбирается
                  подписка с предупреждением об ответственности.
                </p>
                <p><strong>Уголовный кодекс Республики Казахстан</strong></p>
                <p>Статья 423. Разглашение данных досудебного производства</p>
                <p>
                  Разглашение данных досудебного производства лицом,
                  предупрежденным в установленном законом Республики Казахстан
                  порядке о недопустимости их разглашения, если оно совершено
                  без согласия прокурора или лица, осуществляющего досудебное
                  производство, -
                </p>
                <p>
                  наказывается штрафом в размере до двух тысяч месячных
                  расчетных показателей либо исправительными работами в том же
                  размере, либо привлечением к общественным работам на срок до
                  шестисот часов, либо ограничением свободы на срок до двух лет,
                  либо лишением свободы на тот же срок.
                </p>
              </div>
            </div>
          </div>

          <div class="accordion-item child nested hidden">
            <p class="parent" @click="changeToActive">
              СЛУЖЕБНАЯ И КОММЕРЧЕСКАЯ ТАЙНА
            </p>
            <div class="accordion-item child hidden">
              <div class="child text">
                <p>
                  <strong
                    >Гражданский кодекс Республики Казахстан (Общая
                    часть)</strong
                  >
                </p>
                <p>
                  <strong>Статья 126. Служебная и коммерческая тайна</strong>
                </p>
                <p>
                  1. Гражданским законодательством защищается информация,
                  составляющая служебную или коммерческую тайну, в случае, когда
                  информация имеет действительную или потенциальную коммерческую
                  ценность в силу неизвестности ее третьим лицам, к ней нет
                  свободного доступа на законном основании и обладатель
                  информации принимает меры к охране ее конфиденциальности.
                </p>
                <p>
                  2. Лица, незаконными методами получившие такую информацию, а
                  также служащие вопреки трудовому договору или контрагенты
                  вопреки гражданско-правовому договору, разгласившие служебную
                  или коммерческую тайну, обязаны возместить причиненный ущерб.
                </p>
                <p>
                  <strong
                    >Предпринимательский кодекс Республики
                    Казахстан»</strong
                  >
                </p>
                <p>
                  Статья 28. Охрана информации, составляющей коммерческую тайну
                </p>
                <p>
                  1. Охрана коммерческой тайны заключается в запрете незаконного
                  получения, распространения либо использования информации,
                  составляющей коммерческую тайну в соответствии с настоящим
                  Кодексом и законодательством Республики Казахстан.
                </p>
                <p>
                  2. Субъект предпринимательства определяет круг лиц, имеющих
                  право свободного доступа к информации, составляющей
                  коммерческую тайну, и принимает меры к охране ее
                  конфиденциальности.
                </p>
                <p>
                  3. Порядок отнесения информации к категориям доступа, условия
                  хранения и использования информации, составляющей коммерческую
                  тайну, определяются субъектом предпринимательства.
                </p>
                <p>
                  4. Принимаемые субъектом предпринимательства меры по охране
                  информации, составляющей коммерческую тайну, могут включать в
                  себя:
                </p>
                <p>
                  1) определение перечня информации, составляющей коммерческую
                  тайну;
                </p>
                <p>
                  2) ограничение доступа к коммерческой тайне путем установления
                  порядка обращения с этой информацией и контроля за соблюдением
                  такого порядка;
                </p>
                <p>
                  3) учет лиц, получивших доступ к коммерческой тайне, и (или)
                  лиц, которым эта информация была предоставлена или передана.
                </p>
                <p>
                  5. Лица, незаконными методами получившие, раскрывшие и (или)
                  использовавшие информацию, составляющую коммерческую тайну,
                  обязаны возместить причиненный ущерб в соответствии с
                  гражданским законодательством Республики Казахстан.
                </p>
                <p>
                  6. Субъект предпринимательства или лицо, им уполномоченное,
                  вправе требовать у своих работников подписку о неразглашении
                  информации, составляющей коммерческую тайну, а лиц,
                  осуществляющих его проверку, предупреждать об ответственности
                  в соответствии с законами Республики Казахстан.
                </p>
                <p>
                  7. Перечень информации, подлежащей обязательному опубликованию
                  либо обязательному доведению до сведения акционеров
                  акционерного общества, участников хозяйственного товарищества,
                  членов производственного кооператива или иного определенного
                  круга лиц, устанавливается законами Республики Казахстан и
                  учредительными документами субъекта предпринимательства.
                </p>
                <p>
                  8. Информация, составляющая коммерческую тайну, не может быть
                  разглашена без согласия субъекта предпринимательства, за
                  исключением информации, по которой имеется вступившее в
                  законную силу решение суда, или иных случаев, установленных
                  настоящим Кодексом.
                </p>
                <p>
                  9. Государственные органы и их должностные лица без согласия
                  субъекта предпринимательства не вправе распространять и (или)
                  использовать ставшую известной им в силу выполнения
                  должностных (служебных) обязанностей информацию, составляющую
                  коммерческую тайну, за исключением случаев, предусмотренных
                  законами Республики Казахстан.
                </p>
                <p>
                  10. Субъект предпринимательства вправе не предоставлять
                  государственным органам и должностным лицам при выполнении
                  регистрационных, контрольных и надзорных функций и совершении
                  других действий доступ к информации, составляющей коммерческую
                  тайну, кроме той, которая необходима для реализации
                  возложенных на них функций.
                </p>
                <p>
                  10-1. Субъект предпринимательства, владеющий на праве
                  собственности или другом законном основании критически важным
                  объектом информационно-коммуникационной инфраструктуры,
                  передает резервную копию электронного информационного ресурса
                  на единую платформу резервного хранения электронных
                  информационных ресурсов в порядке, определенном
                  законодательством Республики Казахстан, с учетом требований
                  законодательства Республики Казахстан о национальной
                  безопасности.
                </p>
                <p>
                  11. Государственные органы и их должностные лица несут
                  установленную законами Республики Казахстан ответственность за
                  незаконное распространение и (или) использование информации,
                  составляющей коммерческую тайну, а ущерб, причиненный субъекту
                  предпринимательства в результате такого распространения и
                  (или) использования, подлежит возмещению в соответствии с
                  гражданским законодательством Республики Казахстан.
                </p>
                <p>
                  12. Обобщенная информация, не раскрывающая сведений о
                  деятельности конкретного субъекта предпринимательства,
                  является общедоступной.
                </p>
                <p>
                  13. К общедоступной информации субъекта предпринимательства
                  относятся:
                </p>
                <p>
                  1) фамилия, имя, отчество (если оно указано в документе,
                  удостоверяющем личность) или наименование индивидуального
                  предпринимателя;
                </p>
                <p>2) наименование и дата регистрации юридического лица;</p>
                <p>3) идентификационный номер;</p>
                <p>4) юридический адрес (место нахождения);</p>
                <p>5) вид деятельности;</p>
                <p>
                  6) фамилия, имя, отчество (если оно указано в документе,
                  удостоверяющем личность) руководителя.
                </p>
              </div>
            </div>
          </div>

          <div class="accordion-item child nested hidden">
            <p class="parent" @click="changeToActive">ТАЙНА ЧАСТНОЙ ЖИЗНИ</p>
            <div class="accordion-item child hidden">
              <div class="child text">
                <p>
                  <strong
                    >Кодекс РК  «О браке (супружестве) и
                    семье»</strong
                  >
                </p>
                <p>
                  Статья 53. Неприкосновенность частной жизни, личная и семейная
                  тайна
                </p>
                <p>
                  1. Частная жизнь, личная и семейная тайна находятся под
                  охраной закона.
                </p>
                <p>
                  2. Должностные лица, осуществляющие государственную
                  регистрацию актов гражданского состояния, а также другие лица,
                  иным образом осведомленные о частной жизни, обязаны сохранять
                  личную и семейную тайну.
                </p>
                <p>
                  3. Разглашение сведений о личной и семейной жизни граждан
                  влечет ответственность, установленную законами Республики
                  Казахстан.
                </p>
              </div>
            </div>
          </div>

          <div class="accordion-item child nested hidden">
            <p class="parent" @click="changeToActive">ПЕРСОНАЛЬНЫЕ ДАННЫЕ</p>
            <div class="accordion-item child hidden">
              <div class="child text">
                <p>
                  <strong
                    >Закон  «О персональных данных и их
                    защите»</strong
                  >
                </p>
                <p>Статья 6. Доступность персональных данных</p>
                <p>
                  Персональные данные по доступности подразделяются на
                  общедоступные и ограниченного доступа.
                </p>
                <p>
                  Общедоступные персональные данные - персональные данные,
                  доступ к которым является свободным с согласия субъекта или на
                  которые в соответствии с законодательством Республики
                  Казахстан не распространяются требования соблюдения
                  конфиденциальности.
                </p>
                <p>
                  В целях информационного обеспечения населения используются
                  общедоступные источники персональных данных (в том числе
                  биографические справочники, телефонные, адресные книги,
                  общедоступные электронные информационные ресурсы, средства
                  массовой информации).
                </p>
                <p>
                  Персональные данные ограниченного доступа - персональные
                  данные, доступ к которым ограничен законодательством
                  Республики Казахстан.
                </p>
                <p>
                  Статья 9. Сбор, обработка персональных данных без согласия
                  субъекта
                </p>
                <p>
                  6) осуществления законной профессиональной деятельности
                  журналиста и (или) деятельности средства массовой информации
                  либо научной, литературной или иной творческой деятельности
                  при условии соблюдения требований законодательства Республики
                  Казахстан по обеспечению прав и свобод человека и гражданина;
                </p>
              </div>
            </div>
          </div>

          <div class="accordion-item child nested hidden">
            <p class="parent" @click="changeToActive">ТАЙНА УСЫНОВЛЕНИЯ</p>
            <div class="accordion-item child hidden">
              <div class="child text">
                <p>Статья 102. Тайна усыновления ребенка</p>
                <p>1. Тайна усыновления ребенка охраняется законом.</p>
                <p>
                  2. Родители, родственники, должностные лица организаций,
                  осуществляющие функции по защите прав ребенка, и (или)
                  государственных органов, осуществляющих государственную
                  регистрацию усыновления, судьи, вынесшие решение об
                  усыновлении, а также другие лица, иным образом осведомленные
                  об усыновлении ребенка, обязаны сохранять тайну усыновления.
                </p>
              </div>
            </div>
          </div>

          <div class="accordion-item child nested hidden">
            <p class="parent" @click="changeToActive">АДВОКАТСКАЯ ТАЙНА</p>
            <div class="accordion-item child hidden">
              <div class="child text">
                <p><strong>ЗАКОН РК</strong></p>
                <p>Об адвокатской деятельности и юридической помощи</p>
                <p>Статья 9. Сохранение профессиональной тайны</p>
                <p>
                  Профессиональная тайна не может быть раскрыта без согласия
                  клиента.
                </p>
                <p>
                  При оказании юридической помощи принимаются меры по сохранению
                  профессиональной тайны, если иное не предусмотрено законами
                  Республики Казахстан. Обязанность сохранения профессиональной
                  тайны не ограничена во времени.
                </p>
                <p>Статья 37. Адвокатская тайна</p>
                <p>
                  1. Адвокатскую тайну составляют факт обращения к адвокату,
                  сведения о содержании устных и письменных переговоров с лицом,
                  обратившимся за помощью, и другими лицами, о характере и
                  результатах предпринимаемых в интересах лица, обратившегося за
                  помощью, действий, а также иная информация, касающаяся
                  оказания юридической помощи.
                </p>
                <p>
                  2. Адвокаты, их помощники, стажеры, работники президиума
                  коллегии адвокатов, юридической консультации, адвокатской
                  конторы, а также лицо, в отношении которого прекращено или
                  приостановлено право на занятие адвокатской деятельностью, не
                  вправе разглашать, а также использовать в своих интересах или
                  интересах третьих лиц какие-либо сведения, полученные в связи
                  с оказанием юридической помощи.
                </p>
                <p>
                  3. Адвокат, разгласивший сведения, относящиеся к адвокатской
                  тайне, без согласия лица, обратившегося за помощью, несет
                  ответственность в соответствии с законом.
                </p>
                <p>
                  4. Представление сведений и информации в уполномоченный орган
                  по финансовому мониторингу в соответствии с Законом Республики
                  Казахстан  «О противодействии легализации (отмыванию)
                  доходов, полученных преступным путем, и финансированию
                  терроризма» не является разглашением адвокатской тайны.
                </p>
                <p>
                  5. Обязанность хранения адвокатской тайны не ограничена во
                  времени.
                </p>
                <p>
                  6. Адвокаты, их помощники, стажеры, работники президиума
                  коллегии адвокатов, юридической консультации, адвокатской
                  конторы обязаны принимать необходимые меры для сохранения
                  адвокатской тайны, в том числе и ее защиты от
                  несанкционированного доступа.
                </p>
              </div>
            </div>
          </div>

          <div class="accordion-item child nested hidden">
            <p class="parent" @click="changeToActive">БАНКОВСКАЯ ТАЙНА</p>
            <div class="accordion-item child hidden">
              <div class="child text">
                <p><strong>Закон Республики Казахстан </strong></p>
                <p>О банках и банковской деятельности в Республике Казахстан</p>
                <p>Статья 50. Банковская тайна</p>
                <p>
                  1. Банковская тайна включает в себя сведения о клиентах и
                  корреспондентах банков, их операциях и взаимоотношениях с
                  банками, связанных с получением банковских услуг, в том числе
                  без ограничения: информацию о наличии, владельцах и номерах
                  банковских счетов и корреспондентов банков, остатках и
                  движении денег на этих счетах и счетах самого банка,
                  ограничениях на перечисленных счетах (решениях и (или)
                  распоряжениях государственных органов о приостановлении
                  расходных операций, арестах, залогах), операциях клиентов и
                  корреспондентов и самого банка (за исключением общих условий
                  проведения банковских операций), а также наличии, владельцах,
                  характере и стоимости имущества клиентов, находящегося на
                  хранении в сейфовых ящиках, шкафах и помещениях банка,
                  информацию о получении клиентами кредитов (кроме случаев,
                  определенных в настоящей статье), проведении операций по
                  платежам и (или) переводам денег, в том числе выполненным без
                  открытия банковского счета.
                </p>
                <p>
                  Не относятся к банковской тайне сведения о кредитах, выданных
                  банком, находящимся в процессе ликвидации.
                </p>
                <p>
                  2. Банки гарантируют тайну по операциям и счетам своих
                  депозиторов, клиентов и корреспондентов банков, а также тайну
                  имущества, находящегося на хранении в сейфовых ящиках, шкафах
                  и помещениях банков, и иных сведений, составляющих банковскую
                  тайну согласно настоящей статье.
                </p>
                <p>
                  3. Должностные лица, работники банков, банковских
                  холдингов-резидентов Республики Казахстан, юридических лиц,
                  указанных в подпунктах 8), 8-1) и 8-2) части второй пункта 4
                  настоящей статьи, и иные лица, которые в силу осуществления
                  своих служебных обязанностей получили доступ к сведениям,
                  составляющим банковскую тайну, за их разглашение несут
                  уголовную ответственность, за исключением случаев,
                  предусмотренных пунктами 4 - 8, 8-1 настоящей статьи.
                </p>
                <p>
                  4. Банковская тайна может быть раскрыта только клиенту, любому
                  третьему лицу на основании согласия владельца счета
                  (имущества), данного в письменной форме либо посредством
                  идентификационного средства владельца счета, кредитному бюро
                  по банковским заемным, лизинговым, факторинговым,
                  форфейтинговым операциям, учету векселей, а также выпущенным
                  банком гарантиям, поручительствам, аккредитивам в соответствии
                  с законами Республики Казахстан, а также лицам, указанным в
                  пунктах 5, 6, 6-1, 7, 7-1, 7-2, 8 и 8-1 настоящей статьи, по
                  основаниям и в пределах, предусмотренных настоящей статьей.
                </p>
              </div>
            </div>
          </div>

          <div class="accordion-item child nested hidden">
            <p class="parent" @click="changeToActive">ТАЙНА СТРАХОВАНИЯ</p>
            <div class="accordion-item child hidden">
              <div class="child text">
                <p>
                  <strong>Закон  «О страховой деятельности»</strong>
                </p>
                <p>
                  4. Работники и члены объединения актуариев несут
                  ответственность за разглашение сведений, полученных в ходе
                  осуществления ими своих функций, составляющих служебную,
                  коммерческую тайны, тайну страхования или иную охраняемую
                  законом тайну, в соответствии с законами Республики Казахстан.
                </p>
              </div>
            </div>
          </div>

          <div class="accordion-item child nested hidden">
            <p class="parent" @click="changeToActive">
              ТАЙНА ПЕНСИОННЫХ ОТЧИСЛЕНИЙ
            </p>
            <div class="accordion-item child hidden">
              <div class="child text">
                <p>
                  <strong
                    >Закон  «О пенсионном обеспечении в Республике
                    Казахстан»</strong
                  >
                </p>
                <p>Статья 57. Тайна пенсионных накоплений</p>
                <p>
                  1. Тайна пенсионных накоплений включает в себя сведения об
                  остатках и о движении денег на индивидуальных пенсионных
                  счетах вкладчиков (получателей).
                </p>
                <p>
                  Единый накопительный пенсионный фонд, добровольные
                  накопительные пенсионные фонды и Государственная корпорация
                  гарантируют тайну пенсионных накоплений.
                </p>
                <p>
                  2. Должностные лица, работники единого накопительного
                  пенсионного фонда, добровольных накопительных пенсионных
                  фондов, Государственной корпорации и иные лица, которые в силу
                  осуществления своих служебных и функциональных обязанностей
                  получили доступ к сведениям, составляющим тайну пенсионных
                  накоплений, не вправе разглашать сведения, составляющие тайну
                  пенсионных накоплений, и несут за их разглашение
                  ответственность в соответствии с законами Республики
                  Казахстан, за исключением случаев, предусмотренных пунктами 3
                  - 5 настоящей статьи.
                </p>
                <p>
                  3. Тайна пенсионных накоплений может быть раскрыта вкладчику,
                  осуществляющему пенсионные взносы, получателю, любому третьему
                  лицу на основании письменного согласия вкладчика (получателя),
                  оформленного в соответствии с требованиями законодательства
                  Республики Казахстан.
                </p>
                <p>
                  4. Справки об остатках и движении денег на индивидуальных
                  пенсионных счетах могут выдаваться:
                </p>
                <p>
                  1) органам дознания и предварительного следствия - по
                  находящимся в их производстве уголовным делам;
                </p>
                <p>
                  2) судам - по находящимся в их производстве делам на основании
                  определения суда;
                </p>
                <p>
                  3) судебным исполнителям - по находящимся в их производстве
                  исполнительным документам на основании постановления судебного
                  исполнителя, санкционированного прокурором;
                </p>
                <p>
                  4) органам государственных доходов - по вопросам, связанным с
                  исчислением, удержанием (начислением) обязательных пенсионных
                  взносов, обязательных профессиональных пенсионных взносов;
                </p>
                <p>
                  5) уполномоченному органу - по вопросу, возникшему в связи с
                  заявлением вкладчика (получателя) либо в связи с
                  осуществлением им проверки деятельности единого накопительного
                  пенсионного фонда, добровольного накопительного пенсионного
                  фонда;
                </p>
                <p>
                  6) прокурору - на основании постановления о производстве
                  проверки в пределах его компетенции по находящемуся у него на
                  рассмотрении материалу;
                </p>
                <p>
                  7) уполномоченному органу по финансовому мониторингу - в целях
                  и порядке, предусмотренных Законом Республики Казахстан
                   «О противодействии легализации (отмыванию) доходов,
                  полученных преступным путем, и финансированию
                  терроризма»;
                </p>
                <p>
                  8) представителям вкладчика (получателя) - на основании
                  нотариально удостоверенной доверенности или решения суда;
                </p>
                <p>
                  9) Государственной корпорации - для формирования базы данных
                  вкладчиков (получателей) по обязательным пенсионным взносам,
                  обязательным профессиональным пенсионным взносам и расчета
                  сохранности обязательных пенсионных взносов, обязательных
                  профессиональных пенсионных взносов в едином накопительном
                  пенсионном фонде в размере фактически внесенных обязательных
                  пенсионных взносов, обязательных профессиональных пенсионных
                  взносов с учетом уровня инфляции на момент приобретения
                  получателем права на пенсионные выплаты, а также для оказания
                  государственных услуг на основании заявления вкладчика
                  (получателя) либо его представителя по нотариально
                  удостоверенной доверенности или решению суда;
                </p>
                <p>
                  10) центральному исполнительному органу - по вопросу,
                  возникшему в связи с заявлением вкладчика (получателя);
                </p>
                <p>
                  11) аудиторским организациям, проводящим ежегодный
                  обязательный аудит единого накопительного пенсионного фонда,
                  добровольного накопительного пенсионного фонда.
                </p>
                <p>
                  5. Справки о наличии индивидуального пенсионного счета,
                  остатках и движении денег на нем в случае смерти вкладчика
                  (получателя) выдаются по письменному запросу лицам, являющимся
                  наследниками в соответствии со свидетельством о праве на
                  наследство по закону или указанным в завещании, либо судам на
                  основании определения по находящимся в их производстве
                  наследственным делам.
                </p>
                <p>
                  6. Справки о наличии индивидуального пенсионного счета, об
                  остатках и движениях денег на нем в случае смерти вкладчика
                  (получателя) выдаются нотариусам и иностранным консульским
                  учреждениям по находящимся в их производстве наследственным
                  делам.
                </p>
              </div>
            </div>
          </div>

          <div class="accordion-item child nested hidden">
            <p class="parent" @click="changeToActive">ВРАЧЕБНАЯ ТАЙНА</p>
            <div class="accordion-item child hidden">
              <div class="child text">
                <p> «О здоровье народа и системе здравоохранения»</p>
                <p>Статья 95. Врачебная тайна</p>
                <p>
                  1. Информация о факте обращения за медицинской помощью,
                  состоянии здоровья гражданина, диагнозе его заболевания и иные
                  сведения, полученные при его обследовании и (или) лечении,
                  составляют врачебную тайну.
                </p>
                <p>
                  2. Не допускается разглашение сведений, составляющих врачебную
                  тайну, лицами, которым они стали известны при обучении,
                  исполнении профессиональных, служебных и иных обязанностей,
                  кроме случаев, установленных пунктами 3 и 4 настоящей статьи.
                </p>
                <p>
                  3. С согласия пациента или его законного представителя
                  допускается передача сведений, составляющих врачебную тайну,
                  другим физическим и (или) юридическим лицам в интересах
                  обследования и лечения пациента, для проведения научных
                  исследований, использования этих сведений в учебном процессе и
                  иных целях.
                </p>
                <p>
                  4. Представление сведений, составляющих врачебную тайну, без
                  согласия гражданина или его законного представителя
                  допускается в следующих случаях:
                </p>
                <p>
                  1) в целях обследования и лечения гражданина, не способного
                  из-за своего состояния выразить свою волю;
                </p>
                <p>
                  2) при угрозе распространения заболеваний, представляющих
                  опасность для окружающих, в том числе при донорстве крови и ее
                  компонентов;
                </p>
                <p>
                  3) по запросу органов дознания и предварительного следствия,
                  прокурора, адвоката и (или) суда в связи с проведением
                  расследования или судебного разбирательства;
                </p>
                <p>
                  4) при оказании медицинской помощи несовершеннолетнему или
                  недееспособному лицу для информирования его законных
                  представителей;
                </p>
                <p>
                  5) при наличии оснований полагать, что вред здоровью
                  гражданина причинен в результате противоправных деяний.
                </p>
                <p>
                  6) при обнаружении у гражданина психических отклонений и
                  склонностей к сексуальному насилию;
                </p>
                <p>
                  7) при проведении мониторинга договорных обязательств по
                  качеству и объему медицинских услуг.
                </p>
                <p>
                  4-1. Не является разглашением врачебной тайны передача на
                  хранение резервной копии электронного информационного ресурса
                  на единую платформу резервного хранения электронных
                  информационных ресурсов в соответствии с порядком и сроками,
                  определяемыми уполномоченным органом в сфере обеспечения
                  информационной безопасности, за исключением случаев, когда
                  такие электронные информационные ресурсы содержат информацию,
                  связанную с разведывательной, контрразведывательной
                  деятельностью и охранными мероприятиями по обеспечению
                  безопасности охраняемых лиц и объектов, передача которой
                  осуществляется в соответствии с законодательством Республики
                  Казахстан о государственных секретах.
                </p>
                <p>
                  5. Не допускаются без разрешения физических лиц (пациентов)
                  сбор и обработка персональных данных, касающихся их частной
                  жизни, для формирования электронных информационных ресурсов,
                  содержащих персональные данные физических лиц (пациентов),
                  кроме случаев, связанных с донорством крови и ее компонентов,
                  тканей, органов.
                </p>
                <p>
                  Не допускается подключение электронных информационных
                  ресурсов, содержащих персональные данные физических лиц
                  (пациентов), к сетям телекоммуникаций, связывающим их с
                  другими базами данных, без разрешения физических лиц
                  (пациентов) при использовании персональных данных, касающихся
                  их частной жизни, кроме случаев, связанных с донорством крови
                  и ее компонентов, тканей, органов, а также запросами
                  правоохранительных, специальных государственных и иных органов
                  по предоставлению информации в форме электронного документа,
                  составляющей врачебную тайну, о состоянии на диспансерных
                  учетах лиц, страдающих опасными для общества расстройствами и
                  заболеваниями, включая стойкие нарушения психики, алкогольную,
                  наркотическую и иные виды зависимости, склонность к совершению
                  агрессии и насильственных действий.
                </p>
                <p>
                  За незаконный сбор и обработку персональных данных, касающихся
                  частной жизни физических лиц (пациентов), должностные лица
                  несут ответственность, предусмотренную законами Республики
                  Казахстан.
                </p>
              </div>
            </div>
          </div>

          <div class="accordion-item child nested hidden">
            <p class="parent" @click="changeToActive">НАЛОГОВАЯ ТАЙНА</p>
            <div class="accordion-item child hidden">
              <div class="child text">
                <p>
                  <strong
                    > «О налогах и других обязательных платежах в
                    бюджет»</strong
                  >
                </p>
                <p>Статья 30. Налоговая тайна</p>
                <p>
                  1. Налоговую тайну составляют любые полученные налоговым
                  органом сведения о налогоплательщике (налоговом агенте), за
                  исключением сведений:
                </p>
                <p>
                  1) о сумме налогов и платежей в бюджет, уплаченных
                  (перечисленных) налогоплательщиком (налоговым агентом), за
                  исключением физических лиц;
                </p>
                <p>
                  2) о сумме возврата налогоплательщику из бюджета превышения
                  суммы налога на добавленную стоимость, относимого в зачет, над
                  суммой начисленного налога;
                </p>
                <p>
                  3) о сумме налоговой задолженности налогоплательщика
                  (налогового агента);
                </p>
                <p>4) о бездействующих налогоплательщиках;</p>
                <p>
                  5) подлежащих к размещению в базе данных на интернет-ресурсе
                  уполномоченного органа в случае, предусмотренном статьей 19
                  настоящего Кодекса;
                </p>
                <p>
                  6) о представлении налогоплательщиком налогового заявления о
                  проведении налоговой проверки в связи с ликвидацией
                  (прекращением деятельности);
                </p>
                <p>
                  7) о начисленной сумме налогов и платежей в бюджет
                  налогоплательщику (налоговому агенту), за исключением
                  физических лиц;
                </p>
                <p>
                  8) о начисленной сумме налога на имущество, земельного налога,
                  налога на транспортные средства физическим лицам;
                </p>
                <p>
                  9) о мерах ответственности, примененных в отношении
                  налогоплательщика (налогового агента), нарушившего налоговое
                  законодательство Республики Казахстан;
                </p>
                <p>
                  10) о наличии (отсутствии) регистрации в качестве
                  налогоплательщика нерезидента, осуществляющего деятельность
                  через постоянное учреждение, структурное подразделение или без
                  образования постоянного учреждения в соответствии со статьей
                  650 настоящего Кодекса;
                </p>
                <p>
                  11) о следующих регистрационных данных налогоплательщика
                  (налогового агента):
                </p>
                <p>идентификационный номер;</p>
                <p>
                  фамилия, имя, отчество (если оно указано в документе,
                  удостоверяющем личность) физического лица, руководителя
                  юридического лица;
                </p>
                <p>
                  наименование индивидуального предпринимателя, юридического
                  лица;
                </p>
                <p>
                  дата постановки на регистрационный учет, дата снятия с
                  регистрационного учета, причина снятия с регистрационного
                  учета налогоплательщика (налогового агента);
                </p>
                <p>вид деятельности;</p>
                <p>дата начала и окончания приостановления деятельности;</p>
                <p>резидентство налогоплательщика;</p>
                <p>
                  регистрационный номер контрольно-кассовой машины в налоговом
                  органе;
                </p>
                <p>место использования контрольно-кассовой машины;</p>
                <p>применяемый налоговый режим;</p>
                <p>
                  11-1) о полугодовом графике налоговых проверок, проводимых по
                  особому порядку на основе оценки степени риска;
                </p>
                <p>
                  12) о непредставлении налогоплательщиком (налоговым агентом)
                  налоговой отчетности;
                </p>
                <p>
                  13) не являющихся конфиденциальной информацией в соответствии
                  с законодательством Республики Казахстан о реабилитации и
                  банкротстве;
                </p>
                <p>
                  14) о коэффициенте налоговой нагрузки налогоплательщика
                  (налогового агента), рассчитываемом в порядке, определенном
                  уполномоченным органом, за исключением физических лиц, не
                  зарегистрированных в налоговых органах в качестве
                  индивидуальных предпринимателей и не занимающихся частной
                  практикой;
                </p>
                <p>
                  Подпункты 15 - 17 вводятся в действие с 1 января 2021 года
                </p>
                <p>
                  15) об индивидуальном идентификационном номере физического
                  лица, представившего декларации физических лиц;
                </p>
                <p>
                  16) о наличии (отсутствии) в декларации о доходах и имуществе
                  требования по возврату излишне уплаченной суммы
                  индивидуального подоходного налога;
                </p>
                <p>
                  17) подлежащих опубликованию в соответствии с Законом
                  Республики Казахстан  «О противодействии
                  коррупции»;
                </p>
                <p>
                  18) о результатах категорирования налогоплательщиков в
                  зависимости от степени риска.
                </p>
                <p>
                  2. Сведения о налогоплательщике (налоговом агенте), являющиеся
                  налоговой тайной, не могут быть представлены налоговыми
                  органами другому лицу без письменного разрешения
                  налогоплательщика (налогового агента), если иное не
                  установлено настоящей статьей.
                </p>
                <p>
                  3. Налоговые органы представляют сведения о налогоплательщике
                  (налоговом агенте), составляющие налоговую тайну, без
                  получения письменного разрешения налогоплательщика (налогового
                  агента) в следующих случаях:
                </p>
                <p>
                  1) правоохранительным и специальным государственным органам в
                  пределах их компетенции, установленной законодательством
                  Республики Казахстан, на основании мотивированного запроса на
                  бумажном носителе либо в форме электронного документа,
                  санкционированного прокурором. Санкция не требуется в случае
                  запрашивания таких сведений прокурором;
                </p>
                <p>
                  2) суду в ходе судопроизводства по делам, связанным с
                  исчислением, удержанием и перечислением налогов в порядке,
                  определенном настоящим Кодексом;
                </p>
                <p>
                  3) судебному исполнителю в пределах его компетенции,
                  установленной законодательством Республики Казахстан, по
                  находящимся в его производстве делам исполнительного
                  производства на основании постановления, заверенного печатью
                  частного судебного исполнителя либо территориального отдела;
                </p>
                <p>
                  4) центральным государственным органам Республики Казахстан в
                  области государственного планирования, государственной
                  статистики, регулирования торговой деятельности,
                  внешнеторговой деятельности, охраны окружающей среды, в сфере
                  социальной защиты населения, уполномоченному органу внешнего
                  государственного аудита и финансового контроля,
                  антимонопольному органу и уполномоченному органу в сфере
                  взаимодействия с неправительственными организациями в случаях,
                  предусмотренных настоящим Кодексом и (или) законами Республики
                  Казахстан.
                </p>
                <p>
                  Государственные органы Республики Казахстан, указанные в
                  настоящем подпункте, утверждают перечень должностных лиц,
                  имеющих доступ к сведениям, составляющим налоговую тайну.
                </p>
                <p>
                  Порядок и перечень представляемых сведений, составляющих
                  налоговую тайну, устанавливаются совместными актами с
                  уполномоченным органом;
                </p>
                <p>
                  5) центральному уполномоченному государственному органу по
                  государственному планированию, уполномоченному
                  государственному органу, осуществляющему финансовый мониторинг
                  и принимающему иные меры по противодействию легализации
                  (отмыванию) доходов, и уполномоченному органу по внутреннему
                  государственному аудиту в случаях, предусмотренных законами
                  Республики Казахстан.
                </p>
                <p>
                  Уполномоченные государственные органы, указанные в настоящем
                  подпункте, утверждают перечень должностных лиц, имеющих доступ
                  к сведениям, составляющим налоговую тайну;
                </p>
                <p>
                  6) лицу, привлеченному к проведению налоговой проверки в
                  качестве специалиста;
                </p>
                <p>
                  7) налоговым или правоохранительным органам других государств,
                  международным организациям в соответствии с международными
                  договорами (соглашениями) о взаимном сотрудничестве между
                  налоговыми или правоохранительными органами, одной из сторон
                  которых является Республика Казахстан, а также договорами,
                  заключенными Республикой Казахстан с международными
                  организациями;
                </p>
                <p>
                  8) Государственной корпорации  «Правительство для
                  граждан» и государственным органам в части сведений,
                  необходимых для оказания государственных услуг;
                </p>
                <p>
                  9) местным исполнительным органам, органам местного
                  самоуправления в части сведений по физическим лицам по налогу
                  на имущество, земельному налогу, налогу на транспортные
                  средства, а также по плате за размещение наружной (визуальной)
                  рекламы и индивидуальному подоходному налогу по доходам,
                  подлежащим налогообложению физическим лицом самостоятельно.
                </p>
                <p>
                  Органы, указанные в настоящем подпункте, утверждают перечень
                  должностных лиц, имеющих доступ к сведениям, составляющим
                  налоговую тайну;
                </p>
                <p>
                  10) государственным органам и (или) лицам, которым законами
                  Республики Казахстан предусмотрено предоставление сведений об
                  отсутствии (наличии) задолженности, учет по которым ведется в
                  налоговых органах;
                </p>
                <p>
                  11) Национальному Банку Республики Казахстан в части сведений,
                  необходимых для осуществления валютного контроля и последующей
                  их передачи уполномоченным банкам, являющимся агентами
                  валютного контроля.
                </p>
                <p>
                  Перечень и порядок представления сведений, составляющих
                  налоговую тайну, определяется совместным актом Национального
                  Банка Республики Казахстан и уполномоченного органа;
                </p>
                <p>
                  12) членам апелляционной комиссии при рассмотрении жалобы
                  налогоплательщика (налогового агента) на уведомление о
                  результатах проверки;
                </p>
                <p>
                  13) структурному подразделению уполномоченного органа,
                  осуществляющему рассмотрение жалоб на уведомление о
                  результатах проверки и (или) уведомление об устранении
                  нарушений, в части сведений, необходимых при рассмотрении
                  жалоб налогоплательщиков (налоговых агентов) на уведомление о
                  результатах проверки;
                </p>
                <p>
                  14) уполномоченным государственным органам в части сведений о
                  представленных декларациях о доходах и имуществе, с указанием
                  даты представления и кода налогового органа, лицами, на
                  которых Законом Республики Казахстан  «О противодействии
                  коррупции» возложена такая обязанность.
                </p>
                <p>
                  Порядок представления указанных сведений определяется
                  уполномоченным органом.
                </p>
                <p>
                  4. Нормы пункта 3 настоящей статьи не распространяются на
                  сведения и информацию о налогоплательщике, полученные
                  налоговыми органами в процессе проведения легализации в
                  соответствии с Законом Республики Казахстан  «Об амнистии
                  граждан Республики Казахстан, оралманов и лиц, имеющих вид на
                  жительство в Республике Казахстан, в связи с легализацией ими
                  имущества».
                </p>
                <p>
                  5. Налоговая тайна не подлежит разглашению лицами, имеющими
                  доступ к налоговой тайне, как в период исполнения ими своих
                  обязанностей, так и после завершения их исполнения.
                </p>
                <p>
                  6. Утрата документов, содержащих сведения, составляющие
                  налоговую тайну, либо разглашение таких сведений влечет
                  ответственность, установленную законами Республики Казахстан.
                </p>
                <p>7. Не является разглашением налоговой тайны:</p>
                <p>
                  1) передача на хранение резервной копии электронного
                  информационного ресурса на единую платформу резервного
                  хранения электронных информационных ресурсов.
                </p>
                <p>
                  При этом использование таких данных, переданных на хранение,
                  осуществляется только уполномоченным органом.
                </p>
                <p>
                  Передача и хранение резервной копии электронного
                  информационного ресурса осуществляются в соответствии с
                  порядком и сроками, определяемыми уполномоченными органами в
                  сфере обеспечения информационной безопасности и по
                  национальной безопасности по согласованию с уполномоченным
                  органом;
                </p>
                <p>
                  2) передача сведений, полученных центральным государственным
                  органом в области охраны окружающей среды для осуществления
                  контроля за правильностью исчисления, полнотой и
                  своевременностью перечисления производителями (импортерами)
                  платы за организацию сбора, транспортировки, переработки,
                  обезвреживания, использования и (или) утилизации отходов, в
                  порядке, установленном законодательством Республики Казахстан.
                </p>
                <p lang="ru-RU">&nbsp;</p>
              </div>
            </div>
          </div>

          <div class="accordion-item child nested hidden">
            <p class="parent" @click="changeToActive">НОТАРИАЛЬНАЯ ТАЙНА</p>
            <div class="accordion-item child hidden">
              <div class="child text">
                <p><strong>О нотариате</strong></p>
                <p>
                  3. Физическим и юридическим лицам гарантируется тайна
                  совершенных ими нотариальных действий.
                </p>
                <p>
                  Нотариус, занимающийся частной практикой, обязан иметь одно
                  помещение, пригодное для беспрепятственного доступа физических
                  лиц и представителей юридических лиц, соблюдения тайны
                  совершения нотариальных действий и условий для обеспечения
                  сохранности нотариального делопроизводства.
                </p>
              </div>
            </div>
          </div>

          <div class="accordion-item child nested hidden">
            <p class="parent" @click="changeToActive">АУДИТОРСКАЯ ТАЙНА</p>
            <div class="accordion-item child hidden">
              <div class="child text">
                <p>
                  <strong
                    >Закон  «Об аудиторской деятельности»</strong
                  >
                </p>
                <p>
                  4. Сведения, полученные аудитором и (или) аудиторской
                  организацией при исполнении договора на проведение аудита,
                  аудита по налогам, составляют коммерческую тайну, за
                  исключением сведений, представляемых органам государственных
                  доходов.
                </p>
                <p>
                  Сведения, полученные аудитором и (или) аудиторской
                  организацией при исполнении договора на проведение аудита иной
                  информации, составляют коммерческую тайну, за исключением
                  сведений, предоставляемых с согласия клиентов аудиторской
                  организации Национальному Банку.
                </p>
                <p>
                  Нарушение обязанности сохранения сведений, содержащих
                  коммерческую тайну, влечет ответственность, установленную
                  законами Республики Казахстан.
                </p>
              </div>
            </div>
          </div>

          <div class="accordion-item child nested hidden">
            <p class="parent" @click="changeToActive">ЖУРНАЛИСТСКАЯ ТАЙНА</p>
            <div class="accordion-item child hidden">
              <div class="child text">
                <p><strong>Закон о средствах массовой информации</strong></p>
                <p><strong>Статья 20. Права журналиста</strong></p>
                <p>
                  10) на сохранение тайны авторства и источников информации, за
                  исключением случаев, когда эти тайны обнародуются по
                  требованию суда.
                </p>
              </div>
            </div>
