        <div class="accordion-item child nested hidden">
          <p class="parent" @click="changeToActive">РЕКЛАМА НА ТВ И РАДИО</p>
          <div class="accordion-item child hidden">
            <div class="child text">
              <p>
                <strong>Объем рекламы в общем объеме вещания</strong> &ndash; не
                более 20%. Если ваш канал или радио является
                информационно-аналитическим, деловым, детским или научным, то
                общий объем рекламы не должен превышать 20% от общего объема
                вещания в сутки. В этот объем не засчитывается бегущая строка,
                социальная реклама, информация о собственной продукции теле-,
                радиоканалов (анонсы), не содержащей рекламы третьих лиц,
                объявления о мероприятиях, подготовленных и проводимых теле-,
                радиоканалом, а также реклама, размещаемой в месте события,
                транслируемого в прямом эфире или записи повтора прямого эфира.
              </p>
              <p>
                <strong>Объем рекламы в прайм-тайм</strong> &ndash; не более
                20%. В общем объеме вещания рекламы в сутки общая
                продолжительность рекламы в период времени с 18 до 23 часов
                местного времени в течение часа времени вещания должна
                составлять не более 20%.
              </p>
              <p>
                <strong>Объем рекламы на казахском языке</strong> &ndash; не
                менее 50%. Вся реклама на телевидении и радио должна быть
                дублирована на государственном языке в равной пропорции. При
                этом она должна быть равномерна распределена в течение всего
                эфира в шестичасовых интервалах, и ее объем не должен быть менее
                суммарного объема рекламы, распространяемой на других языках.
              </p>
              <p>
                <strong>Объем телеторговли</strong> &ndash; начиная с 30% от
                общего объема рекламы в сутки для телеканалов, не
                специализирующихся на сообщениях и информации рекламного
                характера. Объем телеторговли на теле-, радиоканалах,
                специализирующихся исключительно на сообщениях рекламного
                характера, не ограничен.
              </p>
              <p>
                <strong>Объем социальной рекламы</strong> - не менее 10 выходов
                в день (в эфире обязательных телеканалов), равномерно по всей
                программной сетке канала на казахском и русском языках.
                Дополнительные условия по отбору социальной рекламы содержатся в
                специальных требованиях.
              </p>
              <p>
                <strong>Размер  «бегущей строки»</strong> - не более
                7,5% площади кадра.  «Бегущая строка» не должна
                мешать зрителям воспринимать визуальный ряд или информацию в
                программе.
              </p>
              <p>
                <strong>Звук рекламы</strong> - не должен быть громче звука
                транслируемой программы.
              </p>
              <p>
                <strong
                  >В случае объявления национального траура реклама на теле-,
                  радиоканалах запрещается.</strong
                >
              </p>
