        <div class="accordion-item child nested hidden">
          <p class="parent" @click="changeToActive">НЕНАДЛЕЖАЩАЯ РЕКЛАМА</p>
          <div class="accordion-item child hidden">
            <div class="child text">
              <p>
                <strong>Недобросовестная</strong> &ndash; реклама, которая
                содержит сравнение рекламируемых товаров и услуг с аналогичными
                и, как правило, это сравнение не в пользу последних. Это также
                реклама, которая высмеивает или дискредитирует тех, кто не
                пользуется рекламируемыми товарами и услугами или использует
                очень похожие на другие товарные знаки, наименования, фирменную
                упаковку и т.д. Эта реклама, которая вводит в заблуждение
                потребителя, другими словами.
              </p>
              <p>
                <strong>Недостоверная</strong> &ndash; реклама с искаженными
                (умышленно или неумышленно), то есть несоответствующими
                действительности фактами, характеристиками, сведениям о товарах
                и услугах, гарантийных сроках, предполагаемыми результатами
                применения, статистическая информация, результаты опросов и т.д.
              </p>
              <p>
                <strong>Неэтичная</strong> &ndash; реклама, которая содержит
                оскорбительную лексику или информацию, порочащую других лиц,
                религиозные символы, валюту, символы, объекты культуры,
                искусства и памятники истории.
              </p>
              <p>
                <strong>Заведомо ложная</strong> - реклама, с помощью которой
                рекламодатель умышленно вводит в заблуждение потребителя
                рекламы.
              </p>
              <p>
                <strong>Скрытая</strong> - реклама, которая оказывает не
                осознаваемое потребителем воздействие на его восприятие,
                инстинкты в радио-, теле-, видео-, аудио- и кинопродукции, а
                также в иной продукции, в том числе путем использования
                специальных видеовставок, двойной звукозаписи и иными способами.
              </p>
              <p>
                Реклама должна быть легко распознаваемой без специальных знаний
                или применения специальных средств непосредственно в момент ее
                представления и быть отличимой от других видов информации.
              </p>
            </div>
          </div>
        </div>

        <div class="accordion-item child nested hidden">
