                и других регуляций.
              </p>
              <p>
                В социальной рекламе не допускается упоминание о физических и
                юридических лицах, кроме упоминания о государственных органах,
                органах местного самоуправления, спонсорах, физических лицах,
                оказавшихся в трудной жизненной ситуации или нуждающихся в
                лечении, в целях оказания им благотворительной помощи.
              </p>
            </div>
          </div>
        </div>

        <div class="accordion-item child nested hidden">
