        <div class="accordion-item child nested hidden">
          <p class="parent" @click="changeToActive">СПЕЦИАЛЬНЫЕ ОГРАНИЧЕНИЯ</p>
          <div class="accordion-item child hidden">
            <div class="child text">
              <p>
                <strong
                  >Реклама лекарственных средств, биологически активных
                  препаратов, медицинской техники</strong
                >
              </p>
              <p>
                Лекарственные средства и БАДы должны быть зарегистрированы и
                разрешены к применению на территории РК. Для рекламы
                лекарственных средств и медицинских изделий нужна проверка на
                соответствие рекламы требованиям законодательства, то есть
                специальное разрешение. Такую проверку проводят экспертные
                организации в сфере здравоохранения, и ее должны инициировать не
                редакции СМИ, а рекламодатели, предоставив рекламные материалы
                для получения разрешения для дальнейшего распространения в СМИ.
              </p>
              <p><strong>Реклама традиционной медицины</strong></p>
              <p>
                К традиционной медицине относится относятся гомеопатия,
                гирудотерапия, мануальная терапия, рефлексотерапия, фитотерапия
                и лечение средствами природного происхождения. Требуется
                указание номера, дата выдачи и орган, который выдал лицензии на
                осуществление этих видов деятельности.
              </p>
              <p><strong>Народная медицина (целительство)</strong></p>
              <p>
                Это совокупность накопленных народом эмпирических сведений о
                целительных средствах, а также лечебных и гигиенических приемов
                и навыков и их практическое применение для сохранения здоровья,
                предупреждения и лечения болезней. Это реклама гадалок, народных
                целителей и т.д. Если народный целитель имеет лицензию, вы
                должны указать номер и дату ее выдачи. Запрещено проведение
                сеансов массового целительства через СМИ.
              </p>
              <p>
                <strong
                  >Реклама финансовых, банковских, инвестиционных и страховых
                  услуг
                </strong>
              </p>
              <p>
                В такой рекламе запрещено скрывать любое существенное условие,
                предусмотренное договорами, гарантировать получение дохода и
                указывать размеры дивидендов, рекламировать ценные бумаги без
                регистрации эмиссии.
              </p>
              <p>
                Нельзя распространять рекламу видов предпринимательской
                деятельности, которая призывает физических лиц получить свою
                прибыль или выгоду за счет вовлечения других в деятельность
                данного предприятия или покупку товаров данного предприятия.
                Сложность для СМИ в том, чтобы под видом рекламы
                потребительского кооператива, который  «раздает»
                своим вкладчикам квартиры, разглядеть потенциально опасную и
                мошенническую схему.
              </p>
              <p><strong>Реклама игровых заведений</strong></p>
              <p>
                В Казахстане разрешена деятельность казино, залов игровых
                автоматов, букмекерских контор, тотализаторов. Их деятельность
                лицензируется, поэтому нужно указать номер, дату выдачи лицензии
                и орган, выдавший такую лицензию.
              </p>
              <p>Запрещены следующие формы игорного бизнеса (и их реклама):</p>
              <p>электронное казино и интернет-казино;</p>
              <p>
                организация и проведение азартных игр и (или) пари,
                предусматривающих прием ставок и (или) выдачу выигрыша в виде
                иного имущества, кроме денег, за исключением деятельности
                казино, где допускается выдача выигрыша в виде иного имущества;
              </p>
              <p>
                установка и использование игорного оборудования в
                предпринимательских целях, в местах, которые не предусмотрены
                законом,
              </p>
              <p>
                заключение пари, прием (учет) ставок, выплата выигрыша вне
                игорных заведений;
              </p>
              <p>
                заключение пари, прием (учет) ставок, выплата выигрыша лицами,
                не являющимися организаторами игорного бизнеса, осуществляющими
                деятельность тотализатора или букмекерской конторы.
              </p>
              <p>
                <strong
                  >Реклама строящегося жилья и коммерческой недвижимости</strong
                >
              </p>
              <p>
                Если речь идет о рекламе строящегося жилья и коммерческой
                недвижимости без цели привлечения средств дольщиков, то здесь
                нужно помнить, что Закон РК  «О рекламе» запрещает
                рекламу жилого дома, не соответствующего классификации жилых
                домов в утвержденной проектной документации. То есть
                рекламодатель должен предоставить вам документы, подтверждающие
                такое соответствие.
              </p>
              <p>
                Если реклама адресована потенциальным дольщикам и связана с
                привлечением их средств для завершения строительства, то
                необходимо, чтобы рекламодатель имел разрешение на привлечение
                средств дольщиков. Выдается оно местным исполнительным органом.
              </p>
              <p>
                Без наличия такого разрешения реклама проекта строительства
                жилого дома тоже возможна при следующих условиях:
              </p>
              <p>
                в рекламе нужно обязательно указать, что договоры о долевом
                участии в жилищном строительстве будут заключаться только после
                заключения договора о предоставлении гарантии с Фондом
                гарантирования или выдачи разрешения на привлечение денег
                дольщиков местным исполнительным органом.
              </p>
              <p>
                в рекламе должна содержаться информация о застройщике и
                уполномоченной компании.
              </p>
              <p><strong>Реклама о вакансиях</strong></p>
              <p>
                Запрещается размещать в СМИ информацию о вакансиях для приема на
                работу, содержащую требования дискриминационного характера (по
                мотивам происхождения, социального, должностного и
                имущественного положения, пола, расы, национальности, языка,
                отношения к религии, убеждений, места жительства, возраста или
                физических недостатков, а также принадлежности к общественным
                объединениям).
              </p>
            </div>
          </div>
        </div>

        <div class="accordion-item child nested hidden">
          <p class="parent" @click="changeToActive">ДЕТИ И ЖЕНЩИНЫ В РЕКЛАМЕ</p>
          <div class="accordion-item child hidden">
            <div class="child text">
              <p>В рекламе не допускается:</p>
              <p>
                дискредитация авторитета родителей, воспитателей, педагогов,
                подрыв доверия к ним несовершеннолетних;
              </p>
              <p>
                прямое предложение, призывающее убедить родителей или других лиц
                приобрести рекламируемую продукцию;
              </p>
              <p>
                прямое указание несовершеннолетним, что обладание той или иной
                продукцией дает им какое-либо преимущество над другими, а также
                что отсутствие такой продукции приведет к обратному;
              </p>
              <p>
                размещение в рекламе текстовой, визуальной или звуковой
                информации, показывающей несовершеннолетних в опасных местах и
                ситуациях, когда это не оправдано профилактическими целями;
              </p>
              <p>
                визуальное или звуковое использование образов несовершеннолетних
                в рекламе, не относящейся непосредственно к товарам (работам,
                услугам) для них, за исключением социальной рекламы;
              </p>
              <p>
                приуменьшение необходимого уровня навыков использования
                продукции у несовершеннолетних, за исключением случаев, когда
                результаты использования продукции показаны или описаны;
              </p>
              <p>
                создание у несовершеннолетних искаженного представления о
                стоимости продукции, а также прямое или косвенное указание на
                то, что рекламируемая продукция доступна для любого семейного
                бюджета.
              </p>
            </div>
          </div>
        </div>

        <div class="accordion-item child nested hidden">
          <p class="parent" @click="changeToActive">
            ОТВЕТСТВЕННОСТЬ СМИ И СПОРНЫЕ МОМЕНТЫ
          </p>
          <div class="accordion-item child hidden">
            <div class="child text">
              <p>
                Рекламораспространитель несет ответственность за нарушение
                требований в отношении времени, места и способа распространения,
                размещения рекламы. Рекламопроизводитель несет ответственность
                за нарушение положений законодательства о рекламе в отношении
                оформления или производства рекламы. За соответствие других
                требований по рекламе (язык, содержание) несет ответственность
                рекламодатель.
              </p>
              <p>
                СМИ вправе запрашивать дополнительную информацию у рекламодателя
                (лицензии, различные разрешения, сертификаты соответствия и
                т.д.), чтобы защитить себя от возможных претензий и обвинений в
                нарушении законодательства. Если запрашиваемая информация не
                предоставлена, СМИ вправе расторгнуть рекламный договор.
              </p>
              <p>
                Реклама может полностью или частично являться объектом
                авторского права и смежных прав. Поэтому в случае незаконного
                копирования вашей рекламы вы можете обращаться в суд (Закон РК
                 «Об авторском праве и смежных правах»).
              </p>
              <p>
                В случае, если суд или компетентный уполномоченный орган
                установит факт ненадлежащей рекламы, то лицо, допустившее
                нарушение, обязано немедленно прекратить распространение,
                размещение такой рекламы и сделать опровержение. Все расходы по
                опровержению несет лицо, допустившее нарушение.
              </p>
            </div>
          </div>
        </div>

        <div class="accordion-item child nested hidden">
          <p class="parent" @click="changeToActive">МОНИТОРИНГ РЕКЛАМЫ В СМИ</p>
          <div class="accordion-item child hidden">
            <div class="child text">
              <p>
                Контроль за исполнением требований Закона о рекламе ведет
                государственный уполномоченный орган (Министерство информации и
                общественного развития РК). Мониторинг рекламной деятельности
                СМИ проводится по единым правилам и единой методике и в большей
                мере затрагивает телерадиоканалы.
              </p>
              <p><strong>Методика выявляет:</strong></p>
              <p>
                равномерное распределение рекламы на казахском языке на теле-,
                радиоканалах;
              </p>
              <p>объем рекламы на теле-, радиоканалах в сутках;</p>
              <p>
                объем общей продолжительности рекламы в период времени с 18 до
                23 часов местного времени в течение часа времени вещания на
                теле-, радиоканалах;
              </p>
              <p>
                объем телеторговли на телеканалах, не специализирующихся на
                сообщениях и материалах рекламного характера;
              </p>
              <p>
                площадь дополнительной информации, носящей характер коммерческой
                рекламы, рекламы в виде наложения и бегущей строки от площади
                экрана на телеканалах.
              </p>
              <p><strong>Процедура:</strong></p>
              <p>
                государственный орган направляет в редакцию СМИ рекомендацию об
                устранении нарушения в течение пяти рабочих дней со дня
                выявления нарушения;
              </p>
              <p>
                редакция СМИ рассматривает рекомендацию в течение десяти рабочих
                дней со дня, следующего за днем ее вручения.
              </p>
              <p>
                в случае, если редакция СМИ согласна с фактом выявленного в ходе
                мониторинга нарушения, то она устраняет его.
              </p>
              <p>
                в случае несогласия с нарушениями редакция вправе направить в
                уполномоченный орган возражение в течение пяти рабочих дней со
                дня, следующего за днем вручения рекомендации.
              </p>
            </div>
          </div>
        </div>

        <div class="accordion-item child nested hidden">
          <p class="parent" @click="changeToActive">ЗАКОНОДАТЕЛЬНАЯ БАЗА</p>

          <div class="accordion-item child nested hidden">
            <p class="parent" @click="changeToActive">ТРУДОВОЙ КОДЕКС РК</p>
            <div class="accordion-item child hidden">
              <div class="child text">
                <p>Статья 6. Запрещение дискриминации в сфере труда</p>
                <p>
                  1. Каждый имеет равные возможности в реализации своих прав и
                  свобод в сфере труда. Никто не может быть ограничен в правах в
                  сфере труда, кроме случаев и в порядке, которые предусмотрены
                  настоящим Кодексом и иными законами Республики Казахстан.
                </p>
                <p>
                  2. Никто не может подвергаться какой-либо дискриминации при
                  реализации трудовых прав по мотивам происхождения,
                  социального, должностного и имущественного положения, пола,
                  расы, национальности, языка, отношения к религии, убеждений,
                  места жительства, возраста или физических недостатков, а также
                  принадлежности к общественным объединениям.
                </p>
                <p>
                  3. Не являются дискриминацией различия, исключения,
                  предпочтения и ограничения, которые в соответствии с законами
                  Республики Казахстан устанавливаются для соответствующих видов
                  трудовой деятельности либо обусловлены особой заботой
                  государства о лицах, нуждающихся в повышенной социальной и
                  правовой защите.
                </p>
                <p>
                  4. Лица, считающие, что они подверглись дискриминации в сфере
                  труда, вправе обратиться в суд или иные инстанции в порядке,
                  установленном законами Республики Казахстан.
                </p>
              </div>
            </div>
          </div>

          <div class="accordion-item child nested hidden">
            <p class="parent" @click="changeToActive">ЗАКОН О РЕКЛАМЕ РК</p>
            <div class="accordion-item child hidden">
              <div class="child text">
                <p>Статья 3. Основные понятия</p>
                <p>
                  В настоящем Законе используются следующие основные понятия:
                </p>
                <p>
                  1) социальная реклама &ndash; информация, адресованная
                  неопределенному кругу лиц и направленная на достижение
                  благотворительных и иных общественно полезных целей
                  некоммерческого характера, а также обеспечение интересов
                  государства;
                </p>
                <p>
                  1-1) средство индивидуализации &ndash; обозначение, служащее
                  для различия товаров (работ, услуг), участников гражданского
                  оборота (фирменное наименование, товарный знак, знак
                  обслуживания и другое);
                </p>
                <p>
                  1-2) реклама &ndash; информация, распространяемая и (или)
                  размещаемая в любой форме с помощью любых средств,
                  предназначенная для неопределенного круга лиц и призванная
                  формировать или поддерживать интерес к физическому или
                  юридическому лицу, товарам, товарным знакам, работам, услугам
                  и способствовать их реализации;
                </p>
                <p>
                  2) ненадлежащая реклама - недобросовестная, недостоверная,
                  неэтичная, заведомо ложная и скрытая реклама, в которой
                  допущены нарушения требований к ее содержанию, времени, месту
                  и способу распространения, размещения, установленных
                  законодательством Республики Казахстан;
                </p>
                <p>
                  3) рекламодатель - физическое или юридическое лицо, являющееся
                  источником рекламной информации для производства,
                  распространения и размещения рекламы;
                </p>
                <p>
                  4) рекламопроизводитель - физическое или юридическое лицо,
                  осуществляющее приведение рекламной информации к готовой для
                  распространения и размещения форме;
                </p>
                <p>
                  4-1) регулирующие государственные органы в области рекламы
                  &ndash; государственные органы, осуществляющие руководство в
                  отдельной отрасли, в которой осуществляется рекламная
                  деятельность;
                </p>
                <p>
                  4-2) уполномоченный орган в области рекламы &ndash;
                  центральный исполнительный орган, осуществляющий руководство и
                  межотраслевую координацию в области рекламы;
                </p>
                <p>
                  5) рекламораспространитель - физическое или юридическое лицо,
                  осуществляющее распространение и размещение рекламной
                  информации путем предоставления и (или) использования
                  имущества, в том числе технических средств телерадиовещания и
                  иными способами;
                </p>
                <p>
                  6) потребители рекламы - неопределенный круг физических и
                  (или) юридических лиц, которым предназначается реклама;
                </p>
                <p>
                  7) бегущая строка &ndash; способ распространения рекламы,
                  размещаемой на объектах наружной (визуальной) рекламы или
                  передающейся по телеканалам, в кино- и видеообслуживании,
                  который характеризуется последовательным чередованием
                  (передвижением) сочетаний букв, цифр, знаков, составляющих в
                  совокупности определенную информацию;
                </p>
                <p>
                  11) телеторговля &ndash; публичное предложение на телеканале с
                  целью реализации товаров или оказания услуг.
                </p>
                <p>Статья 5. Авторское право и смежные права на рекламу</p>
                <p>
                  Реклама может полностью или частично являться объектом
                  авторского права и смежных прав. Авторское право и смежные
                  права подлежат защите в соответствии с законодательством
                  Республики Казахстан, а также международными договорами.
                </p>
                <p>Глава 2. Общие и специальные требования к рекламе</p>
                <p>Статья 6. Общие требования к рекламе</p>
                <p>
                  1. Реклама независимо от формы или используемого средства
                  распространения, размещения, включая сферу электронной
                  коммерции, должна быть достоверной, распознаваемой без
                  специальных знаний или применения специальных средств
                  непосредственно в момент ее представления.
                </p>
                <p>
                  2. Реклама на территории Республики Казахстан (за исключением
                  периодических печатных изданий, интернет-ресурсов,
                  информационных агентств) распространяется на казахском и
                  русском языках, а также по усмотрению рекламодателя и на
                  других языках.
                </p>
                <p>
                  Перевод содержания рекламы не должен искажать ее основной
                  смысл.
                </p>
                <p>
                  4. Не допускается реклама товаров (работ, услуг), запрещенных
                  к производству и реализации в соответствии с законодательством
                  Республики Казахстан.
                </p>
                <p>
                  Если деятельность, осуществляемая рекламодателем, подлежит
                  лицензированию, то при рекламе соответствующего товара (работ,
                  услуг), а также при рекламе самого рекламодателя необходимо
                  указывать номер лицензии и наименование органа, выдавшего
                  лицензию, кроме рекламы на радио.
                </p>
                <p>
                  4-1. В рекламе товара (работы, услуги), реализуемого на
                  территории Республики Казахстан, указание цены (тарифов,
                  расценок, ставок) осуществляется в тенге.
                </p>
                <p>
                  5. Запрещается реклама не зарегистрированных в соответствии с
                  законодательством Республики Казахстан религиозных объединений
                  и духовных (религиозных) организаций образования.
                </p>
                <p>
                  5-1. Запрещается реклама электронного казино и
                  интернет-казино.
                </p>
                <p>
                  6. Реклама не должна использоваться для пропаганды или
                  агитации насильственного изменения конституционного строя,
                  нарушения целостности Республики Казахстан, подрыва
                  безопасности государства, войны, социального, расового,
                  национального, религиозного, сословного и родового
                  превосходства, культа жестокости и насилия, порнографии, а
                  также распространения сведений, составляющих государственные
                  секреты Республики Казахстан и иные охраняемые законом тайны.
                </p>
                <p>
                  7. Реклама не должна возбуждать панику в обществе, побуждать
                  физических лиц к агрессии, а также к иному противоправному
                  действию (бездействию).
                </p>
                <p>
                  9. В социальной рекламе не допускается упоминание о средствах
                  индивидуализации, о физических и юридических лицах, за
                  исключением случаев, предусмотренных настоящим пунктом.
                </p>
                <p>
                  Установленные ограничения не распространяются на упоминания о
                  государственных органах, органах местного самоуправления,
                  спонсорах, физических лицах, оказавшихся в трудной жизненной
                  ситуации или нуждающихся в лечении, в целях оказания им
                  благотворительной помощи. В социальной рекламе допускается
                  упоминание о социально ориентированных некоммерческих
                  организациях в случаях, если содержание этой рекламы
                  непосредственно связано с информацией о деятельности таких
                  некоммерческих организаций, направленной на достижение
                  благотворительных или иных общественно полезных целей.
                </p>
                <p>
                  В социальной рекламе, распространяемой в радиопрограммах,
                  продолжительность упоминания о спонсорах не может превышать
                  три секунды о каждом, в социальной рекламе, распространяемой в
                  телепрограммах, при кино- и видеообслуживании, &ndash; три
                  секунды о каждом, и такому упоминанию должно быть отведено не
                  более чем семь процентов площади кадра, а в социальной
                  рекламе, распространяемой другими способами, &ndash; не более
                  чем пять процентов рекламной площади (пространства). Указанные
                  ограничения не распространяются на упоминания в социальной
                  рекламе о физических лицах, оказавшихся в трудной жизненной
                  ситуации или нуждающихся в лечении, в целях оказания им
                  благотворительной помощи.
                </p>
                <p>
                  10. Ограничения на рекламу устанавливаются настоящим Законом и
                  другими законами Республики Казахстан.
                </p>
                <p>Статья 7. Виды ненадлежащей рекламы</p>
                <p>1. Недобросовестной является реклама, которая:</p>
                <p>
                  1) содержит сравнение рекламируемых товаров (работ, услуг) с
                  товарами (работами, услугами) других физических или
                  юридических лиц, а также высказывания, образы, порочащие их
                  честь, достоинство и деловую репутацию;
                </p>
                <p>
                  2) вводит потребителей в заблуждение относительно
                  рекламируемой продукции посредством копирования фирменного
                  наименования, товарного знака, фирменной упаковки, внешнего
                  оформления товара, формул, изображений и другого коммерческого
                  обозначения, используемых в рекламе другой продукции, либо
                  посредством злоупотребления их доверием;
                </p>
                <p>
                  3) содержит указания или утверждения, использование которых
                  при осуществлении предпринимательской деятельности может
                  ввести в заблуждение относительно характера, способа
                  изготовления, свойств, пригодности к применению или количества
                  товара (работ, услуг);
                </p>
                <p>
                  4) дискредитирует, унижает или высмеивает физические или
                  юридические лица, не пользующиеся рекламируемыми товарами
                  (работами, услугами);
                </p>
                <p>
                  5) представляет собой рекламу товаров, запрещенных к рекламе
                  законом Республики Казахстан, если такая реклама
                  осуществляется под видом рекламы другого товара, товарный знак
                  или знак обслуживания которого тождествен или сходен до
                  степени смешения с товарным знаком или знаком обслуживания
                  товара, запрещенного к рекламе, а также под видом рекламы
                  изготовителя или продавца такого товара.
                </p>
                <p>
                  2. Недостоверной является реклама, в которой присутствуют не
                  соответствующие действительности сведения в отношении:
                </p>
                <p>
                  1) таких характеристик продукции, как природа, состав, способ
                  и дата изготовления, назначение, потребительские свойства,
                  условия использования, наличие сертификата соответствия,
                  сертификационных знаков и знаков соответствия национальным
                  стандартам, количество, происхождение;
                </p>
                <p>
                  2) наличия продукции на рынке, возможности ее приобретения в
                  определенном месте;
                </p>
                <p>
                  3) стоимости (цены) продукции и дополнительных условий оплаты
                  на момент распространения и размещения рекламы;
                </p>
                <p>
                  4) доставки, обмена, возврата, ремонта и обслуживания
                  продукции;
                </p>
                <p>
                  5) гарантийных обязательств, сроков службы, сроков годности;
                </p>
                <p>6) предполагаемых результатов применения;</p>
                <p>
                  7) исключительных прав на результаты интеллектуальной
                  деятельности и приравненных к ним средств индивидуализации
                  юридического лица, продукции, выполняемых работ или услуг;
                </p>
                <p>
                  8) прав на использование государственных символов (герба,
                  флага, гимна), а также символов международных организаций;
                </p>
                <p>
                  9) официального признания, получения медалей, призов, дипломов
                  и иных наград;
                </p>
                <p>
                  10) предоставления информации о способах приобретения полной
                  серии продукции, если она является частью серии;
                </p>
                <p>
                  11) результатов исследований и испытаний, научных терминов,
                  цитат из технических, научных и иных публикаций;
                </p>
                <p>
                  12) утверждений о товарах (работах, услугах), осуществляемой
                  предпринимательской деятельности, дискредитирующих физическое
                  или юридическое лицо, промышленную или торговую деятельность
                  других лиц;
                </p>
                <p>
                  13) статистической информации, которая не должна
                  предоставляться в виде, преувеличивающем ее обоснованность;
                </p>
                <p>
                  14) статуса или уровня компетентности производителя, продавца
                  товаров (работ, услуг) или лиц, их рекламирующих.
                </p>
                <p>3. Неэтичной является реклама, которая:</p>
                <p>
                  1) содержит текстовую, зрительную, звуковую информацию,
                  нарушающую общепринятые нормы гуманности и морали путем
                  употребления оскорбительных слов, сравнений, образов в
                  отношении расы, национальности, языка, профессии, социального
                  положения, возраста, пола, религиозных и политических
                  убеждений физических лиц;
                </p>
                <p>
                  2) порочит объекты искусства, культуры, памятники истории,
                  являющиеся национальным или мировым достоянием;
                </p>
                <p>
                  3) порочит государственные символы, национальную валюту
                  Республики Казахстан или иностранную валюту, религиозные
                  символы.
                </p>
                <p>
                  4. Заведомо ложной является реклама, с помощью которой
                  рекламодатель (рекламопроизводитель, рекламораспространитель)
                  умышленно вводит в заблуждение потребителя рекламы.
                </p>
                <p>
                  5. Скрытой является реклама, которая оказывает не осознаваемое
                  потребителем воздействие на его восприятие, инстинкты в
                  радио-, теле-, видео-, аудио- и кинопродукции, а также в иной
                  продукции, в том числе путем использования специальных
                  видеовставок, двойной звукозаписи и иными способами.
                </p>
                <p>6. Ненадлежащая реклама запрещается.</p>
                <p>Статья 8. Реклама на теле-, радиоканалах</p>
                <p>
                  1. Реклама на теле-, радиоканалах, не специализирующихся на
                  сообщениях и материалах рекламного характера, не должна
                  превышать двадцать процентов от общего объема вещания в сутки,
                  за исключением бегущей строки, социальной рекламы, информации
                  о собственной продукции теле-, радиоканалов (анонсов), не
                  содержащей рекламы третьих лиц, объявления о мероприятиях,
                  подготовленных и проводимых теле-, радиоканалом, а также
                  рекламы, размещаемой в месте события, транслируемого в прямом
                  эфире или записи повтора прямого эфира.
                </p>
                <p>
                  В общем объеме вещания рекламы в сутки общая продолжительность
                  рекламы в период времени с восемнадцати до двадцати трех часов
                  местного времени в течение часа времени вещания должна
                  составлять не более двадцати процентов.
                </p>
                <p>
                  На теле-, радиоканалах объем рекламы на казахском языке в
                  интервалах времени через каждые шесть часов, исчисляемых с
                  ноля часов местного времени, не должен быть менее суммарного
                  объема рекламы, распространяемой на других языках.
                </p>
                <p>
                  Объем рекламы на теле-, радиоканалах, за исключением
                  социальной рекламы, на казахском и русском языках, а также по
                  усмотрению рекламодателя и на других языках определяется
                  хронометражем размещаемых рекламных материалов в соответствии
                  с правилами проведения мониторинга средств массовой
                  информации, распространяемых на территории Республики
                  Казахстан, утвержденными уполномоченным органом в области
                  средств массовой информации.
                </p>
                <p>
                  Телеторговля на телеканалах, не специализирующихся на
                  сообщениях и материалах рекламного характера, должна
                  составлять не более тридцати процентов от общего объема
                  вещания рекламы в сутки.
                </p>
                <p>
                  Объем телеторговли на теле-, радиоканалах, специализирующихся
                  исключительно на сообщениях рекламного характера, не
                  ограничен.
                </p>
                <p>
                  При трансляции рекламы ее звук не должен быть громче звука
                  транслируемой программы.
                </p>
                <p>
                  1-1. Ежедневный объем социальной рекламы, размещаемой на
                  безвозмездной основе на обязательных теле-, радиоканалах,
                  должен составлять не менее десяти выходов в интервале времени
                  продолжительностью восемнадцать часов, исчисляемом с шести
                  часов утра местного времени, с обязательными двумя выходами в
                  интервале времени продолжительностью шесть часов, исчисляемом
                  с восемнадцати часов местного времени.
                </p>
                <p>
                  Социальная реклама распространяется равномерно в течение всего
                  ежедневного ее выхода в эфир на территории Республики
                  Казахстан на казахском и русском языках, а также по усмотрению
                  рекламодателя и на других языках.
                </p>
                <p>
                  2. При использовании рекламы в виде наложений, в том числе
                  способом бегущей строки, ее размер не должен превышать семь с
                  половиной процентов площади кадра и нарушать текстовый или
                  информационный материал в телепрограммах.
                </p>
                <p>
                  3. Запрещается прерывать рекламой, в том числе способом
                  бегущей строки, трансляцию официальных сообщений, выступлений
                  кандидатов в Президенты Республики Казахстан и в депутаты
                  представительных органов, образовательных и религиозных
                  телепрограмм, а также демонстрацию детских телепрограмм, за
                  исключением рекламы, предназначенной для детей и подростков.
                </p>
                <p>
                  4. В дни национального траура реклама на теле-, радиоканалах
                  запрещается.
                </p>
                <p>Статья 9. Реклама в периодических печатных изданиях</p>
                <p>
                  Реклама, ее тематика периодическими печатными изданиями
                  определяются самостоятельно.
                </p>
                <p>
                  Периодические печатные издания, распространяемые по подписке,
                  обязаны в условиях подписки указывать тематическую
                  направленность издания.
                </p>
                <p>
                  Реклама в периодических печатных изданиях размещается на
                  языке, закрепленном в свидетельстве о постановке на учет
                  средства массовой информации.
                </p>
                <p>
                  Статья 10. Реклама в кино-, видео- и справочном обслуживании
                </p>
                <p>
                  1. Запрещается прерывать рекламой демонстрацию фильма в кино-
                  и видеообслуживании, за исключением перерывов между сериями.
                </p>
                <p>
                  2. При справочном обслуживании реклама предоставляется только
                  после сообщения запрашиваемой информации.
                </p>
                <p>
                  3. При платном справочном, компьютерном и ином обслуживании
                  реклама распространяется только с согласия клиента. Стоимость
                  такой рекламы не должна включаться в стоимость запрашиваемой
                  информации.
                </p>
                <p>
                  1-1. Запрещается реклама товаров (работ, услуг) с
                  использованием элементов товарного знака или названия,
                  известного как наименование алкогольной продукции, табака и
                  табачного изделия, которые прямо или косвенно предлагают
                  алкогольную продукцию, табак и табачное изделие.
                </p>
                <p>
                  1-2. Запрещается реклама услуг по обязательному подтверждению
                  соответствия и (или) посреднических услуг в сфере
                  подтверждения соответствия продукции организациями, не
                  аккредитованными в порядке, установленном законодательством
                  Республики Казахстан об аккредитации в области оценки
                  соответствия.
                </p>
                <p>
                  Статья 15. Защита несовершеннолетних при производстве,
                  распространении, размещении рекламы
                </p>
                <p>
                  При производстве, распространении, размещении рекламы в целях
                  защиты несовершеннолетних от злоупотребления их доверием и
                  отсутствием у них опыта не допускаются:
                </p>
                <p>
                  1) дискредитация авторитета родителей, воспитателей,
                  педагогов, подрыв доверия к ним несовершеннолетних;
                </p>
                <p>
                  2) прямое предложение, призывающее убедить родителей или
                  других лиц приобрести рекламируемую продукцию;
                </p>
                <p>
                  3) прямое указание несовершеннолетним, что обладание той или
                  иной продукцией дает им какое-либо преимущество над другими, а
                  также что отсутствие такой продукции приведет к обратному;
                </p>
                <p>
                  4) размещение в рекламе текстовой, визуальной или звуковой
                  информации, показывающей несовершеннолетних в опасных местах и
                  ситуациях, когда это не оправдано профилактическими целями;
                </p>
                <p>
                  5) визуальное или звуковое использование образов
                  несовершеннолетних в рекламе, не относящейся непосредственно к
                  товарам (работам, услугам) для несовершеннолетних, за
                  исключением социальной рекламы;
                </p>
                <p>
                  6) приуменьшение необходимого уровня навыков использования
                  продукции у несовершеннолетних, за исключением случаев, когда
                  результаты использования продукции показаны или описаны.
                  Реклама должна давать информацию о том, что реально достижимо
                  для несовершеннолетних той возрастной группы, для которой
                  предназначена продукция;
                </p>
                <p>
                  7) создание у несовершеннолетних нереального (искаженного)
                  представления о стоимости (цене) продукции для
                  несовершеннолетних, а также прямое или косвенное указание на
                  то, что рекламируемая продукция доступна для любого семейного
                  бюджета.
                </p>
                <p>
                  Статья 17. Государственное регулирование в области рекламы
                </p>
                <p>
                  1. Под государственным регулированием в области рекламы
                  понимается устанавливаемый в соответствии с нормативными
                  правовыми актами Республики Казахстан контроль производства,
                  распространения и размещения рекламы.
                </p>
                <p>
                  Государственное регулирование в области рекламы осуществляется
                  уполномоченным органом в области рекламы, регулирующими
                  государственными органами в области рекламы, а также местными
                  исполнительными органами в пределах их компетенции,
                  установленной законодательством Республики Казахстан.
                </p>
                <p>
                  2. Основными целями государственного регулирования в области
                  рекламы являются:
                </p>
                <p>1) защита национальных интересов;</p>
                <p>
                  2) предотвращение и пресечение ненадлежащей рекламы, а также
                  рекламы, посягающей на общественные ценности и общепринятые
                  нормы морали и нравственности;
                </p>
                <p>3) защита от недобросовестной конкуренции.</p>
                <p>
                  3. Государственный контроль за соблюдением законодательства
                  Республики Казахстан о рекламе осуществляется в форме проверки
                  и профилактического контроля в соответствии с
                  Предпринимательским кодексом Республики Казахстан.
                </p>
              </div>
            </div>
          </div>

