                <p>
                  3. Реклама алкогольной продукции запрещается с 1 января 2004
                  года.
                </p>
                <p>Реклама табака и табачных изделий запрещается.</p>
                <p>
                  3-2. Запрещается размещать информацию о вакансиях для приема
                  на работу, содержащую требования дискриминационного характера
                  в сфере труда.
                </p>
                <p>
                  3-3. Запрещается реклама деятельности финансовой
                  (инвестиционной) пирамиды.
                </p>
