                  4-1) интернет-казино &ndash; интернет-ресурс, реализующий
                  возможность организации и проведения азартных игр в режиме
                  реального времени посредством сети Интернет и (или)
                  электронных денег и предусматривающий получение выигрыша;
                </p>
                <p>
                  5) казино - игорное заведение, в котором для организации и
