              ПРАВИЛА ФОРМИРОВАНИЯ И РАЗМЕЩЕНИЯ СОЦИАЛЬНОЙ РЕКЛАМЫ НА
              ОБЯЗАТЕЛЬНЫХ ТЕЛЕ-, РАДИОКАНАЛАХ
            </p>
            <div class="accordion-item child hidden">
              <div class="child text">
                <p>Глава 1. Общие положения</p>
                <p>
                  1. Настоящие Правила формирования и размещения социальной
                  рекламы на обязательных теле-, радиоканалах (далее &ndash;
                  Правила) разработаны в соответствии с подпунктом 1) пункта 2
                  статьи 17-1 Закона Республики Казахстан от 19 декабря 2003
                  года "О рекламе" и определяют порядок формирования и
                  размещения социальной рекламы на обязательных теле-,
                  радиоканалах.
                </p>
                <p>2. В настоящих Правилах используются следующие понятия:</p>
                <p>
                  1) социальная реклама &ndash; информация, адресованная
                  неопределенному кругу лиц и направленная на достижение
                  благотворительных и иных общественно полезных целей
                  некоммерческого характера, а также обеспечение интересов
                  государства;
                </p>
                <p>
                  2) правообладатель &ndash; физические или юридические лица,
                  получившие исключительное право на использование произведения
                  и (или) объекта смежных прав по договору или иному основанию,
                  предусмотренному Законом Республики Казахстан от 10 июня 1996
                  года "Об авторском праве и смежных правах";
                </p>
                <p>
                  3)обязательный теле-, радиоканал &ndash; теле-, радиоканал,
                  входящий в соответствующий перечень, утвержденный
                  уполномоченным органом, обязательный для распространения
                  посредством многоканального вещания на всей территории
                  Республики Казахстан;
                </p>
                <p>
                  4) заявитель &ndash; правообладатель либо иное лицо,
                  действующее от имени правообладателя на основании
                  доверенности, оформленной в соответствии с Гражданским
                  кодексом Республики Казахстан, которое имеет право на подачу
                  заявки на размещение социальной рекламы на обязательных теле-,
                  радиоканалах;
                </p>
                <p>
                  5) комиссия по вопросам развития телерадиовещания (далее
                  &ndash; Комиссия) &ndash; консультативно-совещательный орган
                  по вопросам развития телерадиовещания, созданный при
                  государственном органе, осуществляющим государственное
                  регулирование в области телерадиовещания и средств массовой
                  информации;
                </p>
                <p>
                  6) уполномоченный орган &ndash; государственный орган,
                  осуществляющий государственное регулирование в области
                  телерадиовещания и средств массовой информации.
                </p>
                <p>
                  Глава 2. Порядок формирования и размещения социальной рекламы
                  на обязательных теле-, радиоканалах
                </p>
                <p>
                  3. Социальная реклама на обязательных теле-, радиоканалах
                  формируется на основании следующих критериев:
                </p>
                <p>
                  1) соответствие техническим параметрам, выставляемым к ролику
                  социальной рекламы, установленным в приложении 1 к настоящим
                  Правилам;
                </p>
                <p>
                  2) соответствие требованиям, установленным законодательством
                  Республики Казахстан в области рекламы, телерадиовещания и
                  благотворительности.
                </p>
                <p>
                  4. Для размещения социальной рекламы на обязательных теле-,
                  радиоканалах заявители представляют в уполномоченный орган
                  следующие документы:
                </p>
                <p>
                  1) заявка на размещение социальной рекламы на обязательных
                  теле-, радиоканалах согласно приложению 2 к настоящим
                  Правилам;
                </p>
                <p>
                  2) ролик социальной рекламы на казахском и русском языках, на
                  внешнем электронном носителе, обеспечивающем неизменность
                  качества ролика при его передаче на другие носители;
                </p>
                <p>
                  3) документ, подтверждающий исключительное право на
                  использование произведения и (или) объекта смежных прав;
                </p>
                <p>
                  4) согласие правообладателя в случае подачи заявки лицом,
                  действующем на основании доверенности.
                </p>
                <p>
                  5. Уполномоченный орган рассматривает представленные заявки и
                  прилагаемые документы на соответствие требованиям,
                  установленным законодательством Республики Казахстан в области
                  рекламы, телерадиовещания и благотворительности и настоящими
                  Правилами, и уведомляет о соответствии либо не соответствии
                  заявки в срок не более пятнадцать календарных дней со дня
                  подачи заявки заявителем.
                </p>
                <p>
                  6. В случае соответствия заявки требованиям, предусмотренным
                  законодательством Республики Казахстан в области рекламы,
                  телерадиовещания и благотворительности и настоящими Правилами,
                  а также представления полного пакета документов, социальная
                  реклама выносится на рассмотрение Комиссии, созданной в
                  соответствии со статьей 11 Закона Республики Казахстан от 18
                  января 2012 года "О телерадиовещании" (далее &ndash; Закон),
                  заседания которой проводятся не менее, чем один раз в
                  полугодии или по мере необходимости.
                </p>
                <p>
                  7. Заявки на размещение социальной рекламы на обязательных
                  теле-, радиоканалах подаются не позднее, чем за пятнадцать
                  календарных дней до даты заседания Комиссии.
                </p>
                <p>
                  8. Объявление о проведении заседания Комиссии публикуется на
                  официальном интернет-ресурсе уполномоченного органа не менее
                  чем за месяц до дня его проведения с указанием срока окончания
                  приема заявок и даты заседания.
                </p>
                <p>
                  9. Рассмотрение Комиссией заявок, поступивших после истечения
                  срока приема заявок, указанного в объявлении, переносится на
                  следующее заседание Комиссии.
                </p>
                <p>
                  10. Заявки, не соответствующие критериям, установленным
                  настоящими Правилами, приводятся в соответствие и повторно
                  направляются в уполномоченный орган для рассмотрения на
                  следующем заседании Комиссии.
                </p>
                <p>
                  11. На заседание Комиссии приглашаются заявитель или его
                  уполномоченные представители.
                </p>
                <p>
                  12. Социальная реклама подлежит размещению на обязательных
                  теле-, радиоканалах, если за нее проголосовало большинство
                  присутствующих членов Комиссии. В случае равенства голосов,
                  голос председателя Комиссии является решающим.
                </p>
                <p>
                  13. Секретарем Комиссии оформляется протокол заседания
                  Комиссии в порядке, предусмотренном положением Комиссии,
                  утверждаемым согласно пункту 4 статьи 11 Закона, который
                  включает рекомендации и предложения по размещению роликов
                  социальной рекламы на обязательных теле-, радиоканалах.
                </p>
                <p>
                  14. Протокол заседания Комиссии подлежит размещению на
                  интернет-ресурсе уполномоченного органа.
                </p>
              </div>
            </div>
          </div>

          <div class="accordion-item child nested hidden">
            <p class="parent" @click="changeToActive">
              ПРАВИЛА ОСУЩЕСТВЛЕНИЯ РЕКЛАМЫ БИОЛОГИЧЕСКИ АКТИВНЫХ ДОБАВОК К ПИЩЕ
            </p>
            <div class="accordion-item child hidden">
              <div class="child text">
                <p>Глава 1. Общие положения</p>
                <p>
                  1. Настоящие Правила рекламы биологически активных добавок к
                  пище (далее - Правила) разработаны в соответствии со статьей
                  18 Кодекса Республики Казахстан от 18 сентября 2009 года "О
                  здоровье народа и системе здравоохранения", Законом Республики
                  Казахстан от 19 декабря 2003 года "О рекламе", и определяют
                  порядок осуществления рекламы биологически активных добавок к
                  пище в Республике Казахстан.
                </p>
                <p>2. В настоящих Правилах используется следующее понятие:</p>
                <p>
                  биологически активные добавки к пище (далее &ndash; БАД)
                  &ndash; продукты, содержащие пищевые и (или) биологически
                  активные вещества (их концентраты) природного происхождения
                  или идентичные им вещества искусственного происхождения, а
                  также пребиотические компоненты и пробиотические
                  микроорганизмы, предназначенные для употребления с пищей с
                  целью оптимизации рациона человека и не являющиеся
                  единственным источником пищи или диетического питания.
                </p>
                <p>
                  3. Контроль за рекламой биологически активных добавок к пище в
                  Республике Казахстан осуществляется ведомством уполномоченного
                  органа в сфере санитарно-эпидемиологического благополучия,
                  осуществляющим функции контроля за рекламой биологически
                  активных добавок к пище и его территориальными
                  подразделениями.
                </p>
                <p>Глава 2. Порядок осуществления рекламы биологически</p>
                <p>активных добавок к пище</p>
                <p>
                  4. Размещение рекламы биологически активных добавок к пище
                  осуществляется при ее соответствии законодательству в области
                  здравоохранения и рекламы.
                </p>
                <p>5. Реклама биологически активных добавок:</p>
                <p>
                  1) распространяется на казахском и русском языках, а также по
                  усмотрению рекламодателя и на других языках, за исключением
                  периодических печатных изданий;
                </p>
                <p>
                  2) легко читается, печатается четким и разборчивым шрифтом, за
                  исключением рекламных текстов аудио рекламы;
                </p>
                <p>3) исключает сравнение с лекарственными средствами;</p>
                <p>
                  4) исключает содержание сведений об обладании лечебными
                  свойствами;
                </p>
                <p>
                  5) является достоверной и распознаваемой (без применения
                  специальных знаний или применения специальных средств);
                </p>
                <p>
                  6) не вводит потребителей в заблуждение посредством
                  злоупотребления их доверием, в том числе в отношении
                  характеристик, состава, потребительских свойств, стоимости
                  (цены), предполагаемых результатов применения, результатов
                  исследований и испытаний;
                </p>
                <p>7) способствует рациональному применению;</p>
                <p>
                  8) исключает содержание сравнения рекламируемых биологически
                  активных добавок к пище с товарами других физических или
                  юридических лиц, а также высказывания, образы, порочащие их
                  честь, достоинство и деловую репутацию;
                </p>
                <p>
                  9) исключает ссылки в рекламе на рекомендации ученых,
                  специалистов здравоохранения, а также должностных лиц
                  государственных органов, которые вследствие собственной
                  известности могут поощрять применение и (или) назначение
                  биологически активных добавок.
                </p>
                <p>
                  6. Реклама биологически активных добавок к пище содержит
                  следующую информацию:
                </p>
                <p>1) торговое наименование;</p>
                <p>2) сведения об активных компонентах входящих в состав;</p>
                <p>3) основные показания к применению;</p>
                <p>4) способ применения и дозы;</p>
                <p>5) основные побочные действия;</p>
                <p>6) основные противопоказания;</p>
                <p>
                  7) особые указания в отношении детей, беременных женщин, а
                  также в период кормления грудью;
                </p>
                <p>8) условия отпуска;</p>
                <p>9) наглядную и понятную рекомендацию к применению;</p>
                <p>
                  10) название, адрес производителя и лица уполномоченного
                  производителем на принятие претензий в Республике Казахстан;
                </p>
                <p>
                  11) номер, дату выдачи свидетельства о государственной
                  регистрации.
                </p>
                <p>
                  Реклама, предназначенная для теле- и радиоканалов,
                  интернет-ресурсов содержит информацию, указанную в подпунктах
                  1), 7), 9) и 11) настоящего пункта.
                </p>
                <p>7. Запрещается:</p>
                <p>
                  1) реклама биологически активных добавок к пище не
                  зарегистрированных в Республике Казахстан и Евразийском
                  экономическом союзе;
                </p>
                <p>
                  2) распространение и размещение рекламы биологически активных
                  добавок к пище в общественном транспорте, в организациях, не
                  имеющих отношения к их назначению, использованию и отпуску;
                </p>
                <p>
                  3) представлять в рекламе биологически активные добавки к пище
                  как уникальные, наиболее эффективные и безопасные;
                </p>
                <p>
                  4) вызывать предположения, что эффективность лечения
                  рекламируемой биологически активной добавкой к пище является
                  гарантированной, применение не сопровождается развитием
                  побочных эффектов.
                </p>
                <p>
                  8. Реклама биологически активных добавок в каждом случае
                  сопровождается предупреждением о том, что объект
                  рекламирования не является лекарственным средством<strong
                    >.</strong
                  >
                </p>
              </div>
            </div>
          </div>

          <div class="accordion-item child nested hidden">
            <p class="parent" @click="changeToActive">
              ПРАВИЛА ОСУЩЕСТВЛЕНИЯ РЕКЛАМЫ ЛЕКАРСТВЕННЫХ СРЕДСТВ И МЕДИЦИНСКИХ
              ИЗДЕЛИЙ
            </p>
            <div class="accordion-item child hidden">
              <div class="child text">
                <p>Глава 1. Общие положения</p>
                <p>
                  1. Настоящие Правила осуществления рекламы лекарственных
                  средств и медицинских изделий (далее - Правила) разработаны в
                  соответствии с Законом Республики Казахстан от 19 декабря 2003
                  года "О рекламе", со статьей 18 Кодекса Республики Казахстан
                  от 18 сентября 2009 года "О здоровье народа и системе
                  здравоохранения", и определяют порядок осуществления рекламы
                  лекарственных средств и медицинских изделий в Республике
                  Казахстан.
                </p>
                <p>
                  2. В настоящих Правилах используются следующие термины и
                  определения:
                </p>
                <p>
                  1) реклама лекарственных средств и медицинских изделий (далее
                  &ndash; реклама) - информация, распространяемая и размещаемая
                  в любой форме, с помощью любых средств предназначенная для
                  неопределенного круга лиц, содержащая отдельные сведения или
                  совокупность сведений о лекарственных средствах, и медицинских
                  изделиях, способствующая их продвижению и реализации;
                </p>
                <p>
                  2) научно-информационный материал - сведения или совокупность
                  сведений о лекарственном средстве и медицинском изделий,
                  содержащие научные и аналитические данные, распространяемые в
                  форме научных статей, методических указаний, учебных пособий;
                </p>
                <p>
                  3) заявитель - физическое или юридическое лицо
                  (организация-производитель, дистрибьютор, представительство)
                  или их доверенное лицо, уполномоченное подавать заявление,
                  документы и материалы на рекламу;
                </p>
                <p>
                  4) рекламный материал - документы и материалы, используемые
                  при проведении оценки рекламы лекарственных средств и
                  медицинских изделии, на соответствие законодательству
                  Республики Казахстан в области здравоохранения, полученные от
                  заявителя и из государственных информационных систем;
                </p>
                <p>
                  5) рекламный модуль - макет рекламы, выполненный и
                  распространяемый на бумажном носителе (листовка, лифлет,
                  другое);
                </p>
                <p>
                  6) рекламная статья - информационная статья, содержащая
                  рекламу лекарственного средства и медицинского изделия;
                </p>
                <p>
                  7) раскадровка - последовательность рисунков, служащих
                  вспомогательным средством при создании видео роликов;
                </p>
                <p>
                  8) рекламораспространитель - физическое или юридическое лицо,
                  осуществляющее распространение и размещение рекламной
                  информации путем предоставления и (или) использования
                  имущества, в том числе технических средств телерадиовещания и
                  иными способами;
                </p>
                <p>
                  9) осуществление рекламы лекарственных средств и медицинских
                  изделий &ndash; распространение рекламы лекарственных средств
                  и медицинских изделий соответствующей законодательству
                  Республики Казахстан в области здравоохранения в
                  специализированной печати, предназначенной для медицинских и
                  фармацевтических работников, в средствах массовой информации в
                  рамках установленных сроков;
                </p>
                <p>
                  10) государственная экспертная организация в сфере обращения
                  лекарственных средств и медицинских изделий (далее &ndash;
                  экспертная организация) - организация, определяемая
                  уполномоченным органом в области здравоохранения для
                  проведения оценки рекламы лекарственных средств и медицинских
                  изделий;
                </p>
                <p>
                  11) рекламодатель - физическое или юридическое лицо,
                  являющееся источником рекламной информации для производства,
                  распространения и размещения рекламы;
                </p>
                <p>
                  12) рекламопроизводитель - физическое или юридическое лицо,
                  осуществляющее приведение рекламной информации к готовой для
                  распространения и размещения форме.
                </p>
                <p>
                  3. Распространение и размещение лекарственных средств и
                  медицинских изделий допускаются в периодических печатных
                  изданиях, иных средствах массовой информации и в организациях
                  здравоохранения.
                </p>
                <p>
                  Реклама лекарственных средств, отпускаемых по рецептам, в том
                  числе содержащих наркотические средства, психотропные вещества
                  и прекурсоры, осуществляется в специализированных
                  периодических печатных изданиях, предназначенных для
                  медицинских и фармацевтических работников.
                </p>
                <p>
                  4. Реклама осуществляется после оценки рекламных материалов
                  экспертной организацией на соответствие законодательству
                  Республики Казахстан в области здравоохранения.
                </p>
                <p>
                  5. К рекламе лекарственных средств и медицинских изделий не
                  относятся:
                </p>
                <p>
                  1) информация, имеющая отношение к здоровью или заболеваниям
                  человека;
                </p>
                <p>
                  2) инструкция по медицинскому применению, торговые каталоги,
                  прайс-листы, справочные материалы, научно-информационный
                  материал, методические и учебные материалы медицинского
                  характера;
                </p>
                <p>
                  3) информация о физическом и (или) юридическом лице,
                  производящем или реализующем лекарственное средство и (или)
                  медицинское изделие;
                </p>
                <p>
                  4) логотип, торговое и/или международное непатентованное
                  название, нанесенное на промышленную продукцию,
                  распространяемую среди медицинских и фармацевтических
                  работников (ручки, блокноты, сумки, медицинские халаты,
                  календари, визитницы, флешки и другое).
                </p>
                <p>
                  6. Рекламораспространитель размещает рекламу при
                  предоставлении заключения экспертной организации о
                  соответствии рекламы законодательству Республики Казахстан в
                  области здравоохранения.
                </p>
                <p>7. Реклама соответствует следующим требованиям:</p>
                <p>1) предоставляется на государственном и русском языках;</p>
                <p>
                  2) легко читается, печатается четким и разборчивым шрифтом, за
                  исключением рекламных текстов аудио рекламы;
                </p>
                <p>
                  3) исполняется в таком виде, чтобы было ясно, что
                  рекламируемое средство является лекарственным средством и
                  (или) медицинским изделием;
                </p>
                <p>
                  4) является достоверной и распознаваемой (без специальных
                  знаний или применения специальных средств);
                </p>
                <p>
                  5) исключает сравнения с другими лекарственными средствами и
                  медицинскими изделиями;
                </p>
                <p>
                  6) не вводит потребителей в заблуждение посредством
                  злоупотребления их доверием, в том числе в отношении
                  характеристик, состава, потребительских свойств, стоимости
                  (цены), предполагаемых результатов применения, результатов
                  исследований и испытаний;
                </p>
                <p>7) способствует рациональному применению;</p>
                <p>
                  8) соответствует инструкции по медицинскому применению
                  лекарственного средства и медицинского изделия, утвержденной
                  уполномоченным органом в области здравоохранения при
                  государственной регистрации и эксплуатационному документу для
                  медицинского изделия.
                </p>
                <p>
                  При внесении в инструкцию по медицинскому применению
                  лекарственного средства и медицинского изделия изменений,
                  влияющих на содержание распространяемой рекламы, внесенные
                  изменения отражаются в рекламных материалах.
                </p>
                <p>
                  8. Реклама лекарственных препаратов содержит следующую
                  обязательную информацию:
                </p>
                <p>1) торговое наименование;</p>
                <p>
                  2) международное непатентованное название или сведения об
                  активных компонентах, входящих в состав;
                </p>
                <p>3) основные показания к применению;</p>
                <p>4) способ применения и дозы;</p>
                <p>5) основные побочные действия;</p>
                <p>6) основные противопоказания;</p>
                <p>
                  7) особые указания в отношении детей, беременных женщин, а
                  также в период кормления грудью;
                </p>
                <p>8) условия отпуска;</p>
                <p>
                  9) наглядную и понятную рекомендацию перед назначением и
                  применением внимательно прочитать инструкцию по медицинскому
                  применению;
                </p>
                <p>
                  10) название, адрес производителя и/или торгового
                  представителя в Республике Казахстан;
                </p>
                <p>11) номер дату выдачи регистрационного удостоверения;</p>
                <p>12) дату истечения срока регистрации.</p>
                <p>
                  В рекламе лекарственных препаратов, отпускаемых по рецепту
                  врача, дополнительно указываются основные лекарственные
                  взаимодействия.
                </p>
                <p>
                  Реклама, предназначенная для теле- и радиоканалов,
                  интернет-ресурсов содержит информацию, указанную в подпунктах
                  1), 3), 7), 9), 11), 12) настоящего пункта.
                </p>
                <p>
                  9. Реклама медицинских изделий содержит следующую обязательную
                  информацию:
                </p>
                <p>1) торговое наименование;</p>
                <p>2) основные показания к применению (область применения);</p>
                <p>3) основные побочные действия (при наличии);</p>
                <p>4) основные противопоказания (при наличии);</p>
                <p>
                  5) ясную и наглядную рекомендацию перед назначением и
                  применением внимательно прочитать инструкцию по медицинскому
                  применению (эксплуатационный документ для медицинского
                  изделия);
                </p>
                <p>
                  6) название, адрес производителя и/или торгового представителя
                  в Республике Казахстан;
                </p>
                <p>7) номер дату выдачи регистрационного удостоверения;</p>
                <p>8) дату истечения срока регистрации.</p>
                <p>
                  Реклама, предназначенная для теле- и радиоканалов,
                  интернет-ресурсов содержит информацию, указанную в подпунктах
                  1), 2), 5), 7), 8) настоящего пункта.
                </p>
                <p>
                  10. Реклама лекарственных средств, изделий медицинского
                  назначения и медицинской техники запрещается в случаях
                  предусмотренных пунктом 3 статьи 18 Кодекса Республики
                  Казахстан от 18 сентября 2009 года "О здоровье народа и
                  системе здравоохранения".
                </p>
                <p>
                  Глава 3. Порядок проведения оценки рекламных материалов
                  лекарственных средств и медицинских изделий
                </p>
                <p>
                  11. Оценка рекламы на соответствие проводится экспертной
                  организацией на основании заявления, по форме согласно
                  приложению 1 к настоящим Правилам в соответствии с договором,
                  заключенным между заявителем и экспертной организацией и
                  оплаты стоимости в соответствии с прейскурантом, утвержденным
                  экспертной организацией.
                </p>
                <p>
                  12. Для получения заключения о соответствии рекламы
                  законодательству Республики Казахстан в области
                  здравоохранения заявитель одновременно с заявлением о
                  проведении оценки рекламных материалов представляет в
                  экспертную организацию следующие документы и материалы:
                </p>
                <p>
                  1) рекламную информацию на бумажном и электронном носителях на
                  государственном и русском языках (модуль, статья, раскадровка
                  видео рекламы, рекламный текст аудио рекламы);
                </p>
                <p>
                  2) видео-, аудиозаписи рекламы на государственном и русском
                  языках при распространении на видео-, радио каналах;
                </p>
                <p>
                  3) эксплуатационный документ (в случае предоставления рекламы
                  на медицинские изделия);
                </p>
                <p>
                  4) сведения о документах, удостоверяющих государственную
                  регистрацию лекарственных средств и медицинских изделий,
                  оценку безопасности и эффективности лекарственных средств и
                  медицинских изделий, инструкцию по медицинскому применению
                  экспертная организация получает из соответствующих
                  государственных информационных систем в форме электронных
                  документов.
                </p>
                <p>
                  13. Экспертная организация после принятия и регистрации
                  заявления в течение 20 рабочих дней проверяет соответствие
                  комплектности и достоверности документов, представленных на
                  оценку в соответствии с пунктом 12 настоящего приказа,
                  проводит оценку рекламы на соответствие законодательству
                  Республики Казахстан в области здравоохранения.
                </p>
                <p>
                  14. Результат проведения оценки рекламы оформляется актом
                  экспертной оценки рекламных материалов по форме согласно
                  приложению 2 к настоящим Правилам.
                </p>
                <p>
                  15. На основании акта экспертной оценки рекламных материалов
                  заявителю выдается заключение экспертной организации по форме
                  согласно приложениям 3 или 4 к настоящим Правилам или
                  мотивированный отказ в письменном виде.
                </p>
                <p>
                  16. По результатам проведения оценки рекламных материалов
                  экспертная организация возвращает заявителю рекламную
                  информацию на бумажном носителе на государственном и русском
                  языках (модуль, статья, раскадровка видео рекламы, рекламный
                  текст аудио рекламы) со штампом экспертной организации "Оценка
                  на соответствие законодательству Республики Казахстан
                  проведена" с указанием номера и даты акта экспертной оценки и
                  подписью лица, проводившего оценку рекламныхматериалов.
                </p>
              </div>
            </div>
          </div>

          <div class="accordion-item child nested hidden">
            <p class="parent" @click="changeToActive">
              ПРАВИЛА ПРОВЕДЕНИЯ МОНИТОРИНГА СМИ, РАСПРОСТРАНЯЕМЫХ НА ТЕРРИТОРИИ
              РК
            </p>
            <div class="accordion-item child hidden">
              <div class="child text">
                <p>Глава 1. Общие положения</p>
                <p>
                  1. Настоящие Правила разработаны в соответствии с подпунктом
                  13-2) статьи 4-3 Закона Республики Казахстан от 23 июля 1999
                  года "О средствах массовой информации" (далее &ndash; Закон) и
                  определяют порядок проведения мониторинга средств массовой
                  информации, распространяемых на территории Республики
                  Казахстан.
                </p>
                <p>2. В настоящих Правилах используются следующие понятия:</p>
                <p>
                  1) средство массовой информации &ndash; периодическое печатное
                  издание, теле-, радиоканал, кинодокументалистика,
                  аудиовизуальная запись и иная форма периодического или
                  непрерывного публичного распространения массовой информации,
                  включая интернет-ресурсы;
                </p>
                <p>
                  2) мониторинг средств массовой информации &ndash; процесс
                  сбора и анализа продукции средств массовой информации на
                  предмет соблюдения законодательства Республики Казахстан;
                </p>
                <p>
                  3) продукция средства массовой информации &ndash; тираж или
                  часть тиража отдельного номера периодического печатного
                  издания или аудиовизуальной программы, отдельный выпуск
                  радио-, теле-, кинохроникальной программы, информация,
                  размещенная на интернет-ресурсе;
                </p>
                <p>
                  4) сетевое издание &ndash; интернет-ресурс, прошедший
                  процедуру постановки на учет в уполномоченном органе,
                  информационно-коммуникационная инфраструктура которого
                  размещена на территории Республики Казахстан;
                </p>
                <p>
                  5) интернет-ресурс &ndash; электронный информационный ресурс,
                  отображаемый в текстовом, графическом, аудиовизуальном или
                  ином виде, размещаемый на аппаратно-программном комплексе,
                  имеющий уникальный сетевой адрес и (или) доменное имя и
                  функционирующий в Интернете;
                </p>
                <p>
                  6) периодическое печатное издание &ndash; газета, журнал,
                  альманах, бюллетень, приложения к ним, имеющие постоянное
                  название, текущий номер и выпускаемые не реже одного раза в
                  три месяца;
                </p>
                <p>
                  7) теле-, радиоканал &ndash; средство массовой информации,
                  представляющее собой совокупность теле-, радиопрограмм и
                  аудиовизуальных произведений, сюжетов и материалов,
                  сформированных в соответствии с сеткой вещания для последующей
                  трансляции и/или ретрансляции.
                </p>
                <p>
                  3. Мониторинг средств массовой информации осуществляется с
                  использованием информационной системы "Автоматизированный
                  мониторинг национального информационного пространства".
                </p>
                <p>
                  Глава 2. Порядок проведения мониторинга средств массовой
                  информации, распространяемых на территории Республики
                  Казахстан
                </p>
                <p>
                  4. Мониторинг средств массовой информации, распространяемых на
                  территории Республики Казахстан, проводится уполномоченным
                  органом в области средств массовой информации и
                  телерадиовещания (далее &ndash; уполномоченный орган) с целью
                  выявления и фиксирования нарушений норм законодательства
                  Республики Казахстан.
                </p>
                <p>5. Объектом мониторинга выступают:</p>
                <p>1) теле-, радиоканалы;</p>
                <p>2) периодические печатные издания;</p>
                <p>3) интернет-ресурсы и сетевые издания;</p>
                <p>4) кинодокументалистика;</p>
                <p>5) аудиовизуальная запись.</p>
                <p>
                  6. Проведение мониторинга средств массовой информации включает
                  в себя следующие этапы:
                </p>
                <p>
                  1) просмотр (прослушивание) продукции средства массовой
                  информации, выявление, фиксация и сбор нарушений;
                </p>
                <p>
                  2) правовой анализ и обобщение информации о нарушениях в
                  средствах массовой информации;
                </p>
                <p>
                  3) дополнительный анализ выявленных фактов нарушений
                  законодательства, проводимый уполномоченным органом.
                </p>
                <p>
                  7. По результатам мониторинга указываются следующие сведения:
                </p>
                <p>1) основные данные об адресате:</p>
                <p>
                  для периодических печатных изданий &ndash; наименование
                  собственника средства массовой информации, юридический адрес,
                  официальный адрес электронной почты;
                </p>
                <p>
                  для теле-, радиоканалов &ndash; наименование собственника
                  средства массовой информации, юридический адрес, официальный
                  адрес электронной почты;
                </p>
                <p>
                  для сетевых изданий, информационных агентств и
                  интернет-ресурсов &ndash; наименование собственника сетевого
                  издания, информационного агентства, интернет-ресурса,
                  юридический адрес, IP-адрес интернет-ресурса, при наличии
                  &ndash; официальный адрес электронной почты. В случае
                  невозможности установления сведений о собственнике
                  интернет-ресурса уполномоченным органом направляется
                  уведомление в адрес хостинг-провайдера, на сервере которого он
                  размещен.
                </p>
                <p>2) описательно-мотивировочная часть:</p>
                <p>результаты анализа и сбора информации.</p>
                <p>3) приложение (доказательные материалы):</p>
                <p>
                  для периодических печатных изданий &ndash; сканированная копия
                  фрагмента издания из результатов анализа;
                </p>
                <p>
                  для теле-, радиоканалов &ndash; запись на электронном
                  носителе, экранные снимки материала из результатов анализа;
                </p>
                <p>
                  для сетевых изданий, информационных агентств и
                  интернет-ресурсов &ndash; экранные снимки, запись на
                  электронном носителе в зависимости от способа распространения
                  информации.
                </p>
                <p>
                  8. Для проведения мониторинга по техническому и методическому
                  обеспечению мониторинга средств массовой информации
                  уполномоченный орган в соответствии с бюджетным
                  законодательством, на основании договора привлекает
                  организацию, выполняющую работы по техническому и
                  методическому обеспечению мониторинга (далее &ndash;
                  организация).
                </p>
                <p>
                  9. Уполномоченный орган с помощью методической и технической
                  поддержки, в том числе получением сведений от организации,
                  производит фиксацию и сбор нарушений законодательства
                  Республики Казахстан, выявленных в процессе мониторинга
                  продукции средств массовой информации, а также по уведомлениям
                  государственных органов, по обращениям физических и
                  юридических лиц, запросам должностных лиц, направленных в
                  уполномоченный орган.
                </p>
                <p>
                  10. По результатам проведенного мониторинга средств массовой
                  информации путем просмотра (прослушивание), фиксации и сбора
                  нарушений средств массовой информации уполномоченный орган
                  принимает меры в рамках проведения профилактического контроля
                  без посещения субъекта (объекта) контроля в соответствии со
                  статьей 4-5 Закона.
                </p>
                <p>
                  11. При квалификации нарушения законодательства Республики
                  Казахстан, влекущего уголовную ответственность, уполномоченный
                  орган направляет в государственный орган, к компетенции
                  которого относится рассмотрение дел по данному виду нарушения,
                  материалы с выявленным нарушением.
                </p>
                <p>
                  12. Уполномоченный орган по итогам года не позднее 30 числа
                  первого месяца следующего года размещает на своем
                  интернет-ресурсе итоговую информацию о проведенном мониторинге
                  средств массовой информации.
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>

