        <div class="accordion-item child nested hidden">
          <p class="parent" @click="changeToActive">
            ГАЗЕТА, ЖУРНАЛ, СЕТЕВОЕ ИЗДАНИЕ, ИНФОРМАГЕНТСТВО
          </p>
          <div class="accordion-item child hidden">
            <div class="child text">
              <p>
                Регистрация СМИ в Казахстане &ndash; довольно простая процедура.
                Все, что вам нужно &ndash; иметь намерение и быть индивидуальным
                предпринимателем или иметь юридическое лицо.
              </p>
              <p>
                <strong>Определите характеристики вашего будущего СМИ: н</strong
                >азвание и тематическая направленность; регулярность выхода и
                территория распространения; язык издания или вещания; кто будет
                возглавлять редакцию и где она будет находиться.
              </p>
              <p>
                Самый быстрый и простой способ &ndash; постановка на учет СМИ на
                портале  «Электронное правительство»:
              </p>
              <ul>
                <li>
                  заполняете заявление
                  <a
                    href="https://egov.kz/cms/ru/services/mass_media/pass467_aci"
                    >https://egov.kz/cms/ru/services/mass_media/pass467_aci</a
                  >
                  ,
                </li>
                <li>
                  прикладываете квитанцию об оплате сбора (5 МРП, если детская и
                  научная тематика &ndash; 2 МРП),
                </li>
                <li>подписываете электронной подписью и отправляете,</li>
                <li>
                  ждете 10 рабочих дней и получаете свидетельство о постановке
                  на учет вашего СМИ либо отказ.
                </li>
              </ul>
              <p>
                Начать издание газеты или журнала вы должны в течение трех
                месяцев, а сетевое издание и информационное агентство &ndash; в
                течение шести месяцев с момента получения свидетельства о
                постановке на учет СМИ.
              </p>
              <p>
                Если вам отказали в регистрации СМИ, то причины могут быть
                такие: СМИ с таким же названием уже существует и выходит, вы
                забыли уплатить сбор или не приложили квитанцию, не предоставили
                всю информацию в заявлении.
              </p>
              <p>
                Отказ в постановке на учет СМИ можно и нужно обжаловать, если вы
                не согласны с причиной отказа, по процедуре обжалования действий
                и бездействия, решений государственных органов.
              </p>
            </div>
          </div>
        </div>

