                  >,
                </li>
                <li>прикладываете квитанцию об оплате сбора (5 МРП)</li>
                <li>
                  обязательство о записи и хранении транслируемых и
                  ретранслируемых теле-, радиопрограмм в течение шести месяцев;
                </li>
                <li>
                  форма сведений о наличии имущественных прав собственника
                  теле-, радиоканала на помещения и площади с отдельным входом
                  или на их аренду, в том числе: специальных помещений для
                  размещения и эксплуатации технических средств, необходимых для
                  функционирования телерадиовещания (студийных, аппаратных,
                  вспомогательных); помещений для размещения творческого
                  персонала (редакционных); помещений для
                  административно-управленческого персонала.
                </li>
                <li>подписываете электронной подписью и отправляете.</li>
                <li>
                  ждете 10 рабочих дней и получаете свидетельство о постановке
                  на учет вашего СМИ либо отказ.
                </li>
              </ul>
              <p>В течение шести месяцев вы должны начать вещание.</p>
              <p>
                Причины отказа в постановке на учет могут быть такие: наличие
                уже действующего СМИ с таким же названием на той же территории
                распространения, нет оплаты обязательного сбора или не приложена
                квитанция, или не предоставлены необходимые документы.
              </p>
            </div>
          </div>
        </div>

        <div class="accordion-item child nested hidden">
          <p class="parent" @click="changeToActive">
            ИНОСТРАННОЕ ПЕЧАТНОЕ ИЗДАНИЕ
          </p>
          <div class="accordion-item child hidden">
            <div class="child text">
              <p>
                Аналогичная процедура постановки на учет действует не только для
                казахстанских СМИ, но и для иностранных медиа, которые просто
                распространяются на территории страны. Для иностранных печатных
                СМИ процедура несколько проще, чем для иностранных
                теле-радиоканалов.
              </p>
              <p>
                Во-первых, услуга доступна онлайн на портале  «Электронное
                правительство», во-вторых, она бесплатна. Заказать ее
