                <a href="https://egov.kz/cms/ru/services/mass_media/pass41_mic"
                  >https://egov.kz/cms/ru/services/mass_media/pass41_mic</a
                >.
              </p>
              <p>
                Требуемые документы &ndash; заявление с указанием основных
                сведений по иностранному СМИ и документ того лица, кто
                обращается с заявлением. Срок рассмотрения &ndash; 10 рабочих
                дней.
              </p>
            </div>
          </div>
        </div>

        <div class="accordion-item child nested hidden">
