                <a
                  href="https://egov.kz/cms/ru/services/approval_documents/pass_642_mint"
                  >https://egov.kz/cms/ru/services/approval_documents/pass_642_mint</a
                >, вот перечень дополнительных документов:
              </p>
              <ul>
                <li>
