                  деятельностью, с нотариально засвидетельствованным переводом
                  на казахском и русском языках;
                </li>
                <li>
                  для юридических лиц &ndash; легализованная выписка из
                  торгового реестра или другой легализованный документ,
                  удостоверяющий, что учредитель &ndash; иностранное юридическое
                  лицо является юридическим лицом по законодательству
                  иностранного государства;
                </li>
                <li>
                  документ, подтверждающий оплату в бюджет сбора за постановку
                  на учет теле-, радиоканала;
                </li>
                <li>
                  справка о государственной регистрации (перерегистрации)
                  юридического лица или учетной регистрации (перерегистрации)
                  филиала (представительства) на территории РК иностранных
                  юридических лиц в порядке, определенном законодательством РК о
                  государственной регистрации юридических лиц и учетной
                  регистрации филиалов и представительств.
                </li>
              </ul>
              <p>
                Срок рассмотрения заявления о постановке на учет иностранного
                теле, радиоканала &ndash; 15 рабочих дней, и до вынесения
                решения проводится экспертиза контента, а если канал религиозной
                направленности &ndash; то проводится обязательная
                религиоведческая экспертиза.
              </p>
            </div>
          </div>
        </div>

        <div class="accordion-item child nested hidden">
          <p class="parent" @click="changeToActive">ПЕРЕУЧЕТ СМИ</p>
          <div class="accordion-item child hidden">
            <div class="child text">
              <p>
                Если вы решили продать или передать свое СМИ другому
                собственнику, вам нужно пройти процедуру переучета СМИ. Переучет
