      <!-- СУДЕБНЫЙ ПРОЦЕСС -->
      <div id="court" class="accordion-item">
        <p class="parent" @click="changeToActive">
          ОСВЕЩЕНИЕ СУДЕБНОГО ПРОЦЕССА
        </p>

        <div class="accordion-item child nested hidden">
          <p class="parent" @click="changeToActive">ПРОЦЕДУРА</p>
          <div class="accordion-item child hidden">
            <div class="child text">
              <p>
                Судебные процессы могут быть открытыми и закрытыми в зависимости
                от категории дел и рассматриваемых на них вопросов.
              </p>
              <p>
                Перед началом процесса судья должен объявить, какое дело будет
                рассматриваться и в каком режиме - открытом или закрытом.
              </p>
              <p>
                <strong>Если процесс открытый</strong>, то журналисты и все
                желающие вправе присутствовать в зале судебного заседания.
              </p>
              <p>
                <strong>Если процесс закрытый</strong>, то судья выносит
                мотивированное определение, должен назвать причину
                (государственные секреты, сохранение личной, семейной,
                коммерческой или иной охраняемой законом тайны и пр.) и
                попросить удалиться из зала лиц, не участвующих в деле. Все это
                должно быть занесено в протокол судебного заседания. Вопрос о
                проведении закрытого судебного заседания решается конкретно в
                каждом случае.
              </p>
              <p>
                <strong>Основания для проведения закрытого процесса:</strong>
              </p>
              <p>
                Если в материалах дела содержатся сведения, являющиеся
                государственными секретами, а также при удовлетворении судом
                ходатайства участвующего в деле лица, ссылающегося на
                необходимость обеспечения тайны усыновления, сохранения личной,
                семейной, коммерческой или иной охраняемой законом тайны,
                сведений об интимных сторонах жизни граждан либо на иные
                обстоятельства, препятствующие открытому разбирательству
              </p>
              <p><strong>ВАЖНО!</strong></p>
              <p>
                <strong
                  >Все присутствующие в зале судебного заседания должны
                  подчиняться распоряжениям председательствующего судьи о
                  соблюдении порядка судебного заседания.</strong
                >
              </p>
              <p>
                <strong
                  >Журналист должен соблюдать презумпцию невиновности при
                  освещении судебных процессов.
                </strong>
              </p>
              <p>
                <strong
                  >Приговоры суда в любом случае оглашаются публично, и
                  журналисты имеют право на них присутствовать.
                </strong>
              </p>
            </div>
          </div>
        </div>

        <div class="accordion-item child nested hidden">
          <p class="parent" @click="changeToActive">
            ЕСЛИ ВЫ ОСВЕЩАЕТЕ ГРАЖДАНСКИЙ ПРОЦЕСС
          </p>
          <div class="accordion-item child hidden">
            <div class="child text">
              <p>
                Согласно пункту 8 статьи 19 ГПК не только журналист, но и лица,
                участвующие в деле, а также граждане, присутствующие в открытом
                судебном заседании, имеют право фиксировать письменно или с
                использованием аудиозаписи ход судебного разбирательства с
                занимаемых ими в зале мест. Что касается кино- и фотосъемки,
                видеозаписи, прямой радио- и телетрансляции в ходе судебного
                разбирательства, то они допускаются по разрешению суда с учетом
                мнения лиц, участвующих в деле.
              </p>
              <p>
                Доступ граждан в зал открытого судебного заседания не может быть
                ограничен ни под каким предлогом, за исключением лиц моложе
                шестнадцати лет, если они не являются лицами, участвующими в
                деле или свидетелями. Вопрос допуска всех желающих граждан в
                открытое судебное заседание разрешается с учетом вместимости
                зала судебного заседания.
              </p>
              <p>
                Ваши действия, связанные с проведением видеосъемок, не должны
                мешать нормальному ходу судебного заседания. Кроме того,
                видеосъемка может быть ограничена во времени.
              </p>
              <p><strong>ЕСЛИ НАРУШАЕТСЯ ПОРЯДОК? </strong></p>
              <p>
                Необходимо помнить о поведении в зале судебного заседания,
                которое может быть расценено судом как нарушение порядка.
              </p>
              <p>
                Сначала лицу, нарушившему порядок, судья делает предупреждение
                (это относится к лицам, не участвующим в деле и присутствующим
                на заседании). После повторного предупреждения судья вправе
                удалить человека из зала.
              </p>
            </div>
          </div>
        </div>

        <div class="accordion-item child nested hidden">
          <p class="parent" @click="changeToActive">
            ЕСЛИ ВЫ ОСВЕЩАЕТЕ АДМИНИСТРАТИВНЫЙ ПРОЦЕСС
          </p>
          <div class="accordion-item child hidden">
            <div class="child text">
              <p>
                Почти такие же требования в отношении гласности
                судопроизводства, как и в гражданском процессе, устанавливает
                статья 24 Кодекса об административных правонарушениях РК.
              </p>
              <p>
                <strong
                  >Основания для закрытого административного процесса:</strong
                >
              </p>
              <p>
                Если в материалах дела содержатся ведения, являющиеся
                государственными секретами, а также при удовлетворении судом
                ходатайства участвующего в деле лица, ссылающегося на
                необходимость обеспечения тайны усыновления, сохранения личной,
                семейной, коммерческой или иной охраняемой законом тайны,
                сведений об интимных сторонах жизни физических лиц либо на иные
                обстоятельства, препятствующие открытому разбирательству.
              </p>
              <p>
                Личная переписка и личные телеграфные сообщения физических лиц
                могут быть оглашены при открытом производстве только с согласия
                лиц, между которыми происходила переписка. В противном случае
                переписка и сообщения оглашаются и исследуются в закрытом
                режиме. Указанные правила применяются и при исследовании фото- и
                кинодокументов, звуко- и видеозаписей, содержащих сведения
                личного характера.
              </p>
            </div>
          </div>
        </div>

        <div class="accordion-item child nested hidden">
          <p class="parent" @click="changeToActive">
            ЕСЛИ ВЫ ОСВЕЩАЕТЕ УГОЛОВНЫЙ ПРОЦЕСС
          </p>
          <div class="accordion-item child hidden">
            <div class="child text">
              <p>
                Вы вправе находиться в зале суда и с занимаемых мест вести
                только письменную фиксацию судебного заседания.
              </p>
              <p>
                Фотографирование, применение звуко-, видеозаписи и киносъемки в
                зале судебного заседания при рассмотрении уголовного дела
                допускаются с согласия участников процесса и разрешения судьи.
                Эти действия ограничиваются только в том случае, если они могут
                помешать нормальному ходу судебного разбирательства. С процесса
                вас могут удалить за несоблюдение порядка, но уже без
                предварительного предупреждения.
              </p>
              <p>
                <strong
                  >Основания для проведения закрытого уголовного судебного
                  процесса:</strong
                >
              </p>
              <p>
                &bull; для необходимости охраны государственных секретов и иной
                охраняемой законом тайны;
              </p>
              <p>
                &bull; по делам об уголовных правонарушениях несовершеннолетних,
              </p>
              <p>
                &bull; по делам о половых преступлениях и другим делам в целях
                предотвращения разглашения сведений об интимных сторонах жизни
                участвующих в деле лиц,
              </p>
              <p>
                &bull; в случаях, когда этого требуют интересы безопасности
                потерпевшего, свидетеля или других участвующих в деле лиц, а
                также членов их семей или близких родственников.
              </p>
              <p>
                &bull; в случаях рассмотрения следственным судьей жалобы на
                действия (бездействие) и решения органа, осуществляющего
                уголовное преследование.
              </p>
              <p>
                <strong>ВАЖНО!</strong> Ограничение гласности судебного
                разбирательства может распространяться на весь период судебного
                заседания или на ту его часть, в которой исследуются
                обстоятельства, указанные выше.
              </p>
              <p>
                Если вы считаете, что принцип гласности судебного
                разбирательства был нарушен, вы можете обжаловать действия
                судьи. Для этого необходимо написать заявление в свободной форме
                на имя председателя суда, либо обратиться с жалобой на
                неправомерные действия судьи в Верховный суд РК. Жалоба
                составляется в произвольной форме. (подробнее <span class="highlight"> «Образцы
                документов»</span>)
              </p>
            </div>
          </div>
        </div>

        <div class="accordion-item child nested hidden">
