                специализированные)
              </p>
              <p>
                Вторая инстанция &ndash; апелляция (областные и приравненные к
                ним суды (все решения и приговоры, не вступившие в законную
                силу)
              </p>
              <p>Третья инстанция &ndash; кассация (Верховный суд РК)</p>
            </div>
          </div>
        </div>

        <div class="accordion-item child nested hidden">
          <p class="parent" @click="changeToActive">ЗАКОНОДАТЕЛЬНАЯ БАЗА</p>

          <div class="accordion-item child nested hidden">
            <p class="parent" @click="changeToActive">
              ГРАЖДАНСКИЙ ПРОЦЕССУАЛЬНЫЙ КОДЕКС
            </p>
            <div class="accordion-item child hidden">
              <div class="child text">
                <p>
                  <strong
                    >Статья 19. Гласность судебного разбирательства</strong
                  >
                </p>
                <p>
                  1. Разбирательство дел во всех судах и во всех судебных
                  инстанциях происходит открыто.
                </p>
                <p>
                  2. В закрытом судебном заседании осуществляется в соответствии
                  с законом разбирательство дел, включая объявление решений,
                  содержащих сведения, являющиеся государственными секретами, а
                  также при удовлетворении судом ходатайства участвующего в деле
                  лица, ссылающегося на необходимость обеспечения тайны
                  усыновления, сохранения личной, семейной, коммерческой или
                  иной охраняемой законом тайны, сведений об интимных сторонах
                  жизни граждан либо на иные обстоятельства, препятствующие
                  открытому разбирательству, а также в случае, предусмотренном
                  частью шестой статьи 179 настоящего Кодекса.
                </p>
                <p>
                  3. Личная переписка и личные телеграфные сообщения граждан
                  могут быть оглашены в открытом судебном заседании только с
                  согласия лиц, между которыми происходили эти переписка и
                  телеграфные сообщения. В противном случае личная переписка и
                  личные телеграфные сообщения этих лиц оглашаются и исследуются
                  в закрытом судебном заседании. Указанные правила применяются и
                  при исследовании фото- и кинодокументов, звуко- и
                  видеозаписей, а также сообщений, полученных при помощи иных
                  технических средств, содержащих сведения личного характера.
                </p>
                <p>
                  4. При разбирательстве дела в закрытом судебном заседании
                  присутствуют лица, участвующие в деле, их представители, а в
                  необходимых случаях также свидетели, эксперты, специалисты,
                  переводчики.
                </p>
                <p>
                  5. О разбирательстве дела в закрытом судебном заседании суд
                  выносит мотивированное определение.
                </p>
                <p>
                  6. В зал судебного заседания не допускаются граждане моложе
                  шестнадцати лет, если они не являются лицами, участвующими в
                  деле, или свидетелями.
                </p>
                <p>
                  7. Разбирательство дела в закрытом судебном заседании ведется
                  с соблюдением всех правил гражданского судопроизводства.
                </p>
                <p>
                  8. Лица, участвующие в деле, и граждане, присутствующие в
                  открытом судебном заседании, имеют право фиксировать письменно
                  или с использованием аудиозаписи ход судебного разбирательства
                  с занимаемых ими в зале мест. Кино- и фотосъемка, видеозапись,
                  прямая радио- и телетрансляция в ходе судебного
                  разбирательства допускаются по разрешению суда с учетом мнения
                  лиц, участвующих в деле. Эти действия не должны мешать
                  нормальному ходу судебного заседания и могут быть ограничены
                  судом во времени.
                </p>
              </div>
            </div>
          </div>

          <div class="accordion-item child nested hidden">
            <p class="parent" @click="changeToActive">
              УГОЛОВНО-ПРОЦЕССУАЛЬНЫЙ КОДЕКС
            </p>
            <div class="accordion-item child hidden">
              <div class="child text">
                <p><strong>Статья 29. Гласность</strong></p>
                <p>
                  1. Разбирательство уголовных дел во всех судах и во всех
                  судебных инстанциях происходит открыто. Ограничение гласности
                  судебного разбирательства допускается, лишь когда это
                  противоречит интересам охраны государственных секретов.
                  Закрытое судебное разбирательство, кроме того, допускается по
                  мотивированному постановлению суда по делам о преступлениях
                  несовершеннолетних, по делам о половых преступлениях и другим
                  делам в целях предотвращения разглашения сведений об интимных
                  сторонах жизни участвующих в деле лиц, а также в случаях,
                  когда этого требуют интересы безопасности потерпевшего,
                  свидетеля или других участвующих в деле лиц, а также членов их
                  семей или близких родственников. В закрытом судебном заседании
                  рассматриваются также разрешаемые судом на досудебной стадии
                  судебного процесса жалобы на действия и решения органа,
                  осуществляющего уголовное преследование.
                </p>
                <p>
                  2. Разбирательство дел и жалоб в закрытом заседании
                  осуществляется с соблюдением всех правил, установленных
                  настоящим Кодексом.
                </p>
                <p>
                  3. Приговор суда и постановления, принятые по делу, во всех
                  случаях провозглашаются публично.
                </p>
              </div>
            </div>
          </div>

          <div class="accordion-item child nested hidden">
            <p class="parent" @click="changeToActive">
              КОДЕКС ОБ АДМИНИСТРАТИВНЫХ ПРАВОНАРУШЕНИЯХ
            </p>
            <div class="accordion-item child hidden">
              <div class="child text">
                <p>
                  <strong
                    >Статья 24. Гласность производства по делам об
                    административных правонарушениях</strong
                  >
                </p>
                <p>
                  1. Суд, органы (должностные лица), уполномоченные
                  рассматривать дела об административных правонарушениях,
                  осуществляют производство по этим делам открыто.
                </p>
                <p>
                  2. В соответствии с законом закрытое производство
                  осуществляется в отношении дел, содержащих сведения,
                  являющиеся государственными секретами, а также при
                  удовлетворении судом, органом (должностным лицом),
                  уполномоченным рассматривать дела об административных
                  правонарушениях, ходатайства участвующего в деле лица,
                  ссылающегося на необходимость обеспечения тайны усыновления,
                  сохранения личной, семейной, коммерческой или иной охраняемой
                  законом тайны, сведений об интимных сторонах жизни физических
                  лиц либо на иные обстоятельства, препятствующие открытому
                  разбирательству.
                </p>
                <p>
                  3. Личная переписка и личные телеграфные сообщения физических
                  лиц могут быть оглашены при открытом производстве только с
                  согласия лиц, между которыми происходили переписка и
                  телеграфные сообщения. В противном случае личная переписка и
                  личные телеграфные сообщения этих лиц оглашаются и исследуются
                  при закрытом производстве. Указанные правила применяются и при
                  исследовании фото- и кинодокументов, звуко- и видеозаписей,
                  содержащих сведения личного характера.
                </p>
                <p>
                  4. Лица, участвующие в деле, и физические лица, присутствующие
                  при открытом производстве, имеют право фиксировать письменно
                  или с использованием аудиозаписи ход производства с занимаемых
                  ими мест в помещении, где осуществляется производство. Кино- и
                  фотосъемка, видеозапись, прямая радио- и телетрансляция в ходе
                  производства допускаются по разрешению суда, органа
                  (должностного лица), уполномоченного рассматривать дела об
                  административных правонарушениях, с учетом мнения лиц,
                  участвующих в деле. Эти действия не должны мешать нормальному
                  ходу производства и могут быть ограничены во времени.
                </p>
              </div>
            </div>
          </div>

          <div class="accordion-item child nested hidden">
            <p class="parent" @click="changeToActive">ЗАКОН О СМИ</p>
            <div class="accordion-item child hidden">
              <div class="child text">
                <p>
                  <strong
                    >Статья 2. Свобода слова, получения и распространения
                    информации</strong
                  >
                </p>
                <p>
                  1. Свобода слова, творчества, выражения в печатной и иной
                  форме своих взглядов и убеждений, получения и распространения
                  информации любым не запрещенным законом способом гарантируются
                  Конституцией Республики Казахстан.
                </p>
                <p>Цензура запрещается.</p>
                <p>
                  2. Государственные органы, общественные объединения,
                  должностные лица и средства массовой информации обязаны
                  обеспечить каждому гражданину возможность ознакомиться с
                  затрагивающими его права и интересы документами, решениями и
                  источниками информации.
                </p>
                <p>
                  3. Не допускаются разглашение сведений, составляющих
                  государственные секреты или иную охраняемую законом тайну,
                  пропаганда и оправдание экстремизма или терроризма,
                  распространение информации, раскрывающей технические приемы и
                  тактику антитеррористических операций в период их проведения,
                  пропаганда наркотических средств, психотропных веществ и
                  прекурсоров, а также культа жестокости, насилия и порнографии.
                </p>
                <p>
                  4. Не допускается использование средства массовой информации в
                  целях совершения административных правонарушений либо
                  уголовно-наказуемых деяний.
                </p>
              </div>
            </div>
          </div>

          <div class="accordion-item child nested hidden">
            <p class="parent" @click="changeToActive">
              НОРМАТИВНОЕ ПОСТАНОВЛЕНИЕ ВЕРХОВНОГО СУДА О СОБЛЮДЕНИИ ПРИНЦИПА
              ГЛАСНОСТИ СУДОПРОИЗВОДСТВА ПО УГОЛОВНЫМ ДЕЛАМ
            </p>
            <div class="accordion-item child hidden">
              <div class="child text">
                <p>
                  1. Принцип гласности судопроизводства, закрепленный в статье
                  29 Уголовно-процессуального кодекса (далее - УПК), в
                  совокупности с другими принципами обеспечивает выполнение
                  задач уголовного процесса, направленных на изобличение и
                  привлечение к ответственности лиц, совершивших преступления,
                  на справедливое судебное разбирательство. В этой связи, судам
                  необходимо шире реализовывать данный принцип, обеспечивать
                  возможность свободного доступа в залы судебного заседания всех
                  заинтересованных лиц, в том числе, представителей средств
                  массовой информации.
                </p>
                <p>
                  2. Суды обязаны строго соблюдать требования
                  уголовно-процессуального закона об открытом судебном
                  разбирательстве дел во всех судебных инстанциях и не допускать
                  фактов нарушения принципа гласности судопроизводства,
                  необоснованного отказа гражданам присутствовать в зале суда.
                </p>
                <p>
                  Незаконное ограничение гласности (необоснованное
                  засекречивание материалов дела, проведение закрытых заседаний
                  по не предусмотренным законом основаниям и т.п.) может повлечь
                  умаление и ущемление процессуальных прав участников процесса.
                </p>
                <p>
                  В зависимости от характера и существенности этих нарушений,
                  состоявшееся по делу производство может быть признано
                  недействительным, с отменой вынесенного решения, либо
                  собранные материалы могут быть признаны не имеющими силы
                  доказательств.
                </p>
                <p>
                  3. Под гласностью судопроизводства следует понимать не только
                  проведение открытого судебного разбирательства, но и
                  обеспечение участия в нем сторон, возможности присутствия иных
                  лиц, не участвующих в деле. Гласность предполагает доступность
                  участников процесса ко всем материалам дела, в том числе,
                  полученным в ходе оперативно-розыскных мероприятий, к
                  указаниям прокурора, данным в ходе досудебного расследования
                  (за исключением случаев, предусмотренных законом). Кроме того,
                  в этот принцип включается публичное провозглашение судебного
                  решения, извещение и ознакомление сторон с поступившими
                  жалобами других участников процесса, осведомленность о времени
                  и месте рассмотрения дела в суде любой инстанции, создание
                  единой базы данных вступивших в законную силу приговоров и
                  постановлений судов и свободный доступ к ним, доступность
                  информации об исполнении судебных актов.
                </p>
                <p>
                  4. Пределы гласности на досудебных стадиях производства по
                  делу в соответствии со статьей 201 УПК определяет прокурор, а
                  в судебном заседании - суд.
                </p>
                <p>
                  Суд, по ходатайству сторон или по собственной инициативе, при
                  назначении главного судебного разбирательства, с приведением в
                  постановлении соответствующих оснований, обязан решить вопрос
                  открытого или закрытого рассмотрения дела.
                </p>
                <p>
                  5. Участники процесса могут заявлять ходатайства об
                  ограничении гласности судебного разбирательства как на стадии
                  назначения главного судебного разбирательства
                  (предварительного слушания дела), так и в ходе главного
                  судебного разбирательства.
                </p>
                <p>
                  Иные лица, в том числе и представители средств массовой
                  информации, не вправе заявлять ходатайства о рассмотрении дел
                  в открытом или закрытом судебном заседании.
                </p>
                <p>
                  6. Ходатайства об ограничении гласности подлежат
                  удовлетворению только при наличии обстоятельств, указанных в
                  части первой статьи 29 УПК, в целях охраны государственных
                  секретов, предотвращения разглашения сведений об интимных
                  сторонах жизни участвующих в деле лиц, обеспечения
                  безопасности потерпевших, свидетелей и других лиц, членов их
                  семей или близких родственников, защиты интересов
                  несовершеннолетних подсудимых. В соответствии с частью шестой
                  статьи 106 УПК жалобы рассматриваются следственным судьей
                  единолично без проведения судебного заседания. Если необходимо
                  исследовать обстоятельства, имеющие значение для принятия
                  законного и обоснованного решения, следственный судья
                  рассматривает жалобу в закрытом судебном заседании с участием
                  соответствующих лиц и прокурора, неявка которых не
                  препятствует рассмотрению жалобы. По распоряжению
                  следственного судьи судебное заседание может быть проведено в
                  режиме видеосвязи.
                </p>
                <p>
                  7. Ограничение гласности судебного разбирательства может
                  распространяться на весь период судебного заседания или на ту
                  его часть, в которой исследуются обстоятельства, указанные в
                  части первой статьи 29 УПК, о чем должно быть указано в
                  постановлении.
                </p>
                <p>
                  Если в отношении отдельных подсудимых имеются основания для
                  проведения закрытого судебного разбирательства в связи с
                  охраной государственных секретов, а к другим подсудимым эти
                  обстоятельства не относятся, суд вправе выделить из уголовного
                  дела в отдельное производство другое уголовное дело, которое
                  подлежит рассмотрению в закрытом судебном заседании.
                </p>
                <p>
                  Если выделение дела отразится на всесторонности, полноте его
                  исследования и разрешения, то производство такого
                  процессуального действия, в соответствии с частью четвертой
                  статьи 44 УПК, не допускается. В этом случае суд, в
                  соответствии с частью первой статьи 29 УПК, решает вопрос о
                  проведении закрытого судебного разбирательства по делу в
                  целом.
                </p>
                <p>
                  8. Председательствующий при открытии главного судебного
                  разбирательства объявляет о рассмотрении дела в открытом или
                  закрытом судебном заседании. В протоколе судебного заседания
                  производится соответствующая запись.
                </p>
                <p>
                  При поступлении в главном судебном разбирательстве ходатайства
                  о рассмотрении дела в закрытом судебном заседании суд обязан
                  выяснить у лица, заявившего ходатайство, по каким
                  предусмотренным законом основаниям ставится вопрос об
                  ограничении гласности судебного разбирательства и мнение
                  других участников процесса. Если решение по заявленному
                  ходатайству суд принял, совещаясь на месте, то оно заносится в
                  протокол судебного заседания, если же ходатайство рассмотрено
                  в совещательной комнате, то суд выносит постановление в виде
                  отдельного процессуального документа.
                </p>
                <p>
                  9. В соответствии с требованиями статьи 347-1 УПК фиксирование
                  хода судебного заседания осуществляется секретарем судебного
                  заседания с помощью средств аудио-, видеозаписи. Невозможность
                  использования средств аудио-, видеозаписи из-за технической
                  неисправности оборудования не исключает продолжения судебного
                  заседания с обязательным отражением причин неиспользования
                  аудио-, видеозаписи в протоколе судебного заседания.
                </p>
                <p>
                  Применение судом аудио-, видеозаписи в судебных заседаниях
                  используется только в целях судопроизводства для точного
                  фиксирования хода судебного разбирательства, а также в целях
                  установления фактических данных по делу, что также
                  обеспечивает осуществление судом принципа гласности.
                </p>
                <p>
                  10. Председательствующий может разрешить фотографирование,
                  применение аудио-, видеозаписи с согласия участников процесса
                  в ходе всего главного судебного разбирательства, а также в
                  определенной части главного судебного разбирательства либо в
                  отношении некоторых участников процесса и иных лиц. Эти
                  действия не должны мешать нормальному ходу судебного
                  разбирательства.
                </p>
                <p>
                  11. Все распоряжения председательствующего по вопросам
                  применения в зале судебного разбирательства аудио-,
                  видеозаписи указываются в протоколе судебного заседания.
                </p>
                <p>
                  Исходя из положений статьи 57 УПК о том, что распоряжения
                  председательствующего в судебном заседании обязательны для
                  всех участников процесса и иных лиц, присутствующих в зале
                  суда, его распоряжение о запрещении или прекращении аудио-,
                  видеозаписи должно быть исполнено.
                </p>
                <p>
                  Неподчинение распоряжениям председательствующего влечет
                  применение мер, предусмотренных статьей 346 УПК, а в случаях
                  нарушения конституционных прав на неприкосновенность частной
                  жизни, личной и семейной тайны, виновные могут быть привлечены
                  к установленной законом ответственности.
                </p>
                <p>
                  12. В соответствии с частью третьей статьи 415 и частью второй
                  статьи 448 УПК постановления суда по вопросу гласности
                  судебного разбирательства, принятые в ходе судебного
                  заседания, отдельному обжалованию и опротестованию не
                  подлежат. Возражения против них могут быть изложены в
                  апелляционных жалобах, протестах, принесенных на приговор.
                </p>
                <p>
                  13. Апелляционной, кассационной инстанциям при рассмотрении
                  дел необходимо обращать внимание на выполнение нижестоящими
                  судами требований закона о гласности судебного
                  разбирательства, по каждому факту нарушения реагировать
                  должным образом.
                </p>
                <p>
                  14. Согласно статье 4 Конституции Республики Казахстан
                  настоящее нормативное постановление включается в состав
                  действующего права, а также является общеобязательным и
                  вводится в действие со дня его официального опубликования.
                </p>
              </div>
            </div>
          </div>

          <div class="accordion-item child nested hidden">
            <p class="parent" @click="changeToActive">
              НОРМАТИВНОЕ ПОСТАНОВЛЕНИЕ ВЕРХОВНОГО СУДА О СОБЛЮДЕНИИ ПРИНЦИПА
              ГЛАСНОСТИ СУДОПРОИЗВОДСТВА ПО ГРАЖДАНСКИМ ДЕЛАМ
            </p>
            <div class="accordion-item child hidden">
              <div class="child text">
                <p>
                  1. Принцип гласности судебного разбирательства, закрепленный в
                  статье 19 Гражданского процессуального кодекса Республики
                  Казахстан (далее - ГПК), означает, что разбирательство
                  гражданских дел во всех судах осуществляется открыто с
                  обеспечением свободного доступа в зал заседаний всех граждан,
                  в том числе, представителей средств массовой информации, от
                  начала до конца во всех стадиях гражданского процесса (в судах
                  первой, апелляционной, кассационной, надзорной инстанций), их
                  право лично воспринимать все происходящее в зале судебного
                  заседания, а также по своему усмотрению делать письменные
                  заметки, осуществлять стенографирование или производить
                  аудиозапись. Гласность также предусматривает с соблюдением
                  условий, указанных в законе, трансляцию судебного заседания,
                  производство кино-фотосъемки и видеозаписи.
                </p>
                <p>
                  2. Ограничение гласности в гражданском процессе возможно
                  только в случаях, указанных в законе, в интересах охраны
                  государственной тайны и в целях соблюдения предусмотренных
                  статьей 18 Конституции Республики Казахстан прав каждого на
                  неприкосновенность частной жизни, личную и семейную тайну,
                  защиту своей чести и достоинства, на тайну вкладов,
                  сбережений, переписки, телефонных переговоров, почтовых,
                  телеграфных и иных сообщений.
                </p>
                <p>
                  3. Под гласностью судебного разбирательства следует понимать
                  не только проведение открытого судебного разбирательства дела,
                  но и открытость, прозрачность и доступность судебных процедур,
                  т.е. публичное объявление судебного решения, ознакомление
                  сторон с поступившими жалобами других участников процесса,
                  извещение о времени и месте рассмотрения дела в суде
                  вышестоящей инстанции, свободный доступ к базе данных
                  вступивших в законную силу решений, возможность получения
                  информации об исполнении судебных актов.
                </p>
                <p>
                  4. Доступ граждан в зал открытого судебного заседания не может
                  быть ограничен ни под каким предлогом, за исключением лиц
                  моложе шестнадцати лет, если они не являются лицами,
                  участвующими в деле, или свидетелями. Вопрос допуска всех
                  желающих граждан в открытое судебное заседание разрешается с
                  учетом вместимости зала судебного заседания.
                </p>
                <p>
                  5. Лица, участвующие в деле, и присутствующие граждане вправе
                  в открытом судебном заседании фиксировать с занимаемых ими в
                  зале мест, письменно или с использованием аудиозаписи, ход
                  судебного разбирательства, для чего не требуется
                  предварительное разрешение председательствующего и согласие
                  участвующих в деле лиц. При этом указанные лица должны
                  пользоваться правом на ведение аудиозаписи открыто.
                </p>
                <p>
                  6. В начале судебного заседания выясняется мнение участвующих
                  в деле лиц о возможности трансляции судебного заседания по
                  радио и телевидению, производства кинофотосъемки и
                  видеозаписи, с отражением в протоколе судебного заседания.
                </p>
                <p>
                  Несогласие участвующих в деле лиц с проведением указанных
                  действий в отношении них должно быть мотивированным.
                </p>
                <p>
                  7. В соответствии со статьей 179 ГПК лицам, нарушающим порядок
                  во время разбирательства дела, председательствующий от имени
                  суда вправе сделать предупреждение, а за повторное нарушение
                  порядка присутствующие граждане удаляются из зала заседания.
                  Эти правила распространяются и в отношении лиц, производящих
                  кинофотосъемку, видеозапись, так как эти действия не должны
                  мешать ходу судебного заседания. Суд может вводить временные
                  ограничения производства кинофотосъемки и видеозаписи.
                </p>
                <p>
                  Предусмотренное в части шестой статьи 179 ГПК право на
                  удаление из зала судебного заседания всех граждан, не
                  участвующих в деле, и рассмотрение дела в закрытом заседании,
                  суд может применить только при массовом нарушении порядка
                  присутствующими при разбирательстве дела гражданами.
                </p>
                <p>
                  8. В соответствии со статьей 202 ГПК в открытом судебном
                  заседании личная переписка и личные телеграфные сообщения
                  граждан могут быть оглашены только с согласия лиц, между
                  которыми происходили эти переписка и телеграфные сообщения.
                  Данное правило применяется и при исследовании фото - и
                  кинодокументов, звуко - и видеозаписей, а также сообщений,
                  полученных при помощи иных технических средств, содержащих
                  сведения личного характера.
                </p>
                <p>
                  Если лица, интересы которых затрагивают переписка и
                  телеграфные сообщения, не дают согласия на их оглашение и
                  исследование, то указанные действия совершаются в закрытом
                  судебном заседании.
                </p>
                <p>
                  9. Проведение разбирательства дела в закрытом судебном
                  заседании допускается только по основаниям, предусмотренным
                  законом.
                </p>
                <p>
                  В соответствии с частью второй статьи 19 ГПК в закрытом
                  судебном заседании осуществляется разбирательство дел, включая
                  объявление решений, содержащих сведения, являющиеся
                  государственными секретами. К ним относятся дела, в которых
                  содержатся в соответствии с Законом Республики Казахстан от 15
                  марта 1999 года  «О государственных секретах»
                  защищаемые государством сведения, составляющие государственную
                  и служебную тайны, распространение которых ограничено в целях
                  осуществления эффективной военной, экономической,
                  научно-технической, внешнеэкономической, внешнеполитической,
                  разведывательной, контрразведывательной, оперативно-розыскной
                  и иной деятельности, не вступающей в противоречие с
                  общепринятыми нормами международного права.
                </p>
                <p>
                  Кроме того, дело рассматривается в закрытом судебном
                  заседании, включая объявление решений в случае,
                  предусмотренном частью шестой статьи 179 ГПК, а также при
                  удовлетворении ходатайства участвующего в деле лица,
                  ссылающегося на необходимость обеспечения тайны усыновления,
                  сохранения личной, семейной, коммерческой или иной охраняемой
                  законом тайны, сведений об интимных сторонах жизни граждан
                  либо на иные обстоятельства, препятствующие открытому
                  разбирательству. Лица, не являющиеся участниками процесса, не
                  вправе заявлять суду ходатайство о рассмотрении дела в
                  закрытом судебном заседании.
                </p>
                <p>
                  10. Суд, заслушав мнения участников процесса по заявленному
                  ходатайству об ограничении гласности судебного
                  разбирательства, выносит мотивированное определение о
                  разбирательстве дела в закрытом судебном заседании.
                </p>
                <p>
                  Поскольку ограничение принципа гласности судебного
                  разбирательства может распространяться на весь период
                  судебного заседания либо только на ту его часть, в которой
                  исследуются обстоятельства, указанные в части второй статьи 19
                  ГПК, об этом должно быть указано в определении суда.
                </p>
                <p>
                  11. Нарушения процессуального законодательства, связанные с
                  соблюдением принципа гласности, могут служить основанием к
                  отмене судебного акта, если они привели или могли привести к
                  неправильному разрешению дела.
                </p>
                <p>
                  12. В соответствии с частью второй статьи 344 ГПК на
                  определение суда по вопросу ограничения принципа гласности
                  судебного разбирательства, частные жалобы и протест не
                  подаются, но возражения сторон на это определение могут быть
                  включены в апелляционную жалобу или протест, поданные на
                  решение суда, вынесенное по существу дела.
                </p>
                <p>
                  13. Согласно статье 4 Конституции Республики Казахстан
                  настоящее нормативное постановление включается в состав
                  действующего права, а также является общеобязательным и
                  вводится в действие со дня официального опубликования.
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>

      <!-- ФОТО И ВИДЕО СЪЕМКА -->
      <div id="photo_video" class="accordion-item">
        <p class="parent" @click="changeToActive">ФОТО И ВИДЕО СЪЕМКА</p>

        <div class="accordion-item child nested hidden">
          <p class="parent" @click="changeToActive">
            ПРАВО НА СОБСТВЕННОЕ ИЗОБРАЖЕНИЕ
          </p>
          <div class="accordion-item child hidden">
            <div class="child text">
              <p>
                Опубликование, воспроизведение и распространение
                изобразительного произведения (картина, фотография, кинофильм и
                другие), в котором есть изображение другого лица, допускается
                только с его согласия, а после его смерти - с согласия детей и
                пережившего супруга. Будьте особенно внимательны, если вы
                используете изображение несовершеннолетних (для этого требуется
                согласие их законных представителей (родители, опекуны,
                попечители).
              </p>
              <p><strong>Согласие не требуется, если:</strong></p>
              <ul>
                <li>изображенное лицо позировало за плату;</li>
                <li>
                  лицо присутствует или участвует в зрелищных
                  культурно-массовых, социально значимых в области культуры,
                  спортивно-массовых мероприятиях, собраниях, митингах, шествиях
                  или демонстрациях и иных публичных мероприятиях;
                </li>
                <li>
                  распространяемая информация содержит изображение лица и
                  сведения, связанные со служебной и публичной деятельностью
                  данного лица, а также опубликована самим лицом, его законным
                  представителем или уполномоченным лицом в источниках, доступ к
                  которым не ограничен;
                </li>
                <li>
                  использование изображаемого лица осуществляется в целях защиты
                  конституционного строя, охраны общественного порядка, прав и
                  свобод человека, здоровья и нравственности населения.
                </li>
              </ul>
              <p><strong>Форма согласия</strong></p>
              <p>
                Закон не предусматривает определенную форму, поэтому согласие
                может быть как в устном, так и в письменном виде, а также в виде
                видеозаписи. Предпочтительным и наиболее безопасным является
                письменное согласие.
              </p>
              <p><strong>Образец письменного согласия</strong></p>
              <p>
                Я, (ФИО, ИИН, номер удостоверяющего документа), даю согласие на
                использование моего изображения редакции (наименование
                юридического лица-редакции СМИ)  «_______».
              </p>
              <p>Любые иные условия, которые вы считаете необходимыми.</p>
              <p>Подпись и дата.</p>
              <p><strong>Ответственность</strong></p>
              <p>
                Физическое лицо, чьи права были нарушены, имеет право на
                компенсацию морального вреда, удаление опубликованных фото и
                видеоматериалов.
              </p>
            </div>
          </div>
        </div>

        <div class="accordion-item child nested hidden">
          <p class="parent" @click="changeToActive">
            ПРАВИЛА ПРОВЕДЕНИЯ СЪЕМКИ: ГДЕ И КАК
          </p>
          <div class="accordion-item child hidden">
            <div class="child text">
              <p>
                <strong
                  >ВАЖНО! Правила относятся не только к съемке, но и к
                  распространению изображений (исключение составляют режимные
                  объекты).</strong
                >
              </p>
              <p>
                <strong
                  >Частная собственность (рестораны, кафе, торговые центры,
                  рынки, салоны, кинотеатры и т.п.)</strong
                >
              </p>
              <p>
                Запрещается продавцу (изготовителю, исполнителю), за исключением
                случаев, предусмотренных законодательством РК, ограничивать
                права потребителей в отношении применения фото-, видеосъемки
                (закон о Защите прав потребителей). Что касается квартир, дач,
                земельных участков, то собственник может запретить вести съемку.
              </p>
              <p><strong>Государственные организации </strong></p>
              <p>
                В государственных учреждениях фотографировать можно. Запреты на
                съемку могут ввести, если есть угроза нарушения общественного
                порядка, что должно быть отражено в правилах посещения для
                посетителей. В зданиях государственных учреждений с особым
                пропускным режимом для ведения съемки необходимо специальное
                разрешение. Учреждения, которые оказывают услуги населению
                (например, ЦОН) должны действовать максимально открыто.
              </p>
              <p><strong>Суды</strong></p>
              <p>
                В самом здании суда проведение фото и видеосъемки возможно. Что
                касается кино- и фотосъемки, видеозаписи, прямой радио- и
                телетрансляции в ходе судебного разбирательства, то они
                допускаются по решению суда с учетом мнения лиц, участвующих в
                деле. (подробнее <span class="highlight"> «Освещение судебного процесса»</span>).
              </p>
              <p>
                <strong
                  >Режимные объекты и особо охраняемые объекты (резиденция
                  президента РК, воинские части, объекты с государственными
                  материальными резервами, объекты космической
                  инфраструктуры)</strong
                >
              </p>
              <p>Запрещается съемка и распространение</p>
              <p><strong>Медицинские учреждения</strong></p>
              <p>
                Запрета на съемку в медицинских учреждениях не существует. Но
                важно помнить о праве гражданина на неприкосновенность частной
                жизни и о врачебной тайне. К примеру, проведение съёмки в
                палате, где находятся больные, может нарушить тайну частной
                жизни, поэтому перед тем, как снимать, необходимо получить
                разрешение от пациентов. (подробнее <span class="highlight"> «Тайны и
                секреты»</span>).
              </p>
              <p>
                <strong
                  >Исправительные учреждения и следственные изоляторы</strong
                >
              </p>
              <p>
                Снимать можно только с разрешения администрации. Для съемки
                осужденного необходимо его персональное согласие.
              </p>
              <p>
                <strong
                  >Стратегические объекты (магистральные железнодорожные сети,
                  морские порты, объекты использования атомной энергии, объекты
                  космической отрасли и др).
                </strong>
              </p>
              <p>
                Для проведения специальной съемки стратегических объектов
                требуется разрешение администрации объекта.
              </p>
              <p>
                <strong
                  >Природоохранные учреждения (заповедники, национальные парки и
                  др.)</strong
                >
              </p>
              <p>
                Проведение посетителями профессиональной и любительской
                фото-видеосъемки допускается в зависимости от вида особо
                охраняемой территории и установленного правового режима. Не
                разрешается фотографировать животных с применением штативов и
                вспышек (это необходимо для сохранения животных и личной
                безопасности посетителей).
              </p>
              <p>
                <strong
                  >Фото- и видеозапись действий сотрудников полиции</strong
                >
              </p>
              <p>
                Если распространяемая информация содержит изображение лица и
                сведения, связанные с его служебной и публичной деятельностью,
                то проведение съёмки не запрещается. Но сотрудник полиции может
                потребовать прекращения съемки, если это препятствует выполнению
                служебных обязанностей (например, на месте совершения
                преступления, когда нарушаются законные интересы физических,
                юридических лиц, права и свободы субъекта либо это может
                нарушить следственную тайну).
              </p>
              <p>
                <strong
                  >ВАЖНО! Попытка представителя правоохранительных органов
                  насильственным образом удалить сделанные вами кадры, а также
                  изъять флеш-карту, камеру, диктофон, является посягательством
                  на частную собственность и являются воспрепятствованием
                  профессиональной деятельности журналиста.</strong
                >
              </p>
              <p>
                <strong>Проведение съемки  «скрытой камерой»</strong>
              </p>
              <p>
                Законы не содержат норм, которые регулируют съемку
                &ldquo;скрытой камерой&rdquo;, и журналисту часто приходится
                действовать на свой страх и риск. Как избежать обвинений в
                злоупотреблении свободой слова и в каких случаях использование
                 «скрытой камеры» оправдано?
              </p>
              <p>
                Сьемка  «скрытой камерой» оправдана, когда материал
                представляет широкий интерес и обществу необходимо знать об
                этом. Для этого нужны следующие условия:
              </p>
              <ul>
                <li>
                  материал не должны нарушать конституционные права и свободы
                  граждан;
                </li>
                <li>
                  съемка должна быть произведена для защиты интересов общества
                  или государства, например, борьба с коррупцией;
                </li>
                <li>
                  посторонние лица и случайно попавшие в кадр не должны быть
                  узнаваемы.
                </li>
              </ul>
              <p><strong>Использование съемок с камер наблюдения</strong></p>
              <p>
                Если вы используете съемку с камеры видеонаблюдения для
                массового распространения и демонстрируете кадры с места
                преступления, у вас должно быть разрешение от правоохранительных
                органов.
              </p>
            </div>
          </div>
        </div>

