            <div class="accordion-item child hidden">
              <div class="child text">
                <p>
                  <strong>Статья 145. Право на собственное изображение</strong>
                </p>
                <p>
                  1. Никто не имеет право использовать изображение какого-либо
                  лица без его согласия, а в случае смерти - без согласия
                  наследников.
                </p>
                <p>
                  2. Опубликование, воспроизведение и распространение
                  изобразительного произведения (картина, фотография, кинофильм
                  и другие), в котором изображено другое лицо, допускается лишь
                  с согласия изображенного, а после его смерти с согласия его
                  детей и пережившего супруга. Такого согласия не требуется,
                  если это установлено законодательными актами, либо
                  изображенное лицо позировало за плату.
                </p>
              </div>
            </div>
