            <div class="accordion-item child hidden">
              <div class="child text">
                <p>
                  <strong
                    >Статья 14. Распространение продукции средства массовой
                    информации</strong
                  >
                </p>
                <p>
                  1-1. Средствам массовой информации при подготовке,
                  опубликовании, воспроизведении и распространении продукции
                  средств массовой информации не требуется согласие
                  изображаемого лица в следующих случаях:
                </p>
                <p>
                  1) если данное лицо присутствует или участвует в зрелищных
                  культурно-массовых, социально значимых в области культуры,
                  спортивно-массовых мероприятиях, собраниях, митингах, шествиях
                  или демонстрациях и иных публичных мероприятиях;
                </p>
                <p>
                  2) если распространяемая информация содержит изображение лица
                  и сведения, связанные со служебной и (или) публичной
                  деятельностью данного лица, а также опубликована самим лицом,
                  его законным представителем или уполномоченным лицом в
                  источниках, доступ к которым не ограничен;
                </p>
                <p>
                  3) если использование изображаемого лица осуществляется в
                  целях защиты конституционного строя, охраны общественного
                  порядка, прав и свобод человека, здоровья и нравственности
                  населения.
                </p>
