            <div class="accordion-item child hidden">
              <div class="child text">
                <p>
                  Стратегическим объектом является имущество, имеющее
                  социально-экономическое значение для устойчивого развития
                  казахстанского общества, владение и пользование и
                  распоряжение, которым будут оказывать влияние на состояние
                  национальной безопасности РК.
                </p>
                <p>
                  К стратегическим объектам могут быть отнесены: магистральные
                  железнодорожные сети; магистральные трубопроводы; национальная
                  электрическая сеть; магистральные линии связи; объекты
                  телерадиовещания (производственно-технологический комплекс
                  наземных и спутниковых систем телевизионного и радиовещания);
                  нефтеперерабатывающие производства; энергопроизводящие объекты
                  мощностью не менее 50 мегаватт; национальная почтовая сеть;
                  международные аэропорты; морские порты, имеющие статус
                  международного значения; аэронавигационные устройства системы
                  управления воздушным движением; устройства и навигационные
                  знаки, регулирующие и гарантирующие безопасность судоходства;
                  объекты использования атомной энергии; объекты космической
                  отрасли; водохозяйственные сооружения; автомобильные дороги
                  общего пользования; а также пакеты акций (доли участия, паи) в
                  юридических лицах, в собственности которых находятся
                  стратегические объекты, пакеты акций (доли участия, паи)
                  физических и юридических лиц, которые имеют возможность прямо
                  или косвенно определять решения или оказывать влияние на
                  принимаемые решения юридических лиц, в собственности которых
                  находятся стратегические объекты.
                </p>
              </div>
            </div>
