            ВОЗБУЖДЕНИЕ СОЦИАЛЬНОЙ, НАЦИОНАЛЬНОЙ, РОДОВОЙ, РАСОВОЙ, СОСНОВНОЙ
            ИЛИ РЕЛИГИОЗНОЙ РОЗНИ
          </p>
          <div class="accordion-item child hidden">
            <div class="child text">
              <p>
                Статья 20 Конституции РК гласит, что не допускается пропаганда и
                агитация социального, национального, религиозного, сословного и
                родового превосходства. Для реализации этой нормы была принята
                статья 174 Уголовного Кодекса РК.
              </p>
              <p>
                включает в себя умышленные действия, направленные на возбуждение
                социальной, национальной, родовой, расовой, сословной или
                религиозной розни, на оскорбление национальной чести и
                достоинства либо религиозных чувств граждан, а равно пропаганда
                исключительности, превосходства либо неполноценности граждан по
                признаку их отношения к религии, сословной, национальной,
                родовой или расовой принадлежности, если эти деяния совершены
                публично или с использованием средств массовой информации или
                сетей телекоммуникаций, либо путем изготовления или
                распространения литературы или иных носителей информации,
                пропагандирующих социальную, национальную, родовую, расовую,
                сословную или религиозную рознь.
              </p>
              <p>
                Вторая часть статьи 174 предусматривает те же действия,
                совершенные группой лиц, группой лиц по предварительному сговору
                или неоднократно или соединенные с насилием либо угрозой его
                применения, а равно совершенные лицом с использованием своего
                служебного положения либо лидером общественного объединения.
                Дополнительно появляется понятие  «преступление,
                совершенное  «лидером общественного объединения». Что
                значит это понятие согласно Комментария к Уголовному Кодексу,
                лидер общественного объединения - лицо, способное посредством
                своего влияния и авторитета единолично оказывать воздействие на
                деятельность этого общественного объединения.
              </p>
              <p>
                Третья часть статьи предусматривает ответственность за деяние,
                совершенное преступной группой, либо повлекшее за собой тяжкие
                последствия.
              </p>
              <p><strong>ОСОБЕННОСТИ СТАТЬИ</strong></p>
              <p>
                Ответсвенность наступает в том случае, если информация
