              <p>
                б) с использованием средств СМИ или
                информационно-коммуникационный сетей.
              </p>
              <p>
                в) путем изготовления литературы и иных носителей информации,
                пропагандирующих рознь.
              </p>
              <p>
                г) путем распространения литературы или иных носителей
                информации, пропагандирующих рознь.
              </p>
