          <div class="accordion-item child hidden">
            <div class="child text">
              <p>
                <strong>Экстремизм - </strong>приверженность к крайним взглядам
                и мерам. По закону экстремизм &ndash; это организация и (или)
                совершение физическим и (или) юридическим лицом, объединением
                физических и (или) юридических лиц действий, преследующих
