                участие в нем, разжигание социальной, сословной розни ;
              </p>
              <p>
                разжигание расовой, национальной и родовой розни, в том числе
                связанной с насилием или призывами к насилию ;
              </p>
              <p>
                разжигание религиозной вражды или розни, в том числе связанной с
                насилием или призывами к насилию, а также применение любой
                религиозной практики, вызывающей угрозу безопасности, жизни,
                здоровью, нравственности или правам и свободам граждан ;
              </p>
              <p><strong>Виды экстремизма:</strong></p>
              <p>Политический</p>
              <p>Национальный</p>
              <p>Религиозный</p>
              <p>
                <strong>Политический экстремизм</strong> - насильственное
                изменение конституционного строя, нарушение суверенитета РК
                целостности, неприкосновенности и неотчуждаемости ее территории,
                подрыв национальной безопасности и обороноспособности
                государства, насильственный захват власти или насильственное
                удержание власти, создание, руководство и участие в незаконном
                военизированном формировании, организация вооруженного мятежа и
                участие в нем, разжигание социальной, сословной розни.
              </p>
              <p>
                <strong>Национальный экстремизм</strong> - разжигание расовой,
                национальной и родовой розни, в том числе связанной с насилием
                или призывами к насилию.
              </p>
              <p>
                <strong>Религиозный экстремизм</strong> - разжигание религиозной
                вражды или розни, в том числе связанной с насилием или призывами
                к насилию, а также применение любой религиозной практики,
                вызывающей угрозу безопасности, жизни, здоровью, нравственности
                или правам и свободам граждан.
              </p>
              <p>
                Уголовный кодекс РК выделяет особую категорию преступлений
                &ndash; экстремистские преступления. Почти все они относятся к
                категории преступлений средней тяжести, тяжких и особо тяжких,
                поэтому влекут соответствующее наказание. (подробнее
                <span class="highlight"> «Законодательная база»</span>).
              </p>
            </div>
          </div>
        </div>

        <div class="accordion-item child nested hidden">
          <p class="parent" @click="changeToActive">
            ПРИЗНАКИ ЭКСТРМИСТСКИХ МАТЕРИАЛОВ
          </p>
          <div class="accordion-item child hidden">
            <div class="child text">
              <p>
                Признание организации и (или) информационных материалов
                экстремистскими осуществляется судами общей юрисдикции в рамках
                особого производства. Особое производство диктует рассмотрение
                дела в краткие сроки и по упрощенной процедуре. Гражданским
                процессуальным кодексом предусмотрено, что судья проводит
                подготовку дела к судебному разбирательству в течение десяти
                рабочих дней со дня принятия заявления в производство суда.
                Продление этого срока не допускается. Кроме этого, дела особого
                производства суд рассматривает с участием заявителя и
                заинтересованных лиц, но отсутствие ответчиков не является
                препятствием для рассмотрения дела и вынесения решения по нему.
              </p>
              <p>
                Заявление о признании организации, осуществляющей экстремизм на
                территории РК и (или) другого государства, экстремистской, в том
                числе об установлении изменения ею своего наименования, а также
                о признании информационных материалов, ввозимых, издаваемых,
                изготавливаемых и (или) распространяемых на территории
                Республики Казахстан, экстремистскими, подается прокурором в суд
                по месту нахождения прокурора, заявившего такие требования, или
                по месту обнаружения таких материалов.
              </p>
              <p>
                В заявлении должны быть изложены обстоятельства, подтверждающие
                факт осуществления организацией на территории Республики
                Казахстан и (или) другого государства деятельности, которая
                могла бы быть признана экстремистской или террористической в
                соответствии с законодательством Республики Казахстан, либо факт
                изменения ею своего наименования, а также наличия в
                информационных материалах признаков и (или) призывов к
                экстремизму или терроризму.
              </p>
              <p>
                Как правило, при подаче заявления о признании информационных
                материалов экстремистскими, органы прокуратуры подтверждают свое
                заявление экспертным заключением по данным материалам. Поскольку
                чаще всего подобные дела заявления рассматриваются в отсутствии
                ответчика, оспорить результаты и выводы эксперта просто некому.
              </p>
              <p>
                Суд, признав организацию экстремистской и (или) информационные
                материалы, ввозимые, издаваемые, изготавливаемые и (или)
                распространяемые на территории Республики Казахстан,
                экстремистскими, выносит решение о запрещении деятельности и
                ликвидации организации и (или) запрещении ввоза, издания,
                изготовления и (или) распространения информационных материалов
                на территории Республики Казахстан, а также о конфискации и
                обращении в доход государства имущества организации.
              </p>
              <p>
                Решение суда служит основанием для включения сведений об этом в
                систему специальных учетов Комитета по правовой статистике
                Генеральной прокуратуры РК.
              </p>
              <p>
                Если информационные материалы, признанные в судебном порядке,
                экстремистскими, опубликованы в СМИ или на интернет-ресурсах, то
                их деятельность может быть прекращена, доступ к интернет-ресурсу
                может быть заблокирован для пользователей с территории
                Казахстана также по решению суда.
              </p>
              <p>
                К сожалению, в Казахстане нет реестра сайтов, доступ к которым
                был заблокирован из-за публикации материалов экстремистского
                характера. (подробнее <span class="highlight"> «Блокировки»</span>)
              </p>
            </div>
          </div>
        </div>

        <div class="accordion-item child nested hidden">
          <p class="parent" @click="changeToActive">ПОНЯТИЙНЫЙ АППАРАТ</p>
          <div class="accordion-item child hidden">
            <div class="child text">
              <p>
                <strong>Экстремизм</strong> - организация и (или) совершение:
              </p>
              <p>
                физическим и (или) юридическим лицом, объединением физических и
                (или) юридических лиц действий от имени организаций, признанных
                в установленном порядке экстремистскими;
              </p>
              <p>
                физическим и (или) юридическим лицом, объединением физических и
                (или) юридических лиц действий, преследующих следующие
                экстремистские цели:
              </p>
              <p>
                насильственное изменение конституционного строя, нарушение
                суверенитета Республики Казахстан, целостности,
                неприкосновенности и неотчуждаемости ее территории, подрыв
                национальной безопасности и обороноспособности государства,
                насильственный захват власти или насильственное удержание
                власти, создание, руководство и участие в незаконном
                военизированном формировании, организация вооруженного мятежа и
                участие в нем, разжигание социальной, сословной розни
                (политический экстремизм);
              </p>
              <p>
                разжигание расовой, национальной и родовой розни, в том числе
                связанной с насилием или призывами к насилию (национальный
                экстремизм);
              </p>
              <p>
                разжигание религиозной вражды или розни, в том числе связанной с
                насилием или призывами к насилию, а также применение любой
                религиозной практики, вызывающей угрозу безопасности, жизни,
                здоровью, нравственности или правам и свободам граждан
                (религиозный экстремизм);
              </p>
              <p>
                <strong>Противодействие экстремизму</strong> - деятельность
                государственных органов, направленная на защиту прав и свобод
                человека и гражданина, основ конституционного строя, обеспечение
                целостности и национальной безопасности Республики Казахстан от
                экстремизма, предупреждение, выявление, пресечение экстремизма и
                ликвидацию его последствий, а также выявление и устранение
                причин и условий, способствующих осуществлению экстремизма;
              </p>
              <p>
                <strong>Финансирование экстремизма</strong> - предоставление или
                сбор денег и (или) иного имущества, права на имущество или выгод
                имущественного характера, а также дарение, мена, пожертвования,
                благотворительная помощь, оказание информационных и иного рода
                услуг либо оказание финансовых услуг физическому лицу либо
                группе лиц, либо юридическому лицу, совершенные лицом, заведомо
                осознававшим экстремистский характер их деятельности либо то,
                что предоставленное имущество, оказанные информационные,
                финансовые и иного рода услуги будут использованы для
                осуществления экстремизма либо обеспечения экстремистской
                группы, экстремистской организации, незаконного военизированного
                формирования;
              </p>
              <p>
                <strong>Профилактика экстремизма</strong> - система правовых,
                организационных, воспитательных, пропагандистских и иных мер,
                направленных на предупреждение экстремизма;
              </p>
              <p>
                <strong>Экстремистские действия</strong> - непосредственная
                реализация действий в экстремистских целях, включая публичные
                призывы к совершению таких действий, пропаганду, агитацию и
                публичное демонстрирование символики экстремистских организаций;
              </p>
              <p>
                <strong>Организация экстремистских действий -</strong>
                руководство экстремистскими действиями, финансирование
                экстремизма, вербовка лиц, изготовление и приобретение средств и
                орудий в экстремистских целях, а также другие действия
                физических и (или) юридических лиц, объединений физических и
                (или) юридических лиц, направленные на создание условий для
                осуществления экстремизма;
              </p>
              <p>
                <strong>Экстремистские материалы</strong> - любые информационные
                материалы, содержащие признаки и (или) призывы к осуществлению
                экстремистских действий либо обосновывающие или оправдывающие
                необходимость их совершения;
              </p>
              <p>
                <strong>Экстремистская организация</strong> - юридическое лицо,
                объединение физических и (или) юридических лиц, осуществляющие
                экстремизм и признанные судом экстремистскими.
              </p>
              <p>
                <strong>Идеология насилия</strong> - система общественных
                теорий, взглядов и идей, оправдывающих насилие, в том числе с
                применением террористических методов и средств для достижения
                политических, религиозных, идеологических и иных целей;
              </p>
              <p>
                <strong>Терроризм </strong>- идеология насилия и практика
                воздействия на принятие решения государственными органами,
                органами местного самоуправления или международными
                организациями путем совершения либо угрозы совершения
                насильственных и (или) иных преступных действий, связанных с
                устрашением населения и направленных на причинение ущерба
                личности, обществу и государству;
              </p>
              <p>
                <strong>Акт терроризма</strong> - совершение или угроза
                совершения взрыва, поджога или иных действий, создающих
                опасность гибели людей, причинения значительного имущественного
                ущерба либо наступления иных общественно опасных последствий,
                если эти действия совершены в целях нарушения общественной
                безопасности, устрашения населения либо оказания воздействия на
                принятие решений государственными органами Республики Казахстан,
                иностранными государствами или международными организациями, а
                также посягательство на жизнь человека, совершенное в тех же
                целях, а равно посягательство на жизнь государственного или
                общественного деятеля, совершенное в целях прекращения его
                государственной или иной политической деятельности либо из мести
                за такую деятельность;
              </p>
              <p>
                <strong>Призыв к совершению акта терроризма -</strong>
                обращение, выраженное публично или отраженное в распространяемом
                информационном материале, воздействующее на сознание, волю и
                поведение физического лица с целью побуждения его на совершение
                акта терроризма;
              </p>
              <p>
                <strong>Антитеррористическая операция</strong> - комплекс
                специальных мероприятий с применением физической силы, служебных
                собак, боевой и иной техники, оружия и специальных средств по
                предупреждению, пресечению акта терроризма, обезвреживанию
                террористов, взрывных устройств, обеспечению безопасности
                физических лиц и организаций, а также по минимизации и (или)
                ликвидации последствий акта терроризма.
              </p>
              <p>
                <strong>Финансирование терроризма</strong> - предоставление или
                сбор денег и (или) иного имущества, права на имущество или выгод
                имущественного характера, а также дарение, мена, пожертвования,
                благотворительная помощь, оказание информационных и иного рода
                услуг либо оказание финансовых услуг физическому лицу либо
                группе лиц, либо юридическому лицу, совершенные лицом, заведомо
                осознававшим террористический характер их деятельности либо то,
                что предоставленное имущество, оказанные информационные,
                финансовые и иного рода услуги будут использованы для
                осуществления террористической деятельности либо обеспечения
                террористической группы, террористической организации,
                незаконного военизированного формирования;
              </p>
              <p>
                <strong>Профилактика терроризма</strong> - комплекс правовых,
                экономических, социальных, организационных, воспитательных,
                пропагандистских и иных мер, осуществляемых государственными и
                местными исполнительными органами по выявлению, изучению,
                устранению причин и условий, способствующих возникновению и
                распространению терроризма;
              </p>
              <p>
                <strong>Террорист -</strong> лицо, участвующее в осуществлении
                террористической деятельности;
              </p>
              <p>
                <strong>Террористические материалы</strong> - любые
                информационные материалы, содержащие информацию о способах и
                средствах совершения акта терроризма, а также признаки и (или)
                призывы к осуществлению террористической деятельности либо
                обосновывающие или оправдывающие необходимость осуществления
                такой деятельности;
              </p>
              <p>
                <strong>Террористическая группа</strong> - организованная
                группа, преследующая цель совершения одного или нескольких
                террористических преступлений;
              </p>
              <p>
                <strong>Террористическая организация</strong> - организация,
                осуществляющая террористическую деятельность либо признающая
                возможность использования в своей деятельности терроризма, в
                отношении которой принято и вступило в законную силу решение
                суда о признании ее террористической;
              </p>
              <p>
                <strong>Террористическая деятельность -</strong> совершение
                любых из нижеследующих деяний:
              </p>
              <p>
                организация, планирование, подготовка, финансирование и
                реализация акта терроризма;
              </p>
              <p>подстрекательство к акту терроризма;</p>
              <p>
                организация незаконного военизированного формирования,
                преступного сообщества (преступной организации), организованной
                группы в целях совершения акта терроризма, а равно участие в
                таких структурах;
              </p>
              <p>вербовка, вооружение, обучение и использование террористов;</p>
              <p>
                информационное или иное пособничество в организации,
                планировании, подготовке и совершении акта терроризма;
              </p>
              <p>
                пропаганда идей терроризма, распространение террористических
                материалов, в том числе с использованием средств массовой
                информации или сетей телекоммуникаций;
              </p>
              <p>
                оказание финансовой, правовой помощи или иное содействие
                террористам, а также организациям, деятельность которых признана
                террористической в соответствии с законодательством Республики
                Казахстан, с осознанием того, что указанные действия будут
                использованы для осуществления террористической деятельности
                либо обеспечения террористической организации.
              </p>
              <p>
                <strong
                  >Возбуждение социальной, национальной, родовой, расовой,
                  сословной или религиозной розни-</strong
                >
              </p>
              <p>
                Умышленные действия, направленные на возбуждение социальной,
                национальной, родовой, расовой, сословной или религиозной розни,
                на оскорбление национальной чести и достоинства либо религиозных
                чувств граждан, а равно пропаганда исключительности,
                превосходства либо неполноценности граждан по признаку их
                отношения к религии, сословной, национальной, родовой или
                расовой принадлежности, если эти деяния совершены публично или с
                использованием средств массовой информации или сетей
                телекоммуникаций, а равно путем изготовления или распространения
                литературы или иных носителей информации, пропагандирующих
                социальную, национальную, родовую, расовую, сословную или
                религиозную рознь.
              </p>
            </div>
          </div>
        </div>

        <div class="accordion-item child nested hidden">
          <p class="parent" @click="changeToActive">ЗАКОНОДАТЕЛЬНАЯ БАЗА</p>

          <div class="accordion-item child nested hidden">
            <p class="parent" @click="changeToActive">
              УГОЛОВНЫЕ ПРЕСТУПЛЕНИЯ, СВЯЗАННЫЕ С ЭКСТРЕМИЗМОМ
            </p>
            <div class="accordion-item child hidden">
              <div class="child text">
                <p>
                  Статья 174. Возбуждение социальной, национальной, родовой,
                  расовой, сословной или религиозной розни
                </p>
                <p>
                  1. Умышленные действия, направленные на возбуждение
                  социальной, национальной, родовой, расовой, сословной или
                  религиозной розни, на оскорбление национальной чести и
                  достоинства либо религиозных чувств граждан, а равно
                  пропаганда исключительности, превосходства либо
                  неполноценности граждан по признаку их отношения к религии,
                  сословной, национальной, родовой или расовой принадлежности,
                  если эти деяния совершены публично или с использованием
                  средств массовой информации или сетей телекоммуникаций, а
                  равно путем изготовления или распространения литературы или
                  иных носителей информации, пропагандирующих социальную,
                  национальную, родовую, расовую, сословную или религиозную
                  рознь, - наказываются ограничением свободы на срок от двух до
                  семи лет либо лишением свободы на тот же срок.
                </p>
                <p>
                  2. Те же действия, совершенные группой лиц, группой лиц по
                  предварительному сговору или неоднократно или соединенные с
                  насилием либо угрозой его применения, а равно совершенные
                  лицом с использованием своего служебного положения либо
                  лидером общественного объединения, в том числе с
                  использованием средств, полученных из иностранных источников,
                  - наказываются лишением свободы на срок от пяти до десяти лет
                  с лишением права занимать определенные должности или
                  заниматься определенной деятельностью на срок до трех лет или
                  без такового.
                </p>
                <p>
                  3. Деяния, предусмотренные частями первой или второй настоящей
                  статьи, совершенные преступной группой либо повлекшие тяжкие
                  последствия, наказываются лишением свободы на срок от
                  двенадцати до двадцати лет с лишением права занимать
                  определенные должности или заниматься определенной
                  деятельностью на срок до трех лет или без такового.
                </p>
                <p>Статья 180. Сепаратистская деятельность</p>
                <p>
                  1. Пропаганда или публичные призывы к нарушению унитарности и
                  целостности Республики Казахстан, неприкосновенности и
                  неотчуждаемости ее территории либо дезинтеграции государства,
                  а равно изготовление, хранение с целью распространения или
                  распространение материалов такого содержания - наказываются
                  штрафом в размере от одной тысячи до пяти тысяч месячных
                  расчетных показателей либо ограничением свободы на срок до
                  семи лет, либо лишением свободы на тот же срок.
                </p>
                <p>
                  2. Те же действия, совершенные лицом с использованием своего
                  служебного положения либо лидером общественного объединения,
                  либо с использованием средств массовой информации или сетей
                  телекоммуникаций, либо группой лиц или группой лиц по
                  предварительному сговору, в том числе с использованием
                  средств, полученных из иностранных источников, -
                </p>
                <p>
                  наказываются лишением свободы на срок от пяти до десяти лет.
                </p>
                <p>
                  3. Действия, совершенные с целью нарушения унитарности и
                  целостности Республики Казахстан, неприкосновенности и
                  неотчуждаемости ее территории либо дезинтеграции государства,
                  - наказываются лишением свободы на срок от десяти до
                  пятнадцати лет с лишением гражданства Республики Казахстан или
                  без такового.
                </p>
                <p>
                  Статья 182. Создание, руководство экстремистской группой или
                  участие в ее деятельности
                </p>
                <p>
                  1. Создание экстремистской группы, а равно руководство ею -
                </p>
                <p>
                  наказываются лишением свободы на срок от десяти до семнадцати
                  лет с конфискацией имущества.
                </p>
                <p>
                  2. Участие в деятельности экстремистской группы или в
                  совершаемых ею преступлениях -
                </p>
                <p>
                  наказывается лишением свободы на срок от восьми до двенадцати
                  лет с конфискацией имущества.
                </p>
                <p>
                  3. Деяния, предусмотренные частями первой или второй настоящей
                  статьи, совершенные лицом с использованием своего служебного
                  положения либо лидером общественного объединения, -
                </p>
                <p>
                  наказываются лишением свободы на срок от двенадцати до
                  семнадцати лет с конфискацией имущества, лишением права
                  занимать определенные должности или заниматься определенной
                  деятельностью на срок до пяти лет, лишением гражданства
                  Республики Казахстан или без такового.
                </p>
                <p>
                  Примечание. Лицо, добровольно прекратившее участие в
                  деятельности экстремистской группы, освобождается от уголовной
                  ответственности, если в его действиях не содержится состава
                  иного преступления.
                </p>
                <p>
                  Статья 183. Дача разрешения на публикацию в средствах массовой
                  информации экстремистских материалов
                </p>
                <p>
                  Дача разрешения на публикацию в печати и других средствах
                  массовой информации сведений и материалов, направленных на
                  разжигание национальной, родовой, расовой, социальной и
                  религиозной вражды, пропагандирующих сословную
                  исключительность, войну, содержащих призывы к насильственному
                  захвату власти, насильственному удержанию власти, подрыву
                  безопасности государства или насильственному изменению
                  конституционного строя, а равно нарушению территориальной
                  целостности Республики Казахстан, -
                </p>
                <p>
                  наказывается штрафом в размере до двухсот месячных расчетных
                  показателей либо исправительными работами в том же размере,
                  либо привлечением к общественным работам на срок до двухсот
                  часов, либо арестом на срок до пятидесяти суток, с лишением
                  права занимать определенные должности или заниматься
                  определенной деятельностью на срок до двух лет или без
                  такового.
                </p>
                <p>Статья 184. Диверсия</p>
                <p>
                  Совершение с целью подрыва безопасности и обороноспособности
                  Республики Казахстан взрыва, поджога или иных действий,
                  направленных на массовое уничтожение людей, причинение вреда
                  их здоровью, разрушение или повреждение предприятий,
                  сооружений, путей и средств сообщения, средств связи, объектов
                  жизнеобеспечения населения, а равно совершение в тех же целях
                  массовых отравлений или распространение эпидемий и эпизоотий
                  &ndash;
                </p>
                <p>
                  наказываются лишением свободы на срок от пятнадцати до
                  двадцати лет либо пожизненным лишением свободы с лишением
                  гражданства Республики Казахстан или без такового, либо
                  смертной казнью, с конфискацией имущества.
                </p>
                <p>
                  Статья 258. Финансирование террористической или экстремистской
                  деятельности и иное пособничество терроризму либо экстремизму
                </p>
                <p>
                  1. Предоставление или сбор денег и (или) иного имущества,
                  права на имущество или выгод имущественного характера, а также
                  дарение, мена, пожертвования, благотворительная помощь,
                  оказание информационных и иного рода услуг либо оказание
                  финансовых услуг физическому лицу либо группе лиц, либо
                  юридическому лицу, совершенные лицом, заведомо осознававшим
                  террористический или экстремистский характер их деятельности
                  либо то, что предоставленное имущество, оказанные
                  информационные, финансовые и иного рода услуги будут
                  использованы для осуществления террористической или
                  экстремистской деятельности либо обеспечения террористической
                  или экстремистской группы, террористической или экстремистской
                  организации, незаконного военизированного формирования, -
                </p>
                <p>
                  наказываются лишением свободы на срок от пяти до девяти лет с
                  конфискацией имущества.
                </p>
                <p>
                  2. Те же деяния, совершенные неоднократно или лицом с
                  использованием своего служебного положения либо лицом,
                  выполняющим управленческие функции в коммерческой или иной
                  организации, либо лидером общественного объединения, либо
                  группой лиц по предварительному сговору, либо в крупном
                  размере, -
                </p>
                <p>
                  наказываются лишением свободы на срок от семи до двенадцати
                  лет с конфискацией имущества.
                </p>
                <p>
                  Примечание. Лицо, осуществляющее финансирование
                  террористической или экстремистской деятельности и иное
                  пособничество терроризму либо экстремизму под угрозой насилия
                  и добровольно заявившее об этом, а также активно
                  способствовавшее раскрытию или пресечению преступления,
                  освобождается от уголовной ответственности, если в его
                  действиях не содержится состава иного преступления.
                </p>
                <p>
                  Статья 259. Вербовка или подготовка либо вооружение лиц в
                  целях организации террористической либо экстремистской
                  деятельности
                </p>
                <p>
                  1. Вербовка или подготовка либо вооружение лиц в целях
                  организации террористической либо экстремистской деятельности
                  -
                </p>
                <p>
                  наказывается лишением свободы на срок от восьми до двенадцати
                  лет с конфискацией имущества.
                </p>
                <p>
                  2. То же деяние, совершенное лицом с использованием своего
                  служебного положения или в отношении несовершеннолетнего, -
                </p>
                <p>
                  наказывается лишением свободы на срок от десяти до пятнадцати
                  лет с конфискацией имущества.
                </p>
                <p>
                  Статья 260. Прохождение террористической или экстремистской
                  подготовки
                </p>
                <p>
                  Прохождение лицом, в том числе за пределами Республики
                  Казахстан, подготовки, заведомо для обучающегося направленной
                  на приобретение умений и навыков совершения террористического
                  или экстремистского преступления, -
                </p>
                <p>
                  наказывается лишением свободы на срок от трех до семи лет.
                </p>
                <p>
                  Примечание. Лицо, совершившее преступление, предусмотренное
                  настоящей статьей, освобождается от уголовной ответственности,
                  если оно добровольно сообщило о прохождении подготовки,
                  способствовало выявлению других лиц, прошедших такую
                  подготовку, осуществлявших, организовавших или финансировавших
                  такую подготовку, предоставило информацию о месте ее
                  проведения и если в его действиях не содержится иного состава
                  преступления.
                </p>
                <p>
                  Статья 267. Организация незаконного военизированного
                  формирования
                </p>
                <p>
                  1. Создание незаконного военизированного формирования, а равно
                  руководство таким формированием &ndash;
                </p>
                <p>
                  наказываются лишением свободы на срок от двух до семи лет.
                </p>
                <p>
                  2. Участие в незаконном военизированном формировании &ndash;
                </p>
                <p>
                  наказывается штрафом в размере до трех тысяч месячных
                  расчетных показателей либо исправительными работами в том же
                  размере, либо привлечением к общественным работам на срок до
                  восьмисот часов, либо ограничением свободы на срок до трех
                  лет, либо лишением свободы на тот же срок.
                </p>
                <p>
                  Примечание. Лицо, добровольно прекратившее участие в
                  незаконном военизированном формировании и сдавшее оружие и
                  воинское снаряжение, освобождается от уголовной
                  ответственности, если в его действиях не содержится состава
                  иного преступления.
                </p>
                <p>
                  Статья 404. Создание, руководство и участие в деятельности
                  незаконных общественных и других объединений
                </p>
                <p>
                  1. Создание или руководство религиозным или общественным
                  объединением, деятельность которого сопряжена с насилием над
                  гражданами или иным причинением вреда их здоровью, либо с
                  побуждением граждан к отказу от исполнения гражданских
                  обязанностей или совершению иных противоправных деяний, а
                  равно создание или руководство партией на религиозной основе,
                  либо политической партией или профессиональным союзом,
                  финансируемых из запрещенных законами Республики Казахстан
                  источников, &ndash;
                </p>
                <p>
                  наказываются штрафом в размере до шести тысяч месячных
                  расчетных показателей либо исправительными работами в том же
                  размере, либо ограничением свободы на срок до шести лет, либо
                  лишением свободы на тот же срок, с лишением права занимать
                  определенные должности или заниматься определенной
                  деятельностью на срок до шести лет.
                </p>
                <p>
                  2. Создание общественного объединения, провозглашающего или на
                  практике реализующего расовую, национальную, родовую,
                  социальную, сословную или религиозную нетерпимость или
                  исключительность, призывающего к насильственному
                  ниспровержению конституционного строя, подрыву безопасности
                  государства или посягательствам на территориальную целостность
                  Республики Казахстан, а равно руководство таким объединением
                  &ndash;
                </p>
                <p>
                  наказываются лишением свободы на срок от трех до семи лет с
                  лишением права занимать определенные должности или заниматься
                  определенной деятельностью на срок до трех лет.
                </p>
                <p>
                  3. Активное участие в деятельности объединений, указанных в
                  частях первой или второй настоящей статьи, &ndash;
                </p>
                <p>
                  наказывается штрафом в размере до шести тысяч месячных
                  расчетных показателей либо исправительными работами в том же
                  размере, либо ограничением свободы на срок до шести лет, либо
                  лишением свободы на тот же срок.
                </p>
                <p>
                  Статья 405. Организация и участие в деятельности общественного
                  или религиозного объединения либо иной организации после
                  решения суда о запрете их деятельности или ликвидации в связи
                  с осуществлением ими экстремизма или терроризма
                </p>
                <p>
                  1. Организация деятельности общественного или религиозного
                  объединения либо иной организации, в отношении которых имеется
                  вступившее в законную силу решение суда о запрете их
                  деятельности или ликвидации в связи с осуществлением ими
                  экстремизма или терроризма, &ndash;
                </p>
                <p>
                  наказывается штрафом в размере до шести тысяч месячных
                  расчетных показателей либо исправительными работами в том же
                  размере, либо ограничением свободы на срок до шести лет, либо
                  лишением свободы на тот же срок, с лишением права занимать
                  определенные должности или заниматься определенной
                  деятельностью на срок до пяти лет или без такового, с
                  выдворением за пределы Республики Казахстан иностранца или
                  лица без гражданства сроком на пять лет.
                </p>
                <p>
                  2. Участие в деятельности общественного или религиозного
                  объединения или иной организации, в отношении которых имеется
                  вступившее в законную силу решение суда о запрете их
                  деятельности или ликвидации в связи с осуществлением ими
                  экстремизма или терроризма, &ndash;
                </p>
                <p>
                  наказывается штрафом в размере до двух тысяч месячных
                  расчетных показателей либо исправительными работами в том же
                  размере, либо ограничением свободы на срок до двух лет, либо
                  лишением свободы на тот же срок, с лишением права занимать
                  определенные должности или заниматься определенной
                  деятельностью на срок до трех лет или без такового, с
                  выдворением за пределы Республики Казахстан иностранца или
                  лица без гражданства сроком на пять лет.
                </p>
                <p>
                  Примечание. Лицо, добровольно прекратившее участие в
                  деятельности общественного или религиозного объединения либо
                  иной организации, в отношении которых имеется вступившее в
                  законную силу решение суда о запрете их деятельности или
                  ликвидации в связи с осуществлением ими экстремизма или
                  терроризма, освобождается от уголовной ответственности, если в
                  его действиях не содержится иного состава преступления.
                </p>
              </div>
            </div>
          </div>

          <div class="accordion-item child nested hidden">
            <p class="parent" @click="changeToActive">
              КОДЕКС РК ОБ АДМИНИСТРАТИВНЫХ ПРАВОНАРУШЕНИЯХ
            </p>
            <div class="accordion-item child hidden">
              <div class="child text">
                <p>
                  Статья 214. Нарушение законодательства Республики Казахстан о
                  противодействии легализации (отмыванию) доходов, полученных
                  преступным путем, и финансированию терроризма
                </p>
                <p>
                  1. Нарушение субъектами финансового мониторинга
                  законодательства Республики Казахстан о противодействии
                  легализации (отмыванию) доходов, полученных преступным путем,
                  и финансированию терроризма в части документального
                  фиксирования, хранения и предоставления информации об
                  операциях, подлежащих финансовому мониторингу, их клиентах,
                  надлежащей проверки клиентов (их представителей) и
                  бенефициарных собственников, принятия мер по замораживанию
                  операций с деньгами и (или) иным имуществом, отказа в
